// Components for Main Page Only
// Components styles are page components/modules that are reusable. | nav, ads, etc.

// Sitewide Navigation including dropdowns on HEADER
// Place styling here that are global (all devices)
// --------------------------------------------------

.sitewide-nav {
	width: 100%;
	z-index: 500;
	top: 0;
	height: 55px;
	background: $brand-white;
	border-bottom: 1px solid $brand-accent-grayscale-5;
	position: fixed;
	@include font-smoothing;
	/* Force Hardware Acceleration */
	@include transform(translateZ(0));
	@include backface-visibility(hidden);

	@include transition(transform .6s);

	&.nav-is-visible {
		@include transform(translateX(260px)); }

	&::before {
		/* never visible - this is used in jQuery to check the current MQ - do not delete */
		display: none;
		content: 'mobile'; }
	@include MQ(M) {
		height: 55px;
		&.nav-is-visible {
 }			//+transform(translateX(0))
		&::before {
			content: 'desktop'; } }

	.navbar-brand {
		padding: 19px 0 0 0;
		margin-left: 50px;

		/* remove jump on iOS devices - when we fire the navigation */
		@include transform(translateZ(0));
		@include backface-visibility(hidden);

		img {
			width: 145px; }
		@include desktop {
			padding: 17px 0 0 0;
			margin-left: 0;
			float: left; } }
	.nav {
		padding-right: 15px; }
	.nav li {
		float: left; }
	.nav .open > a {
		color: $brand-mid-blue;
		background-color: transparent;
		height: auto;
		letter-spacing: .05em;
		&:hover, &:focus, &:active {
			background-color: transparent;
			border-color: inherit; } }
	.nav > li > a {
		color: $brand-accent-grayscale-2;
		// text-transform: uppercase
		font-weight: 600;
		letter-spacing: .05em;
		padding: 18px 6px;
		font-size: 14px;
		font-family: $font-family-sans-serif;
		@include font-smoothing;
		cursor: pointer;
		&:hover,&:focus, &:active {
			text-decoration: none;
			background-color: inherit;
			color: $brand-black-lighter;
			outline: none; }
		&:hover .btn {
			color: $brand-black-lighter;
			font-weight: inherit;
			transition: color 0.3s ease-in-out; }
		&.is-active {
			color: $brand-black-lighter; }
		&.request-demo {
			&:hover {
				font-weight: 400; } } }

	.sign-up-login {
		margin: 0;
		li a {
			color: $brand-accent-grayscale-2;
			&:hover {
				color: $brand-blue; } }
		.signup {
			margin: 0 10px; }
		.login > a {
			position: relative;
			padding: 18px 13px 0 0; }
		.demo-request {
			margin-left: 15px; } }

	@include tablet {
		border-bottom: none;
		background: $brand-accent-p;
		@include transition(height 250ms ease-in-out);
		transition: transform 0.6s;
		&.section-first {
			height: 66px; }
		.site-navbar {
			@include make-md-column(12);
			a:hover,
			a:focus {
				color: $brand-blue;
				cursor: pointer; } }
		.login-nav {
			display: inline-block;
			float: right; }
		.login-translate-row {
			float: right;
			position: absolute;
			top: 11px;
			right: 0;
			height: 17px;
			@include transition(height 250ms ease-in-out);
			&.nots1 {
				height: 0;
				overflow: hidden; } }
		.main-nav-row {
			float: right;
			margin-top: 33px;
			@include transition(margin 250ms ease-in-out);
			&.nots1 {
				margin-top: 17px; } }
		.main-nav-list {
			list-style: none;
			li {
				float: left;
				padding-right: 20px;
				font-weight: 200;
				&:last-of-type {
					padding: 0; }
				a {
					font-size: 14px;
					font-weight: 600;
					color: $brand-accent-grayscale-3;

					&.main-nav-ent:hover {
						&::after {
							border: 2px solid $brand-p-ent;
							border-top: 1px solid transparent;
							border-left: 1px solid transparent;
							transform: rotate(45deg); } }
					&.main-nav-apps:hover {
						&::after {
							border: 2px solid $brand-p-apps;
							border-top: 1px solid transparent;
							border-left: 1px solid transparent;
							transform: rotate(45deg); } } }

				&.sub-nav-list {
					&.is-active > a.main-nav-go {
						color: $brand-p-go; }
					a.main-nav-go {
						&:hover,
						&:focus {
							color: $brand-p-go;
							cursor: pointer; } }
					&.is-active > a.main-nav-ent {
						color: $brand-p-ent;
						&::after {
							border: 2px solid $brand-p-ent;
							border-top: 1px solid transparent;
							border-left: 1px solid transparent;
							transform: rotate(45deg); } }
					a.main-nav-ent {
						&:hover,
						&:focus {
							color: $brand-p-ent;
							cursor: pointer; } }
					&.is-active > a.main-nav-apps {
						color: $brand-p-apps;
						&::after {
							border: 2px solid $brand-p-apps;
							border-top: 1px solid transparent;
							border-left: 1px solid transparent;
							transform: rotate(45deg); } }
					a.main-nav-apps {
						&:hover,
						&:focus {
							color: $brand-p-apps;
							cursor: pointer; } } } } }
		.sign-up-login {
			.login {
				a {
					padding: 0; } }
			li {
				padding-right: 15px; }
			li > a {
				font-size: 12px;
				font-weight: 600;
				letter-spacing: 0em;
				padding: 0; } } }
	@include desktop {
		.subnav-level-4 {
			padding-left: 11px;
			a {
				font-size: 12px !important; } } }

	@include desktop-xl {
		.main-nav-list {
			li {
				padding-right: 30px;
				&:last-of-type {
					padding: 0; } } } } }
