// Sitewide Nav Styling - Mobile
// ---------------------------------------------

.main-nav-trigger {
	position: absolute;
	display: block;
	height: 55px;
	width: 50px;

	/* hide text */
	overflow: hidden;
	white-space: nowrap;
	color: transparent;

	span {
		/* hamburger icon created in CSS */
		position: absolute;
		height: 2px;
		width: 20px;
		top: 50%;
		margin-top: -1px;
		left: 50%;
		margin-left: -10px;
		background-color: $brand-black;
		@include transition(background-color .3s .3s);

		&::before, &::after {
			/* upper and lower lines of the menu icon */
			content: '';
			position: absolute;
			width: 100%;
			height: 100%;
			background-color: $brand-black;

			/* Force Hardware Acceleration */
			@include transform(translateZ(0));
			@include backface-visibility(hidden);

			@include transition(transform .3s .3s); }
		&::before {
			@include transform(translateY(-6px)); }
		&::after {
			@include transform(translateY(6px)); } }
	.nav-is-visible & span {
		background-color: rgba($brand-black, 0);
		&::before {
			@include transform(translateY(0) rotate(-45deg)); }
		&::after {
			@include transform(translateY(0) rotate(45deg)); } }
	@include desktop {
		height: 30px;
		width: 30px;
		top: 22px;
		right: 60px;
		span {
			height: 1px;
			background-color: transparent;
			&::before, &::after {
				background-color: $brand-white; }
			&::before {
				@include transform(translateY(0) rotate(-45deg)); }
			&::after {
				@include transform(translateY(0) rotate(45deg)); } } } }

.main-nav-solutions {
	position: fixed;
	z-index: 500;
	top: 0;
	left: 0;
	width: 260px;
	height: 100vh;
	overflow-x: hidden;
	-webkit-overflow-scrolling: touch;
	background: $brand-white;
	padding-top: 3px;
	@extend .list-unstyled;
	@include transition(all .6s);
	@include transform(translateX(-100%));
	&.nav-is-visible {
		opacity: 1;
		@include transform(translateX(0));
		@include box-shadow(2px 0px 4px 1px rgba(0,0,0,0.25)); }
	&.nav-is-visible > li > button {
		@include transform(translateX(0));
		@include transition(transform .6s); }
	&::after {
		content: '';
		z-index: 5;
		display: block;
		position: absolute;
		height: 100vh;
		width: 260px;
		top: 0;
		left: 0;
		background-color: $brand-white; }
	a {
		display: block;
		padding: 15px 21px 15px 30px;
		z-index: 15;
		font-family: $font-family-sans-serif;
		font-weight: 600;
		color: $brand-accent-grayscale-2;
		background: url('../img/home/main-nav-bg.png') no-repeat bottom left 30px;
		@include font-smoothing;
		@include transition(transform .6s, opacity .6s);
		&:hover,
		&:focus {
			background-image: url('../img/home/main-nav-bg.png');
			background-color: $brand-accent-p;
			text-decoration: none;
			color: $brand-p-ent; } }

	.nav-list .nav-is-visible a {
		background: url("../img/home/main-nav-bg.png") no-repeat bottom left 30px;
		background-size: 80% 1px; }

	.ts-subnav, .ent-subnav {
		a {
			display: block;
			padding: 15px 21px 15px 30px;
			z-index: 15;
			font-family: $font-family-sans-serif;
			font-weight: 600;
			color: $brand-accent-grayscale-2;
			background: url("../img/home/main-nav-bg.png") no-repeat bottom left 30px;
			background-size: 80% 1px;
			@include font-smoothing;
			@include transition(transform .6s, opacity .6s);
			&:hover,
			&:focus {
				background-image: url('../img/home/main-nav-bg.png');
				background-color: $brand-accent-p;
				text-decoration: none;
				color: $brand-p-ent; } } }
	.main-header-nav-links, .sub-menu {
		padding-bottom: 0;
		margin-bottom: -10px;

		a {
			background: none; } }
	li.p2p {
		background: none !important;
		&:after {
			content: "";
			position: absolute;
			left: 0px;
			height: 1px;
			width: 90%;
			background: url("../img/home/main-nav-bg.png") no-repeat bottom left 30px;
			background-size: 100%; } }

	li.p2p .sub-menu a, a.p2p {
		background: none !important; }
	.sub-menu {
		a:first-child {
			padding: 0px 0px 7px 30px !important;
			margin-top: 0px !important; }
		a:last-child {
			padding: 0px 0px 22px 30px !important;
			margin-top: 0px !important; } }


	li ul {
		position: absolute;
		top: 0;
		right: 0;
		width: 100%;
		z-index: 10;
		@extend .list-unstyled;
		@include transform(translateX(260px));
		@include transition(transform .6s);
		@include font-smoothing; }

	&.moves-out > li > a,
	&.moves-out > li > button {
		/* push the navigation items to the left - and lower down opacity - when secondary nav slides in */
		@include transform(translateX(-260px));
		@include transition(transform .6s);
		// opacity: 0
		/* Reveal secondary nav */ }

	&.moves-out > li > ul {
		@include transform(translateX(0));
		@include transition(transform .6s); }

	.subnav-trigger {
		position: relative;
		&::before,
		&::after {
			/* arrow icon in CSS */
			content: '';
			position: absolute;
			top: 50%;
			right: 15px;
			margin-top: -1px;
			display: inline-block;
			height: 2px;
			width: 10px;
			background: $brand-p-ent;
			@include backface-visibility(hidden);
			@include transform-origin(9px 50%); }
		&::before {
			@include transform(rotate(45deg)); }
		&::after {
			@include transform(rotate(-45deg)); } }

	.go-back {
		@extend .hidden-md;
		@extend .hidden-lg;
		a {
			position: relative;
			padding: 16px 16px 17px 15px;
			line-height: 1;
			&::before {
				content: '\00AB';
				font-size: 22px; }
			&::before, &::after {
				/* arrow goes on the left side - go back button if needed */
				left: 20px;
				@include transform-origin(1px 50%); } } }
	.register,
	.log-in,
	.demo {
		@extend .hidden-md;
		@extend .hidden-lg; }
	.demo {
		margin: 5px 0 0 35px;
		a {
			border-bottom: none;
			.btn {

				&:hover {
					background-color: $brand-blue-opaque;
					transition: all 0.3s ease-in-out;
					color: $brand-white; } }
			&:hover {
				background: initial; } } }


	.ent-subnav,
	.app-subnav,
	.ts-subnav,
	.nav-is-hidden {
		visibility: hidden; }

	.ent-subnav,
	.app-subnav,
	.ts-subnav {
		margin-top: 55px;
		&.nav-is-visible {
			visibility: visible; } }

	.no-subnav {
		position: relative; } }


