// Custom Styling for Enterprise Solutions Apps
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css: css/style-ent.css

.ent-apps-page {
	/* Global Theme Sizes - New Put on Theme.sass later */
	h1,
	h2,
	h3,
	p {
		color: $brand-black-lighter;
		font-weight: 200; }
	h1 {
		font-size: 34px;
		line-height: 44px; }
	h2 {
		font-size: 26px;
		line-height: 33px;
		text-align: center; }
	h3 {
		font-size: 20px;
		line-height: 33px; }
	p {
		font-size: 14px;
		line-height: 22px; }

	@include tablet {
		h1 {
			font-size: 40px;
			line-height: 44px; }
		h2 {
			font-size: 30px;
			line-height: 44px; }
		h3 {
			font-size: 24px;
			line-height: 33px; }
		p {
			font-size: 18px;
			line-height: 33px; } }
	@include desktop {
		h1 {
			font-size: 50px;
			line-height: 66px; }
		h2 {
			font-size: 40px;
			line-height: 55px; }
		h3 {
			font-size: 24px;
			line-height: 33px; }
		p {
			font-size: 18px;
			line-height: 33px; } }
	@include desktop-xl {
		h1 {
			font-size: 60px;
			line-height: 77px; }
		h2 {
			font-size: 44px;
			line-height: 55px; }
		h3 {
			font-size: 34px;
			line-height: 44px; } }

	.hero {
		padding: 44px 0;
		background: $brand-accent-p url('../img/solutions/appsv2/iPhone_mobile.png') repeat-x center bottom;
		position: relative;
		text-align: center;
		h1 {
			margin-bottom: 28px; }
		.contents {
			@include make-xs-column(12); }

		.form-container {
			width: 532px;
			margin: auto;
			display: flex;
			justify-content: space-between;
			flex-direction: column; }
		.form-container input[type="e-mail"] {
			width: 337px !important;
			height: 44px;
			float: left;
			font-weight: 400;
			font-size: 18px;
			line-height: 28px;
			color: #646464; }
		.form-control {
			border: transparent !important; }
		.btn-cta {
			background: #ff9000;
			float: right;
			padding: 9px 22px !important;
			font-size: 18px !important;
			font-weight: 600 !important;
			height: 44px !important;
			margin: 0 auto !important;
			margin-left: 0px !important;
			border: none !important;
			box-shadow: none;
			border-radius: 22px !important;
			letter-spacing: -0.01em;
			color: #fff !important;
			text-align: center !important;
			-webkit-transition: background 200ms ease-in;
			-moz-transition: background 200ms ease-in;
			transition: background 200ms ease-in; }

		@include phone {
			height: 660px;
			h1 {
				padding: 0 15px; }
			.contents {
				width: 320px;
				margin: 0 auto;
				float: none; }
			.form-container {
				width: 100%;
				input[type="e-mail"] {
					width: 100% !important;
					margin-bottom: 22px; }
				.btn-cta {
					display: block;
					margin: auto !important;
					float: none !important; } } }

		@include tablet {
			padding: 66px 0;
			height: 660px;
			background: $brand-accent-p url('../img/solutions/appsv2/hero-bg.png') no-repeat center bottom;
			background-size: 1200px;
			h3 {
				padding: 0 30px; }
			.product-screenshot {
				width: 400px;
				margin: 22px auto 0;
				&.active {
					@include transform(translateY(22px));
					@include transition(transform 1000ms ease-in);
					// @include animation-name(slideIn)
					// @include animation-duration(1000ms)
					// @include animation-iteration-count(1)
					// @include animation-timing-function(linear)
					// @include keyframes(slideIn)
					// 	from
					// 		@include transform(translateY(-22px))
					// 	to
 } } }					// 		@include transform(translateY(0))
		@include desktop {
			padding: 88px 0;
			height: 935px;
			background: $brand-accent-p url('../img/solutions/appsv2/hero-bg.png') no-repeat center bottom;
			background-size: 1920px;
			.product-screenshot {
				width: 616px;
				margin: 66px auto 0; } } }

	.goldilocks {
		padding: 44px 0;
		position: relative;
		.contents {
			@include make-xs-column(12);
			@include make-lg-column-offset(1);
			@include make-lg-column(10); }
		a {
			font-weight: 600;
			color: #00b0ff; }
		sup {
			font-size: 50%; }

		@include phone {
			.contents {
				width: 320px;
				margin: 0 auto;
				float: none; } }
		@include tablet {
			padding: 66px 0;
			.contents {
				padding: 0 30px;
				text-align: center; } }
		@include desktop;
		@include desktop-xl; }

	.change {
		padding: 44px 0 330px;
		background: $brand-accent-p url('../img/solutions/appsv2/faster-change-xs-min.png') no-repeat center top;
		position: relative;
		.contents {
			@include make-xs-column(12);
			@include make-lg-column-offset(1);
			@include make-lg-column(10); }

		@include phone {
			height: 666px;
			.contents {
				width: 290px;
				margin: 0 auto;
				float: none; } }
		@include tablet {
			padding: 66px 0 330px;
			height: 686px;
			background: $brand-accent-p url('../img/solutions/appsv2/faster-change-min.png') no-repeat center top;
			position: relative;
			.contents {
				padding: 0 30px;
				text-align: center; } }
		@include desktop;
		@include desktop-xl; }

	.more-with-data {
		background: $brand-accent-p url('../img/solutions/appsv2/more-data-xs.jpg') no-repeat center top;
		position: relative;
		.contents {
			@include make-xs-column(12);
			@include make-lg-column-offset(1);
			@include make-lg-column(10); }

		@include phone {
			background: #f2f2f2 url("../img/solutions/appsv2/more-data-xs.jpg") no-repeat center top 33px;
			padding: 253px 0 44px 0;
			height: 827px;
			.contents {
				width: 320px;
				margin: 0 auto;
				float: none; } }
		@include tablet {
			padding: 253px 0 66px 0;
			background: $brand-accent-p url('../img/solutions/appsv2/more-data-sm.jpg') no-repeat center top;
			background-size: 1000px;
			.contents {
				padding: 0 30px;
				text-align: center; } }
		@include desktop {
			padding: 396px 66px 0;
			height: 988px;
			background: $brand-accent-p url('../img/solutions/appsv2/more-data.jpg') no-repeat center top;
			.contents {
				padding: 0 90px; } } }
	.more-with-data-wrapper {
		@include desktop-1920 {
			background: linear-gradient(rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.03)), url("../img/solutions/appsv2/more-data-bg.jpg") no-repeat center top;
			background-size: cover;
			height: 988px; } }
	.empower {
		background: $brand-accent-p;
		position: relative;
		.icons-apps {
			@include make-xs-column(6);
			@include make-sm-column(3); }
		.contents {
			@include make-xs-column(12);
			@include make-lg-column-offset(1);
			@include make-lg-column(10); }
		.contents-icon {
			@include make-lg-column-offset(0);
			@include make-lg-column(12); }

		@include phone {
			padding: 44px 0 22px;
			.icons-apps {
				margin: 11px 0;
				img {
					width: 100%; } }
			.contents {
				width: 290px;
				margin: 0 auto;
				float: none;
				.icon-graph {} } }
		@include tablet {
			padding: 66px 0 44px;
			.icons-apps {
				margin: 22px 0;
				img {
					width: 160px; } }
			.contents {
				text-align: center; }
			.contents-icon {
				padding-top: 44px; } }
		@include desktop-xl {
			.icons-apps {
				img {
					width: 206px; } } } }

	.app-store {
		padding: 44px 0;
		h2 {
			margin: 0 30px 44px;
			text-align: center;
			a {
				color: $brand-black-lighter; } }
		.apps {
			@include make-xs-column(6);
			@include make-sm-column(4);
			text-align: center;
			height: 198px;
			font-size: 18px;
			line-height: 22px;
			font-weight: 200;
			padding: 0 22px;
			img {
				margin: 0 auto 11px;
				display: block;
				width: 100px; } }
		@include tablet {
			h2 {
				width: 100%;
				margin: 0 auto 44px; }
			.apps {
				padding: 0; } }

		@include desktop {
			h3 {
				padding: 0 90px; }
			padding: 66px 0 33px;
			.apps {
				width: 20%;
				float: left; }
			.apps.api {
				margin-left: 10%; }
			.apps-french {
				margin-right: 10%; }
			.apps.api,
			.apps.so,
			.apps.ss,
			.apps.french {
				width: 20%; } } }

	.beyond-core {
		padding: 44px 0;
		background: $brand-accent-p url('../img/solutions/appsv2/beyond-core-xs-min.png') no-repeat center top;
		position: relative;
		h2,
		h3 {
			font-weight: 200; }
		.contents {
			@include make-xs-column(12);
			@include make-md-column-offset(6);
			@include make-md-column(6);
			@include make-lg-column-offset(7);
			@include make-lg-column(5); }

		@include phone {
			padding: 462px 0 44px 0;
			height: 787px;
			.contents {
				width: 290px;
				margin: 0 auto;
				float: none; } }
		@include tablet {
			padding: 484px 0 44px;
			background: $brand-accent-p url('../img/solutions/appsv2/beyond-core-xs-min.png') no-repeat center top;
			.contents {
				padding: 0 30px;
				text-align: center; } }
		@include desktop {
			padding: 143px 0 66px;
			background: $brand-accent-p url('../img/solutions/appsv2/beyond-core-min.png') no-repeat left 62% top;
			h2 {
				text-align: left; }
			.contents {
				padding: 0 15px;
				text-align: left; } }
		@include desktop-xl {
			padding: 209px 0 66px;
			background: $brand-accent-p url('../img/solutions/appsv2/beyond-core-min.png') no-repeat center top; } }

	.consistent {
		padding: 44px 0;
		// background: $brand-accent-p url('../img/solutions/appsv2/hero-bg-xs.jpg') repeat-x center bottom
		position: relative;
		.contents {
			@include make-xs-column(12);
			@include make-lg-column-offset(1);
			@include make-lg-column(10); }

		@include phone {
			.contents {
				width: 290px;
				margin: 0 auto;
				float: none; } }
		@include tablet {
			padding: 66px 0;
			.contents {
				padding: 0 25px;
				text-align: center; } }
		@include desktop-xl {
			.contents {
				padding: 0 15px; } } }

	.success-story {
		padding: 44px 0;
		background: $brand-accent-p;
		em {
			display: block;
			font-style: normal;
			color: $brand-p-ent; }
		a {
			color: $brand-black-lighter; }
		blockquote {
			border-left: none;
			position: relative;
			margin: 22px 0 0 0;
			footer {
				margin-top: 11px;
				font-size: 14px;
				line-height: 22px;
				color: $brand-accent-grayscale-3; } }
		@include phone {
			p {
				padding: 0 28px;
				&.lead-xs {
					text-align: left;
					padding: 0 15px; } }
			blockquote {
				&::before {
					content: '';
					background: url('../img/solutions/appsv2/lquo.png') no-repeat center top;
					position: absolute;
					margin: 0 auto;
					text-align: center;
					top: 0;
					left: 0;
					right: 0;
					width: 27px;
					height: 23px; }
				&::after {
					content: '';
					background: url('../img/solutions/appsv2/rquo.png') no-repeat center top;
					position: absolute;
					margin: 0 auto;
					text-align: center;
					bottom: 0;
					left: 0;
					right: 0;
					width: 27px;
					height: 23px; } }
			.contents {
				width: 320px;
				margin: 0 auto;
				float: none;
				text-align: center; }
			.owl-carousel {
				background: url('../img/solutions/appsv2/blue-line.png') no-repeat center top;
				background-size: 220px;
				padding-top: 22px;
				margin-top: 22px; }
			.quyntess,
			.procureport,
			.timestarter {
				blockquote {
					font-size: 20px;
					line-height: 33px;
					padding: 44px 30px; } }
			.procureport {
				blockquote {
					padding: 44px 15px; } }
			.timestarter {
				blockquote {
					padding: 44px 15px; } }
			.owl-theme {
				.owl-controls {
					.owl-pagination {
						display: none; }
					.owl-buttons {
						div {
							background: none; } } } } }

		@include tablet {
			padding: 66px 0 0;
			h2 {
				margin: 0 auto 11px;
				text-align: center;
				em {
					display: inline; } }
			p {
				font-weight: 200;
				text-align: center;
				padding: 0 30px; }
			blockquote {
				font-size: 24px;
				line-height: 33px;
				font-weight: 200;
				padding: 22px 15px 88px 0;
				margin: 44px 0 0 15px;
				border-top: 1px solid $brand-p-ent;
				display: inline-block;
				img {
					margin-top: -7px; }
				footer {
					text-indent: 0;
					font-size: 18px; } }
			.owl-carousel {
				background: url('../img/solutions/appsv2/blue-line.png') no-repeat center top;
				background-size: 570px;
				padding-top: 22px !important;
				margin-top: 22px; }
			.container {
				padding: 0; }
			.quyntess {
				background: url('../img/solutions/appsv2/quyntess-saas-min.png') no-repeat 330px 108%;
				background-size: 768px;
				blockquote {
					width: 318px; }
				.contents {
					@include make-sm-column(12); } }
			.procureport {
				background: url('../img/solutions/appsv2/procureport-saas-min.png') no-repeat 363px 108%;
				background-size: 768px;
				blockquote {
					width: 375px; }
				.contents {
					@include make-sm-column(12); } }
			.timestarter {
				blockquote {
					width: 392px; }
				.contents {
					background: url('../img/solutions/appsv2/timestarter-saas-min.png') no-repeat right 30px bottom;
					background-size: 320px;
					@include make-sm-column(12); } }
			.owl-theme {
				position: relative;
