					left: 15px;
					text-align: left;
					.owl-page {
						span {
							background: $brand-accent-p;
							border: 2px solid $brand-accent-grayscale-4; } }
					.owl-page.active {
						span {
							background: $brand-accent-grayscale-4;
							border: 2px solid $brand-accent-grayscale-4; } } } } }
		@include desktop {
			blockquote {
				font-size: 26px;
				line-height: 44px;
				margin: 88px 0 0 2%;
				padding: 22px 15px 66px 0; }
			.contents {
				p {
					width: 845px;
					margin: 11px auto 0; } }
			.quyntess {
				background: url('../img/solutions/appsv2/ss-quyntess-min.png') no-repeat left -400px bottom -16px;
				background-size: 1920px;
				p {
					padding: 0 30px; }
				blockquote {
					width: 375px; }
				.contents {
					width: 850px;
					margin: 0 auto;
					float: none; } }
			.procureport {
				background: url('../img/solutions/appsv2/ss-procureport-min.png') no-repeat left -400px bottom -16px;
				background-size: 1920px;
				p {
					padding: 0 30px; }
				blockquote {
					width: 402px; }
				.contents {
					width: 850px;
					margin: 0 auto;
					float: none; } }
			.timestarter {
				blockquote {
					width: 450px; }
				.contents {
					background: url('../img/solutions/appsv2/timestarter-saas-min.png') no-repeat 100% bottom;
					background-size: 397px;
					width: 900px;
					margin: 0 auto;
					float: none;
					p {
						padding: 0 15px; } } }
			.owl-theme {
				position: relative;
				.owl-controls {
					position: absolute;
					top: 260px;
					bottom: 100%;
					left: 0;
					right: 0;
					text-align: center;
					margin: 0; } } }

		@include desktop-xl {
			.quyntess {
				background: url('../img/solutions/appsv2/ss-quyntess-min.png') no-repeat center bottom -16px;
				background-size: 1920px;
				.contents {
					width: 980px; } }
			.procureport {
				background: url('../img/solutions/appsv2/ss-procureport-min.png') no-repeat center bottom -16px;
				background-size: 1920px;
				.contents {
					width: 980px; } }
			.timestarter {
				.contents {
					background: url('../img/solutions/appsv2/timestarter-saas-min.png') no-repeat 100% bottom;
					background-size: 397px;
					width: 980px; } } } }

	.questions {
		padding: 66px 0;
		text-align: center;
		@include linear-gradient(150deg, $brand-p-ent 70%, $brand-s-ent 100%);
		.contents {
			@include make-xs-column(12);
			@include make-sm-column-offset(1);
			@include make-sm-column(10);
			@include make-md-column-offset(1);
			@include make-md-column(10);
			@include make-lg-column-offset(2);
			@include make-lg-column(8); }
		h2 {
			padding: 0 0 11px;
			color: $brand-white; }
		h3 {
			margin-bottom: 22px;
			color: $brand-white; }
		p {
			color: $brand-white; }
		a {
			font-weight: 600;
			text-transform: initial; }
		@include phone {
			.contents {
				width: 320px;
				margin: 0 auto;
				float: none; } }
		@include tablet {
			a {
				font-size: 18px;
				line-height: 24px; } } } }






