// Custom Styling for Solutions - Pay
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

@include desktop {
	.vidyard_tbox iframe.vidyard_iframe {
		width: 100% !important;
		height: 100% !important; }

	.vidyard_tinner, .vidyard_tcontent, #vidyard_span_KXqGLcOokS0-8Cx9k2_McA {
		width: 650px !important;
		height: 366px !important; }

	.vidyard_tbox {
		width: 650px !important;
		height: 366px !important; } }
@include tablet {
	.vidyard_tcontent span {
		width: 100% !important;
		height: 100% !important; }
	#RFDisplayFrame {
		overflow: hidden !important;
		.tabCompList caption {
			width: 400px !important;
			overflow: hidden !important; } } }
@include phone {
	.container {
		width: 320px;
		margin: auto; } }
