				width: 450px; }
			.pay-capability {
				margin-right: 22px;
				&:last-of-type {
					margin-right: 0px; } } }
		@include tablet {
			h1 {
				sup {
					font-size: 14px;
					top: -1.4em; } }
			h3 {
				font-size: 26px;
				line-height: 33px;
				color: #282828; }
