// Davos Landing Page (/davos)
.container {
	@include phone {
		width: 320px; } }
.wef2016-davos {
	.hero.intro {
		background: $brand-white url('../img/davos/2018/davos-hero-60.jpg') no-repeat center 55%;
		height: 275px;
		position: relative;
		padding: 33px 0 0;
		text-align: center;
		.contents {
			@extend .col-xs-12;
			@extend .col-lg-offset-1;
			@extend .col-lg-10; }
		h1 {
			color: $brand-white;
			font-size: 28px;
			line-height: 33px;
			padding: 0 11px;
			font-weight: 300; }

		h3 {
			color: $brand-white;
			margin-top: 11px;
			font-size: 18px;
			line-height: 22px;
			padding: 0 11px;
			font-weight: 300; }
		.arrow {
			position: absolute;
			bottom: 0;
			width: 100%;
			height: 0;
			background-color: $brand-white; }

		.arrow:after {
			left: 50%;
			border-bottom: 16px solid $brand-white;
			border-left: 14px solid transparent; }
		a.app {
			font-size: 22px;
			color: $brand-white;
			text-decoration: underline;
			&:after {
				content: "";
				background: url('../img/wec2017/icon-davosX-app.png') no-repeat;
				position: relative;
				display: inline-block;
				margin-top: -22px;
				height: 45px;
				width: 45px;
				top: 18px;
				left: 8px; } }
		.featured-content {
			width: 880px !important;
			margin: auto !important;
			@include flex;
			@include flex-justify(center);
			@include flex-align(center); }

		@include phone {
			padding: 132px 0 88px 0;
			padding: 66px 0;
			background: $brand-white url('../img/davos/2018/hero-bg-xs.jpg') no-repeat center -41px;
			background-size: cover;
			height: auto;
			h1 {
				font-size: 34px;
				width: 280px;
				margin: auto; }
			h3 {
				font-size: 26px;
				line-height: 33px; }
			.contents {
				padding: 0px; }
			.featured-info {
				padding: 22px;
				h3 {
					font-size: 18px !important;
					line-height: 24px !important;
					margin-top: 0; }
				.btn-primary-cta {
					padding: 3px 33px 0 33px !important;
					height: 33px !important; } }
			a.app {
				font-size: 14px;
				&:before {
					content: "";
					background: url('../img/wec2017/icon-davosX-app.png') no-repeat;
					display: block;
					margin: auto;
					margin-top: 11px;
					margin-bottom: 11px;
					height: 45px;
					width: 45px;
					top: 11px;
					left: 0px; }
				&:after {
					content: "";
					background: none; } }
			.featured-image {
				padding: 0px; } }
		@include tablet-only {
			.featured-content {
				width: 760px !important; }
			.featured-image {
				width: 60% !important;
				padding-left: 0px !important; }
			.featured-info {
				width: 40% !important;
				text-align: left;
				padding: 36px 11px;
				h3 {
					text-align: left;
					font-size: 20px !important;
					line-height: 24px !important;
					padding-right: 0px; }
				p {
					text-align: left;
					max-height: auto !important;
					margin-right: 0;
					margin-top: 22px; } } }
		@include tablet {
			padding: 66px 0;
			background: $brand-white url('../img/davos/2018/davos-hero-60.jpg') no-repeat center center;
			background-size: cover;
			height: auto;
			h1 {
				font-size: 36px;
				line-height: 55px; }
			h3 {
				font-size: 26px;
				line-height: 33px; }
			a.app {
				font-size: 18px;
				&:after {
					margin-top: -11px; } } }
		@include desktop {
			padding: 88px 0 88px 0;
			background: $brand-white url('../img/davos/2018/davos-hero-60.jpg') no-repeat center center;
			background-size: cover;
			height: auto;
			h1 {
				font-size: 60px;
				font-weight: 300;
				line-height: 66px;
				padding-bottom: 11px; }
			h3 {
				font-size: 28px;
				line-height: 36px;
				padding-bottom: 18px; }
			a.app {
				&:after {
					margin-top: -22px; } } } }

	.video-of-the-day {
		padding: 33px 0;
		text-align: center;
		.contents {
			@extend .col-xs-12; }
		.featured-image {
			@extend .col-sm-7;
			@extend .col-md-7;
			//@extend .col-lg-offset-1
			//@extend .col-lg-6
			@extend .col-lg-7; }
		.featured-info {
			@extend .col-sm-5;
			@extend .col-md-5;
			@extend .col-lg-5;
			background: $brand-white;
			.meta {
				font-size: 12px;
				font-weight: 600;
				text-transform: uppercase;
				color: $brand-grey;
				margin-top: 33px;
				text-align: left; }
			.type.webinars {
				color: $brand-orange;
				padding-right: 7px; }
			.type.events {
				color: $brand-blue;
				padding-right: 7px; }
			.event-location {
				padding-right: 7px;
				&::before {
					content: '|';
					padding-right: 7px; } }
			.date-posted {
				&::before {
					content: '|';
					padding-right: 7px; } }
			.btn-primary-cta {
				background: #ff9000 !important;
				&:hover {
					background-color: #F05522 !important; } } }
		h2 {
			margin-bottom: 33px;
			text-align: center; }
		h3 {
			color: #ff9000 !important;
			font-weight: 400 !important;
			font-size: 22px !important;
			padding-left: 0px !important;
			line-height: 22px !important; }
		h4 {
			text-align: left; }
		img {
			@extend .img-responsive; }
		p {
			text-align: left;
			max-height: 66px;
			overflow: hidden;
			margin-bottom: 22px; }
		hr {
			margin-top: 44px; }
		.featured-image {
			width: auto;
			padding-right: 0px;
			img {
				-webkit-box-shadow: -5px 4px 5px 0px rgba(0,0,0,0.46);
				-moz-box-shadow: -5px 4px 5px 0px rgba(0,0,0,0.46);
				box-shadow: -5px 4px 5px 0px rgba(0,0,0,0.46); } }


		@include phone {
			h3 {
				font-size: 18px !important; } }

		@include tablet {
			padding: 44px 0;
			text-align: left;
			h3 {
				text-align: left;
				color: $brand-orange;
				margin-top: 3px !important;
				line-height: 28px !important; }
			h4 {
				font-size: 22px;
				line-height: 28px; }
			p {
				font-size: 14px;
				line-height: 20px;
				margin-top: 22px; }
			hr.dashed {
				margin-top: 44px; }
			.featured-info {
				.meta {
					margin-top: 0; } }
			.video-of-the-day {
				h3 {
					color: #ff9000 !important;
					font-weight: 400 !important;
					font-size: 22px !important;
					margin-bottom: 22px !important; }
				p {
					margin-top: 0px !important; }
				.featured-image {
					padding-left: 0; } } }
		@include tablet-only {
			.featured-info {
				padding: 22px 66px;
				width: 100%;
				text-align: center;
				h3, p {
					text-align: center; } }
			.featured-image {
				width: 100%; }
			img {
				width: 100%; } }
		@include desktop {
			padding: 44px 0 66px 0;
			.featured-info {
				height: 309px;
				width: 335px;
				padding: 0 33px;
				.meta {
					margin-top: 22px; } }
			h3 {
				margin-top: 44px !important;
				padding: 0px !important;
				padding-bottom: 22px !important; }
			h4 {
				font-size: 24px;
				line-height: 33px; }
			hr {
				margin-top: 55px; }
			p {
				margin-top: 0px; } } }

	.resource-listing {
		padding-bottom: 55px;
		padding-top: 66px;
		.contents {
			@extend .col-lg-offset-1;
			@extend .col-lg-10; }
		.resource-list {
			@extend .col-xs-12;
			@extend .col-sm-6;
			min-height: 280px;
			.imagery {
				a {
					display: inline-block;
					width: 100%;
					height: 100%; }
				&.analyst-reports,
				&.case-study,
				&.ebook,
				&.infographic,
				&.partner-brief,
				&.product-brief,
				&.solution-overview,
				&.video,
				&.webinar,
				&.whitepapers {
					position: relative;
					&::before {
						font-family: LinearIcons;
						position: absolute;
						bottom: 16px;
						right: 11px;
						font-size: 36px;
						color: $brand-white;
						line-height: 1;

						/* Enable Ligatures */
						-webkit-font-feature-settings: "liga";
						-moz-font-feature-settings: "liga=1";
						-moz-font-feature-settings: "liga";
						-ms-font-feature-settings: "liga" 1;
						-o-font-feature-settings: "liga";
						font-feature-settings: "liga";
						@include font-smoothing; } }
				&.analyst-reports {
					&::before {
						content: '\e7fb'; } }
				&.case-study {
					&::before {
						content: '\e911'; } }
				&.ebook {
					&::before {
						content: '\e715'; } }
				&.infographic {
					&::before {
						content: '\e7f8'; } }
				&.partner-brief {
					&::before {
						content: '\e913'; } }
				&.product-brief {
					&::before {
						content: '\e90c'; } }
				&.solution-overview {
					&::before {
						content: '\e6d4'; } }
				&.video {
					&::before {
						content: '\e96a'; } }
				&.webinar {
					&::before {
						content: '\e6f0'; } }
				&.whitepapers {
					&::before {
						content: '\e61b'; } } }

			.meta {
				font-size: 12px;
				font-weight: 600;
				text-transform: uppercase;
				color: $brand-grey;
				margin-top: 22px;
				text-align: left; }
			.roles,
			.media-type {
				color: $brand-orange;
				padding-right: 7px; }
			.date-posted {
				&::before {
					content: '|';
					padding-right: 7px; } }
			img {
				width: 100% !important;
				overflow: hidden; }
			h4 {
				text-align: left;
				margin-bottom: 22px;
				a {
					padding-left: 4px;
					color: $brand-blue;
					&:hover {
						color: $brand-mid-blue; } } } }
		@include phone {
			padding-top: 22px; }

		@include tablet {
			.resource-list {
				min-height: 360px; } } }

	.tweets-davos {
		padding: 44px 0;
		background: $brand-lt-gray;
		text-align: center;
		.contents {
			@extend .col-xs-12;
			@extend .col-lg-offset-1;
			@extend .col-lg-10; }
		.twitter-feed {
			padding: 0;
			text-align: left;
			height: 308px;
			background: url('../img/eprocurement/tweets-container-bg.png') no-repeat center top;
			background-size: contain;
			z-index: 450;
			.tweets-container {
				padding: 0 22px;
				height: 240px;
				overflow-x: scroll;
				margin: 55px 22px 0;
				width: 90%;
				// show the scrollbar on overflow
				// &::-webkit-scrollbar
				//	-webkit-appearance: none
				//	width: 7px
				// &::-webkit-scrollbar-thumb
				//	border-radius: 4px
				// 	background-color: $brand-dk-grey
				// &::-webkit-scrollbar-corner
				// 	background-color: $brand-black
				ul {
					@extend .list-unstyled;
					word-wrap: break-word;
					font-size: 12px;
					line-height: 16px; }
				.user {
					img[data-scribe='element:avatar'] {
						display: none; }
					span[data-scribe='element:name'] {
						font-weight: 800;
						margin-right: 3px;
						color: $brand-white; }
					span[data-scribe='element:screen_name'] {
						color: $brand-white; } }
				.timePosted {
					color: $brand-white;
					margin: 0;
					padding: 0;
					font-size: 12px; }
				.tweet {
					font-size: 12px;
					line-height: 18px;
					font-style: italic;
					padding: 0;
					margin: 0 0 22px 0;
					color: $brand-white; } } }
		@include tablet-only {
			.twitter-feed {
				width: 725px !important; } }
		@include tablet {
			.twitter-feed {
				background: url('../img/wec2016/tweet-davos-md.png') no-repeat center top;
				background-size: contain;
				height: 374px;
				padding-top: 22px;
				.tweets-container {
					padding: 0;
					height: 275px;
					margin: 22px auto 0;
					ul {
						margin-top: 0; } } } }
		@include desktop {
			.twitter-feed {
				height: 462px;
				margin-top: 11px;
				.tweets-container {
					height: 330px;
					margin: 44px auto 0;
					ul {
						li {
							font-size: 14px;
							line-height: 22px; } }
					.tweet {
						font-size: 18px;
						line-height: 22px; } } } } }
	.blog-feed {
		padding: 44px 0 0;
		h2 {
			text-align: center;
			color: $brand-blue;
			padding: 0 22px; }
		h4 {
			margin: 0;
			a {
				font-size: 16px;
				line-height: 22px;
				color: $brand-black-lighter; } }
		.contents {
			@extend .col-lg-offset-1;
			@extend .col-lg-10; }
		article {
			@extend .col-xs-12;
			@extend .col-sm-6;
			margin-top: 22px;
			min-height: 220px;
			img {
				width: 100%;
				height: auto; }
			p {
				margin: 0; }
			.pub-date {
				font-weight: 600;
				font-size: 12px;
				text-transform: uppercase;
				padding-right: 6px;
				color: $brand-orange; }
			.author {
				font-size: 12px;
				text-transform: uppercase;
				position: relative;
				padding-left: 14px;
				&::before {
					font-family: FontAwesome;
					content: '\f111';
					font-size: 6px;
					position: absolute;
					top: -2px;
					left: 0;
					color: $brand-black-lighter;
					@include font-smoothing; } } }
		@include tablet {
			h2 {
				margin-bottom: 22px; }
			h4 {
				margin: 0;
				a {
					font-size: 18px;
					line-height: 24px;
					color: $brand-black-lighter;
					font-weight: 400; } }
			article {
				min-height: 308px;
				.author {
					&::before {
						top: -7px; } }
				img {
					height: 172px; } } } }
	.start-exploring {
		padding: 33px 0;
		background: #f2f2f2;
		text-align: center;

		h2 {
			margin: 0 auto 11px;
			width: 280px;
			text-align: center;
			color: $brand-orange;
			margin-top: 6px; }
		h3 {
			padding: 0;
			font-weight: 200;
			font-size: 18px;
			line-height: 22px;
			text-align: center;
			width: 280px;
			margin: 0 auto; }
		p {
			text-align: left; }
		img {
			width: 100% !important; }

		.contents {
			@extend .col-xs-12; }
		.resource-wrapper {
			@extend .col-sm-4;
			margin-top: 22px; }

		@include tablet {
			h2 {
				width: 100%;
				margin-bottom: 22px; }
			p {
				width: 500px;
				padding: 0 33px 0 0;
				font-size: 16px;
				line-height: 28px; }
			p.last-of-type {
				padding-top: 11px; } }
		@include desktop {
			p {
				font-size: 18px;
				line-height: 22px; } } }


	.icon-down-arrow {
		font-size: 16px;
		font-family: $font-family-sans-serif;
		color: $brand-white;
		font-weight: 300;
		position: absolute;
		bottom: 30px;
		left: 0;
		right: 0;
		margin: 0 auto;
		text-align: center;
		width: 240px;
		height: 40px;
		cursor: pointer;
		@include tablet {
			color: $brand-black;
			&::after {
				content: '';
				background: url(../img/wec2017/down-arrow-black.png) no-repeat bottom center;
				width: 35px;
				height: 16px;
				position: absolute;
				bottom: 6px;
				left: 0;
				right: 0;
				margin: 0 auto; } }
		@include desktop {
			color: $brand-white;
			&::after {
				content: '';
				background: url(../img/mainv2/down-arrow.png) no-repeat bottom center;
				width: 35px;
				height: 16px;
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				margin: 0 auto; } }

		@include animation(bouncy 6s infinite);
		@include keyframes(bouncy) {
			0%, 20%, 50%, 80%, 100% {
				@include transform(translateY(0)); }
			40% {
				@include transform(translateY(-22px)); }
			60% {
				@include transform(translateY(-11px)); } } } }





