// Custom Styling for Roles > Executives / CXO
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.cxo {
	.hero.evolve-your-business {
		background: url('../img/roles/cxo/hero-800.jpg') no-repeat center top;
		height: 440px;
		.contents,
		.customer-logos {
			@extend .col-xs-12; }
		.contents {
			@extend .col-md-offset-1;
			@extend .col-md-11; }
		h1,
		h3 {
			color: $brand-white; }
		h1 {
			width: 225px;
			margin-top: 50px;
			margin-bottom: 20px; }
		h3 {
			width: 220px;
			padding-bottom: 30px; }
		.customer-logos {
			background: url('../img/roles/cxo/customer-logos-800.png') no-repeat center bottom;
			background-size: contain;
			height: 65px;
			z-index: 50; }
		@include bg-customer-logos;
		&::after {
			top: 340px;
			z-index: 40; }
		@include tablet {
			background: url('../img/roles/cxo/hero-desktop.jpg') no-repeat center top;
			height: 550px;
			.contents {
				padding-left: 30px;
				padding-right: 30px; }
			&::after {
				top: 450px; }
			h1 {
				width: 560px;
				margin-top: 60px;
				padding: 0; }
			h3 {
				width: 540px; }
			.customer-logos {
				background: url('../img/roles/p2p/customer-logos-desktop.png') no-repeat center bottom;
				background-size: contain;
				height: 110px;
				z-index: 50; } }
		@include desktop {
			.contents {
				padding-left: 0;
				padding-right: 0; } }
		@include desktop-xl {
			height: 600px;
			background-size: cover;
			&::after {
				top: 500px; }
			h1 {
				margin-top: 90px; }
			.customer-logos {
				height: 130px; } } }

	.control-your-future {
		background: $brand-blue;
		text-align: left;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-offset-6;
			@extend .col-sm-6;
			@extend .col-md-offset-7;
			@extend .col-md-5;
			@extend .col-lg-offset-7;
			@extend .col-lg-4; }

		h2,
		p {
			color: $brand-white; }
		h2 {
			margin-bottom: 30px; }
		.control {
			padding-top: 30px;
			height: 655px;
			img {
				margin-bottom: -15px; } }
		.execs-agree {
			height: 220px;
			background: $brand-sky-blue;
			h4 {
				font-size: 19px;
				color: $brand-navy;
				margin-top: 30px; }
			p {
				color: $brand-navy;
				margin-top: 10px;
				margin-bottom: 30px; }
			p.sub {
				color: $brand-black-lighter;
				font-size: 10px;
				line-height: 1.2;
				margin-bottom: 10px; } }
		@include tablet {
			padding: 44px 0;
			background: $brand-blue;
			height: 418px;
			.control {
				background: $brand-blue url('../img/roles/cxo/control-bg-desktop.png') no-repeat 30px top;
				background-size: 350px;
				padding-top: 0;
				h2 {
					margin-top: 0;
					margin-bottom: 0; }
				p {
					margin-top: 22px;
					padding-right: 15px; }
				p.sub {
					color: $brand-black-lighter;
					font-size: 10px;
					line-height: 1.2;
					margin-top: 22px; } } }
		@include desktop {
			height: 484px;
			.control {
				background: $brand-blue url('../img/roles/cxo/control-bg-desktop.png') no-repeat 16.6% top;
				background-size: 408px;
				p.sub {
					margin-top: 66px; } } }
		@include desktop-xl {
			height: 594px;
			.control {
				background: $brand-blue url('../img/roles/cxo/control-bg-desktop.png') no-repeat 16.6% top;
				background-size: initial;
				h2 {
					margin-top: 44px; }
				p.sub {
					margin-top: 132px; } } } }

	.transform-processes {
		padding: 77px 0;
		background: $brand-blue url('../img/roles/cxo/transform-800.jpg') no-repeat center top;
		color: $brand-white;
		text-align: center;
		height: 525px;
		.contents {
			@extend .col-xs-12;
			@extend .col-md-offset-1;
			@extend .col-md-11; }

		h2 {
			width: 200px;
			margin: 0 auto;
			color: $brand-white; }
		p {
			padding: 0 15px; }

		@include tablet {
			padding: 44px 0;
			background: $brand-blue url('../img/roles/cxo/transform-bg-desktop.jpg') no-repeat center top;
			text-align: left;
			height: 530px;
			.contents {
				padding-left: 30px;
				padding-right: 30px; }
			h2 {
				width: 300px;
				margin: 30px 0 20px; }
			p {
				width: 380px;
				padding: 0; } }
		@include desktop {
			.contents {
				padding-left: 0;
				padding-right: 0; } } }

	.testimonials {
		padding: 22px 0 44px;
		background: $brand-blue;
		color: $brand-white;
		text-align: center;
		.contents {
			@extend .col-xs-12;
			@extend .col-md-offset-1;
			@extend .col-md-10; }
		.quotation {
			margin-top: 20px;
			padding: 0 15px;
			font-size: 18px;
			font-weight: 300;
			margin-bottom: 30px; }
		.quoted-person {
			color: $brand-black-lighter; }
		@include tablet {
			padding: 25px 0;
			.contents {
				padding-left: 30px;
				padding-right: 30px; }
			img {
				float: left;
				margin: 30px 40px 60px 0px; }
			.quotation {
				margin-top: 0;
				font-size: 24px;
				text-align: left;
				margin-bottom: 20px; }
			.quoted-person {
				text-align: left; } }
		@include desktop {
			.contents {
				padding-left: 0;
				padding-right: 0; } }
		@include desktop-xl {
			img {
				margin: 20px 40px 30px 0px; } } }

	.dare-collaborate {
		padding: 25px 0;
		background: $brand-gray-lighter url('../img/roles/cxo/dare-ipad.png') no-repeat 100% 270px;
		text-align: left;
		.contents {
			@extend .col-xs-12; }
		.text-intro {
			@extend .col-sm-offset-6;
			@extend .col-sm-5;
			@extend .col-md-offset-7;
			@extend .col-md-5; }
		h2 {
			margin-bottom: 25px; }
		p {
			color: $brand-black-lighter; }
		p.easy-collaboration {
			margin-top: 20px;
			margin-bottom: 275px;
			color: $brand-navy;
			font-weight: 600;
			font-style: initial;
			display: inline-block; }
		@include tablet {
			padding: 0;
			height: initial;
			background: url('../img/roles/cxo/dare-ipad.png') no-repeat -250px 10%;
			.contents {
				padding: 30px 0; }
			h2 {
				margin-top: 30px; }
			p.easy-collaboration {
				margin-top: 20px;
				margin-bottom: 50px; } }
		@include desktop {
			background: url('../img/roles/cxo/dare-ipad.png') no-repeat -60px 10%;
			h2 {
				margin-top: 10px; } }
		@include desktop-xl {
			background: initial;
			.container {
				background: url('../img/roles/cxo/dare-ipad.png') no-repeat -5% 10%;
				background-size: contain; }
			h2 {
				margin-top: 10px; } } }

	.silos-are-for-farmers {
		padding: 22px 0;
		background: $brand-black-lighter url('../img/roles/cxo/silos-bg-800.png') no-repeat 50% bottom;
		text-align: left;
		.contents {
			@extend .col-xs-12; }
		.intro-text {
			padding-left: 0;
			padding-right: 0;
			text-align: left;
			@extend .col-xs-11;
			@extend .col-sm-12;
			@extend .col-md-4;
			@extend .col-lg-offset-1;
			@extend .col-lg-4; }
		.silos-farming {
			@extend .col-sm-12;
			@extend .col-md-8;
			@extend .col-lg-7; }
		h2 {
			margin-top: 30px; }
		p {
			color: $brand-white;
			margin-bottom: 400px; }
		p.sub {
			position: absolute;
			top: 580px;
			width: 300px;
			color: $brand-black-lighter;
			font-size: 10px;
			line-height: 1.3;
			margin: 0 auto; }
		@include tablet {
			padding: 30px 0 0;
			background: $brand-black-lighter url('../img/roles/cxo/silos-bg-desktopv2.png') no-repeat 70% bottom;
			height: 740px;
			position: relative;
			.contents {
				padding-left: 30px;
				padding-right: 30px; }
			.intro-text {
				padding-left: 0;
				padding-right: 0;
				text-align: left; }
			h2 {
				margin-top: 10px; }
			p {
				margin-bottom: 400px; }
			p.sub {
				color: $brand-lt-grey;
				position: absolute;
				top: 180px;
				width: 220px; } }
		@include desktop {
			padding: 0;
			background: $brand-black-lighter url('../img/roles/cxo/silos-bg-desktopv2.png') no-repeat 60% bottom;
			height: 640px;
			h2 {
				margin: 0;
				margin-top: 40px; }
			p {
				margin: 0;
				margin-top: 20px; }
			img {
				text-align: right; }
			p.sub {
				color: $brand-black-lighter;
				position: relative;
				top: inherit;
				width: 300px;
				margin-top: 105px; }
			.intro-text {
				padding-left: 0; } }

		@include desktop-xl {
			h2 {
				margin-top: 80px; }
			img {
				float: right; }
			p {
				padding-right: 70px; }
			p.sub {
				margin-top: 111px;
				padding-right: 60px;
				width: 380px; } } }


	.strengthen-decisions {
		padding: 30px 0;
		background: $brand-lt-grey url('../img/roles/cxo/strengthen-800.jpg') no-repeat center top;
		color: $brand-black-lighter;
		position: relative;
		text-align: center;
		max-height: 570px;
		.contents {
			@extend .col-xs-12; }
		.intro-text {
			@extend .col-sm-6;
			@extend .col-md-5;
			@extend .col-lg-offset-1;
			@extend .col-lg-4; }
		.product-screenshot {
			@extend .col-sm-6;
			@extend .col-md-7; }
		h2 {
			padding: 0 20px;
			margin: 0 auto; }
		p {
			padding: 0 15px 33px;
			margin-bottom: 30px; }
		@include tablet {
			padding: 40px 0;
			background: $brand-white url('../img/roles/cxo/strengthen-bg-desktop.jpg') no-repeat center top;
			height: 600px;
			text-align: left;
			.contents {
				padding-left: 30px;
				padding-right: 30px; }
			h2 {
				margin: 0;
				margin-top: 20px;
				padding-left: 0; }
			p {
				margin: 0;
				margin-top: 20px;
				padding-left: 0; } }
		@include desktop {
			.intro-text {
				padding-left: 0; } } }
	.resources {
		padding: 33px 0;
		background: $brand-baby-blue;
		text-align: center;

		h2 {
			margin: 0 auto 11px;
			width: 280px;
			text-align: center;
			color: $brand-orange;
			margin-top: 6px; }
		h3 {
			padding: 0;
			font-weight: 200;
			font-size: 18px;
			line-height: 22px;
			text-align: center;
			width: 280px;
			margin: 0 auto; }
		p {
			text-align: left; }
		img {
			width: 100% !important; }

		.contents {
			@extend .col-xs-12; }
		.resource-wrapper {
			@extend .col-sm-4;
			margin-top: 22px; }

		@include tablet {
			h2 {
				width: 100%;
				margin-bottom: 22px; }
			p {
				width: 100%;
				padding: 0 11px 0 0;
				font-size: 16px;
				line-height: 22px; }
			p.last-of-type {
				padding-top: 11px; } }
		@include desktop {
			p {
				font-size: 18px;
				line-height: 26px; }
			img {
				max-height: 153px; } }
		@include desktop-xl {
			img {
				max-height: 188px; } } } }


/* Looking for the Marketo Form: go to _layout.sass */

/* Internationalization styling overrides below */

.smartling-de {
	.cxo {
		.hero.evolve-your-business {
			h1 {
				width: 290px;
				margin-top: 33px;
				margin-bottom: 20px;
				font-size: 24px;
				line-height: 30px; }
			h3 {
				width: 220px; }
			@include tablet {
				h1 {
					width: 580px; }
				h3 {
					width: 540px; }
				.customer-logos {
					height: 66px; } }
			@include desktop-xl {
				h1 {
					margin-top: 66px;
					width: 620px; }
				h3 {
					width: 620px; }
				.customer-logos {
					height: 110px; } } }
		.control-your-future {
			.execs-agree {
				height: 380px; }
			@include tablet {
				height: 600px; }
			@include desktop {
				.control {
					p.sub {
						margin-bottom: 33px; } } }
			@include desktop-xl {
				height: 680px;
				.control {
					p.sub {
						margin-bottom: 22px; } } } } } }
.smartling-fr {
	.cxo {
		.hero.evolve-your-business {
			h1 {
				width: 295px;
				margin-top: 11px; }
			h3 {
				width: 290px; }
			@include tablet {
				h1 {
					width: 700px; } } }
		.silos-are-for-farmers {
			@include desktop-xl {
				h2 {
					margin-top: 44px; } } } } }






