// Custom Styling for Roles > Procure to Payment (P2P) Page
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.p2p {
	.hero.standardize {
		background: url('../img/roles/p2p/hero-800.jpg') no-repeat center bottom;
		height: 400px;
		.contents,
		.customer-logos {
			@extend .col-xs-12; }
		.contents {
			@extend .col-md-offset-1;
			@extend .col-md-11; }
		h1 {
			margin-top: 20px;
			margin-bottom: 15px;
			width: 250px;
			@extend .on-dark; }
		h3 {
			padding-bottom: 20px;
			width: 220px;
			@extend .on-dark; }
		.customer-logos {
			background: url('../img/roles/p2p/customer-logos-800.png') no-repeat center bottom;
			background-size: contain;
			height: 70px;
			z-index: 50; }
		@include bg-customer-logos;
		&::after {
			top: 300px;
			z-index: 40; }
		@include tablet {
			background: url('../img/roles/p2p/hero-p2p-desktop.jpg') no-repeat center top;
			background-size: cover;
			height: 550px;
			.contents {
				padding-left: 30px; }
			&::after {
				top: 450px; }
			h1 {
				margin-top: 80px;
				width: 490px; }
			h3 {
				width: 400px; }
			.customer-logos {
				background: url('../img/roles/p2p/customer-logos-desktop.png') no-repeat center bottom;
				background-size: contain;
				height: 110px;
				z-index: 50; } }
		@include desktop {
			.contents {
				padding-left: 0; }
			h1 {
				width: 490px; }
			h3 {
				width: 380px; } }
		@include desktop-xl {
			background-size: cover; } }

	.make-p2p-effective {
		padding: 20px 0;
		background: $brand-white url('../img/roles/finance/make-p2p-chart-800.png') no-repeat 50% 90%;
		text-align: center;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-offset-2;
			@extend .col-sm-8; }
		h2 {
			margin: 0 auto;
			width: 86%; }
		p {
			margin: 20px auto;
			width:  86%;
			margin-bottom: 160px; }
		@include tablet {
			padding: 60px 0;
			background: $brand-white;
			background-size: cover;
			height: 540px;
			h2 {
				width: 100%; }
			p {
				margin-top: 20px;
				width: 100%;
				margin-bottom: 40px; }
			img {
				width: 100%;
				max-width: 850px; } } }

	.silos-are-for-farmers {
		padding: 20px 0;
		background: $brand-white url('../img/roles/p2p/silos-bg-800.png') no-repeat center bottom;
		color: $brand-black-lighter;
		text-align: center;
		.contents {
			@extend .col-xs-12; }
		.intro-text {
			@extend .col-sm-7; }
		.better-way {
			@extend .col-sm-5; }
		h4 {
			color: $brand-blue;
			padding-bottom: 10px;
			font-size: 14px; }
		p {
			margin-bottom: 30px; }
		p.sub {
			padding: 0;
			color: $brand-dk-grey; }
		hr {
			height: 2px;
			border-bottom: 1px dashed $brand-black;
			border-top: none;
			margin: 0; }
		img {
			width: 240px;
			margin-top: 10px;
			margin-left: 50px; }
		@include tablet {
			background: $brand-white url('../img/roles/p2p/silos-bg.jpg') no-repeat center bottom;
			background-size: cover;
			padding: 60px 0;
			h4 {
				font-size: 18px;
				color: $brand-dk-grey; }
			p.sub {
				font-size: 14px;
				line-height: 1.4;
				margin-top: 5px; }
			.contents {
				text-align: left; }
			.intro-text {
				p {
					padding-right: 50px; } }
			.better-way {
				padding: 0 0 0 0px;
				text-align: center;
				margin-top: 30px; }
			img {
				width: 340px;
				margin-top: 10px; } } }

	.one-process {
		padding: 20px 0;
		background: $brand-white url('../img/roles/p2p/one-process-800.jpg') no-repeat center bottom;
		text-align: center;
		.contents {
			@extend .col-xs-12; }
		.product-screenshot {
			@extend .col-sm-7;
			.imac {
				@extend .pull-right; } }
		.one-process-text {
			@extend .col-sm-5; }

		h2 {
			margin: 0 auto; }
		p {
			margin-bottom: 330px; }
		@include tablet {
			padding: 60px 0;
			background: $brand-white url('../img/roles/p2p/one-process-books-desktop.jpg') no-repeat 100% 100%;
			background-size: cover;
			height: 570px;
			.product-screenshot {}

			.one-process-text {
				text-align: left; }
			p {
				margin-bottom: 0; } }
		@include desktop {
			background: $brand-white url('../img/roles/p2p/one-process-books-desktop.jpg') no-repeat 20% 100%;
			background-size: initial; } }

	.get-better-compliance {
		padding: 20px 0;
		background: $brand-black-lighter url('../img/roles/p2p/map-800.png') no-repeat center 95%;
		text-align: center;
		.contents {
			@extend .col-xs-12; }
		h2 {
			width: 230px;
			margin: 0 auto; }
		p {
			color: $brand-lt-grey;
			padding: 0px 5px;
			margin-bottom: 210px; }
		@include tablet {
			padding: 60px 0;
			background: $brand-black-lighter url('../img/roles/p2p/get-better-map-desktop.png') no-repeat center 85%;
			h2 {
				width: 100%; }
			p {
				padding: 0px 100px;
				margin-bottom: 520px; } } }

	.testimonials {
		background: $brand-blue;
		padding: 20px 0;
		text-align: center;
		.contents {
			@extend .col-xs-11;
			@extend .col-sm-offset-1;
			@extend .col-sm-10; }
		img {
			display: block;
			padding: 10px 0;
			margin: 0 auto;
			width: 77px; }
		.contents {
			@extend .col-xs-12; }
		.quotation {
			font-size: 18px;
			padding: 0 0 5px;
			color: $brand-sky-blue; }
		.quoted-person {
			color: $brand-navy;
			font-size: 12px; }
		@include tablet {
			img {
				float: left;
				margin-right: 20px; }
			.quotation {
				font-size: 26px;
				line-height: 1.2;
				text-align: left; }
			.quoted-person {
				text-align: left;
				font-size: 14px; } } }

	.no-training-required {
		padding: 20px 0;
		background: $brand-white url('../img/roles/p2p/no-training-800.jpg') no-repeat center bottom;
		color: $brand-black-lighter;
		text-align: center;
		min-height: 450px;
		.contents {
			@extend .col-xs-12; }
		.intro-text {
			@extend .col-sm-offset-1;
			@extend .col-sm-5; }
		.persona {
			@extend .col-sm-6; }
		p {
			padding: 0 10px;
			margin-bottom: 300px; }
		@include tablet {
			padding: 20px 0 0;
			background: $brand-white url('../img/roles/p2p/no-training-desktop-bg.jpg') no-repeat center bottom;
			background-size: cover;
			height: 489px;
			text-align: left;
			h2 {
				margin-top: 130px; }
			p {
				padding: 0;
				width: 540px;
				margin-bottom: 0; } } }

	.whip-supplier-data {
		padding: 20px 0;
		background: $brand-white url('../img/roles/p2p/whip-data-800.jpg') no-repeat center top;
		text-align: center;
		color: $brand-white;
		.contents {
			@extend .col-xs-12; }
		h2 {
			margin: 0 auto;
			color: $brand-white; }
		p {
			margin-top: 150px; }
		@include tablet {
			padding: 60px 0;
			background: $brand-white url('../img/roles/p2p/whip-data-desktop.jpg') no-repeat center top;
			background-size: cover;
			height: 500px;
			p {
				margin-top: 20px;
				padding: 0 15%; } } }

	.study-quotes {
		background: $brand-blue;
		padding: 10px 0;
		text-align: center;
		color: $brand-white;
		img {
			display: block;
			padding: 10px 0;
			margin: 0 auto; }
		.contents {
			@extend .col-xs-12; }
		.sub {
			font-size: 10px;
			line-height: 1.3; } }


	.improve-with-right-insights {
		background: #E7E7E7 url('../img/roles/p2p/get-more-done-laptop.png') no-repeat 100% 90%;
		color: $brand-black-lighter;
		text-align: center;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-12;
			padding: 20px 0; }
		.product-screenshot {
			@extend .col-sm-6;
			@extend .hidden-xs;
			.laptop {
				@extend .pull-right; } }
		.text-intro {
			@extend .col-sm-5; }
		p {
			padding: 0 10px;
			margin-bottom: 310px; }
		@include phoneplus {
			background: #E7E7E7 url('../img/roles/p2p/get-more-done-laptop.png') no-repeat 50% 90%; }
		@include tablet {
			padding: 33px 0;
			background: #E7E7E7;
			height: 572px;
			.product-screenshot {
				text-align: right;
				.laptop {
					margin-right: -120px; } }
			.text-intro {
				padding-top: 30px;
				text-align: left; }
			p {
				padding: 0;
				margin-bottom: 0; } }
		@include desktop {
			.text-intro {
				padding-top: 90px; } } }

	.achieve-long-term-goals {
		padding: 20px 0 40px;
		background: $brand-blue url('../img/roles/ap/achieve-faster-mobile.png') no-repeat 50% 95%;
		color: $brand-white;
		text-align: center;
		.contents {
			@extend .col-xs-12; }
		h2 {
			width: 80%;
			margin: 0 auto;
			color: $brand-white; }
		h3 {
			margin: 20px auto 0;
			color: $brand-navy-light; }
		hr {
			@extend hr.dash;
			width: 90%; }
		p {
			padding: 0px 20px; }
		p.sub {
			padding: 0px 35px;
			margin-bottom: 190px; }
		@include tablet {
			.contents {
				background: $brand-blue url('../img/roles/ap/achieve-faster-roi-desktop.png') no-repeat 50% 95%;
				background-size: 90%; }
			padding: 40px 0;
			background: $brand-blue;
			h2 {
				width: 90%; }
			hr {
				width: 50%; }
			p {
				padding: 0px 50px;
				margin: 20px auto; }
			p.sub {
				margin: 10px auto 250px;
				width: 650px;
				font-size: 14px;
				line-height: 1.4; } }
		@include desktop {
			h2 {
				width: 90%; } }
		@include desktop-xl {
			background-size: initial; } }
	.resources {
		padding: 33px 0;
		background: $brand-baby-blue;
		text-align: center;

		h2 {
			margin: 0 auto 11px;
			width: 280px;
			text-align: center;
			color: $brand-orange;
			margin-top: 6px; }
		h3 {
			padding: 0;
			font-weight: 200;
			font-size: 18px;
			line-height: 22px;
			text-align: center;
			width: 280px;
			margin: 0 auto; }
		p {
			text-align: left; }
		img {
			width: 100% !important; }

		.contents {
			@extend .col-xs-12; }
		.resource-wrapper {
			@extend .col-sm-4;
			margin-top: 22px; }

		@include tablet {
			h2 {
				width: 100%;
				margin-bottom: 22px; }
			p {
				width: 100%;
				padding: 0 11px 0 0;
				font-size: 16px;
				line-height: 22px; }
			p.last-of-type {
				padding-top: 11px; } }
		@include desktop {
			p {
				font-size: 18px;
				line-height: 26px; }
			img {
				max-height: 153px; } }
		@include desktop-xl {
			img {
				max-height: 188px; } } } }

/* Looking for the Marketo Form: go to _layout.sass */

.smartling-fr {
	.p2p {
		.hero.standardize {
			@include tablet {
				h1 {
					width: 530px; }
				h3 {
					width: 480px; } } } } }






