// Custom Styling for Delivery Services
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.delivery-services {
	.hero {
		background: $brand-black-lighter url('../img/delivery/hero-bg-xs.jpg') no-repeat top center;
		height: 341px;
		position: relative;
		h1,
		h3 {
			color: $brand-white; }
		h1 {
			width: 290px;
			margin-top: 143px;
			margin-bottom: 22px; }
		h3 {
			width: 290px;
			padding-bottom: 22px; }
		.contents {
			@extend .col-sm-offset-1;
			@extend .col-sm-10;
			@extend .col-lg-offset-2;
			@extend .col-lg-8; }
		.cta-buttons {
			white-space: nowrap; }
		.btn-play-home {
			background: url('../img/delivery/btn-play.png') no-repeat top center;
			width: 110px;
			height: 90px;
			color: $brand-white;
			padding-top: 66px;
			margin-left: 33px;
			margin-bottom: 22px;
			display: block;
			&:hover,
			&:focus,
			&:active {
				cursor: pointer;
				color: $brand-white; } }
		@include phone {
			.contents {
				width: 320px;
				margin: 0 auto;
				float: none; } }
		@include tablet {
			background: $brand-black-lighter url('../img/delivery/hero-bg.jpg') no-repeat top center;
			height: 605px;
			h1 {
				font-size: 38px;
				line-height: 44px;
				width: 560px;
				margin-top: 297px;
				margin-bottom: 11px;
				padding: 0; }
			h3 {
				width: 440px;
				margin-bottom: 0; }

			.btn-play-navy-cta {
				margin-right: 22px; } }

		@include desktop {
			&::after {
				height: 66px; }
			h1 {
				font-size: 48px;
				line-height: 55px;
				width: 480px;
				padding: 0;
				margin-bottom: 11px; }
			h3 {
				width: 440px; }
			.container {
				background-size: contain; }
			.contents {
				padding-left: 0px; }
			.btn-play-home {
				margin-bottom: 33px; } }

		@include desktop-xl {
			background-size: cover;
			height: 715px;
			h1 {
				margin-top: 352px; } } }

	.success-intro {
		padding: 33px 0;
		background: $brand-blue-light;
		text-align: center;
		position: relative;
		h2 {
			color: $brand-blue;
			margin-bottom: 22px;
			padding: 0 22px; }
		p {
			margin: 0 22px 22px; }

		.contents {
			@extend .col-xs-12;
			@extend .col-sm-offset-1;
			@extend .col-sm-10;
			@extend .col-md-offset-2;
			@extend .col-md-8; }

		.arrow {
			position: absolute;
			bottom: 0;
			width: 100%;
			height: 0;
			background-color: $brand-white; }

		.arrow:before,
		.arrow:after {
			content: '';
			position: absolute;
			bottom: 100%;
			width: 50%;
			box-sizing: border-box; }

		.arrow:before {
			right: 50%;
			border-bottom: 26px solid $brand-white;
			border-right: 22px solid $brand-blue-light; }

		.arrow:after {
			left: 50%;
			border-bottom: 26px solid $brand-white;
			border-left: 22px solid $brand-blue-light; }

		@include phone {
			.contents {
				width: 320px;
				margin: 0 auto;
				float: none; } }
		@include tablet {
			padding: 44px 0 66px;
			p {
				padding: 0;
				margin: 0 0 11px; } }

		@include desktop {
			padding: 66px 0;
			h3 {
				font-size: 26px;
				line-height: 33px; } } }

	.success-starts-here {
		padding: 22px 0 11px;
		background: $brand-white;
		text-align: center;
		h2 {
			width: 220px;
			margin: 11px auto 33px; }
		h3 {
			color: $brand-blue;
			font-weight: 300;
			font-size: 18px;
			margin: 11px 0 0; }
		p {
			margin: 0 auto 44px;
			width:  86%; }
		ul {
			@extend .list-unstyled; }
		.contents {
			@extend .col-xs-12; }
		.get-started,
		.jump-in,
		.live-one-week,
		.smooth-sailing,
		.success {
			@extend .col-xs-12;
			text-align: center; }
		@include phone {
			.contents {
				width: 320px;
				margin: 0 auto;
				float: none; } }

		@include tablet {
			padding: 22px 0 44px;
			background: $brand-white;
			h2 {
				width: 100%; }
			.success-steps-container {
				width: 740px;
				height: 530px;
				// background: url('../img/home/success-steps-bg2.png') no-repeat center top
				margin: 66px auto 0;
				position: relative;
				&::before {
					content: '';
					width: 840px;
					height: 530px;
					border: solid 6px $brand-sky-blue;
					border-color: $brand-sky-blue transparent transparent transparent;
					border-radius: 50%/265px 265px 0 0;
					position: absolute;
					top: 0;
					left: -70px;
					right: 0; }
				ul {
					height: 110px; } }
			.get-started,
			.jump-in,
			.live-week,
			.smooth-sailing,
			.success {
				a {
					position: absolute; }
				.step-title {
					position: absolute;
					color: $brand-blue;
					font-size: 18px; } }
			.get-started {
				a {
					top: 66px;
					left: -10px; }
				.step-title {
					top: 164px;
					left: -14px; } }
			.jump-in {
				a {
					top: -11px;
					left: 145px; }
				.step-title {
					top: 90px;
					left: 160px; } }
			.live-week {
				a {
					top: -40px;
					left: 325px; }
				.step-title {
					top: 55px;
					left: 315px; } }
			.smooth-sailing {
				a {
					top: -11px;
					right: 145px; }
				.step-title {
					top: 90px;
					right: 155px; } }
			.success {
				a {
					top: 66px;
					right: -10px; }
				.step-title {
					top: 164px;
					right: -2px; } }

			.success-steps-list {
				.success-trigger {
					i {
						border: 2px solid $brand-blue;
						width: 84px;
						height: 84px;
						background-color: $brand-white;
						border-radius: 50%;
						display: block;
						box-shadow: 0 0 0 7px $brand-white;
						&::before {
							font-family: LinearIcons;
							font-size: 44px;
							color: $brand-blue;
							position: absolute;
							top: 20px;
							left: 0;
							right: 0;
							@include font-smoothing; } }
					i.icon-rocket {
						&::before {
							content: '\e837'; } }
					i.icon-key {
						&::before {
							content: '\e66e'; } }
					i.icon-calendar {
						&::before {
							content: '\e785'; } }
					i.icon-boat {
						&::before {
							content: '\e85b'; } }
					i.icon-trophy {
						&::before {
							content: '\e6e3'; } } }
				&.is-active {
					.success-trigger {
						i {
							border: 2px solid $brand-orange;
							@include transition (all, 1s);
							&::before {
								color: $brand-orange;
								@include transition (color, 1s); } } }
					.step-title {
						color: $brand-orange;
						@include transition (color, 1s); }
					.get-started-desc,
					.jump-in-desc,
					.live-week-desc,
					.smooth-sailing-desc,
					.success-desc {
						opacity: 1;
						@include transition (opacity, 1s); } }
				.get-started-desc,
				.jump-in-desc,
				.live-week-desc,
				.smooth-sailing-desc,
				.success-desc {
					position: absolute;
					color: $brand-blue;
					font-size: 26px;
					padding-top: 88px;
					opacity: 0;
					@include transition (opacity, 1s); }
				.get-started-desc {
					top: 183px;
					left: 265px;
					width: 205px; }
				.jump-in-desc {
					top: 155px;
					left: 253px;
					width: 242px; }
				.live-week-desc {
					top: 193px;
					left: 255px;
					width: 232px; }
				.smooth-sailing-desc {
					top: 163px;
					left: 245px;
					width: 250px; }
				.success-desc {
					top: 203px;
					left: 275px;
					width: 200px; } }

			.success-step-list-container {
				width: 362px;
				height: 362px;
				background: $brand-white;
				border: 2px dashed $brand-blue;
				border-radius: 362px;
				margin: 44px auto 0; }
			.success-step-bg {
				width: 330px;
				height: 330px;
				background: #e9f3fc;
				border-radius: 330px;
				margin: 15px auto; } }

		@include desktop {
			padding: 44px 0 66px;
			.get-started,
			.jump-in,
			.live-week,
			.smooth-sailing,
			.success {
				.step-title {
					font-size: 22px; } }
			.get-started {
				.step-title {
					left: -27px; } }
			.jump-in {
				.step-title {
					left: 149px; } }
			.live-week {
				.step-title {
					left: 297px; } }
			.smooth-sailing {
				.step-title {
					right: 144px; } }
			.success {
				.step-title {
					right: -11px; } } } }
	.testimonials {
		padding: 33px 0;
		background: $brand-blue;
		color: $brand-white;
		text-align: center;
		position: relative;

		blockquote {
			display: inline-block;
			padding: 0 11px;
			margin: 0;
			border: none;
			p {
				font-size: 26px;
				line-height: 33px;
				margin: 0 0 22px 0;
				quotes: "“" "”" "‘" "’";
				&::before {
					content: open-quote; }
				&::after {
					content: close-quote; } }
			footer {
				display: block;
				&::before {
					color: $brand-black-lighter; } }
			cite {
				font-size: 14px;
				font-style: normal;
				color: $brand-black-lighter; } }
		.owl-theme {
			.owl-controls {
				.owl-page {
					span {
						background: $brand-white; } } } }

		.contents {
			@extend .col-sm-offset-1;
			@extend .col-sm-10;
			@extend .col-md-offset-2;
			@extend .col-md-8; }

		@include phone {
			.contents {
				width: 320px;
				margin: 0 auto;
				float: none; } }
		@include tablet {
			padding: 44px 0;
			blockquote {
				p {
					font-size: 34px;
					line-height: 44px;
					font-weight: 200; }
				cite {
					font-size: 18px; } } }
		@include desktop {
			padding: 66px 0; } }

	.steps-implementation {
		padding: 33px 0;
		background: #F2F2F2;
		h2 {
			color: $brand-orange;
			margin-bottom: 44px;
			text-align: center; }
		.pre-contract,
		.contract-date,
		.day-zero,
		.go-live,
		.additional {
			.process-definition {
				@extend .col-sm-5;
				@extend .col-md-offset-1;
				@extend .col-md-4;
				@extend .col-lg-offset-2;
				@extend .col-lg-3; }

			.process-state {
				@extend .col-sm-offset-2;
				@extend .col-sm-5;
				@extend .col-md-offset-2;
				@extend .col-md-5;
				@extend .col-lg-offset-2;
				@extend .col-lg-5; } }

		@include phone {
			.pre-contract,
			.contract-date,
			.day-zero,
			.go-live,
			.additional {
				margin-bottom: 11px;
				.contents {
					position: relative;
					&::before {
						content: ' ';
						position: absolute;
						top: 11px;
						height: 80px;
						width: 68px; }
					&::after {
						content: ' ';
						position: absolute;
						bottom: 0;
						left: 27px; } } }
			.pre-contract,
			.contract-date,
			.day-zero,
			.go-live {
				.contents {
					height: 198px;
					&::after {
						background: url('../img/delivery/steps-path-xs.png') no-repeat;
						height: 106px;
						width: 120px; } } }
			.additional {
				.contents {
					height: 220px;
					&::after {
						background: url('../img/delivery/steps-path-end-xs.png') no-repeat;
						height: 125px;
						width: 120px; } } }
			.pre-contract {
				.contents {
					&::before {
						background: url('../img/delivery/icon-pre-contract-grey.png') no-repeat;
						left: 6px; } } }
			.contract-date {
				.contents {
					&::before {
						background: url('../img/delivery/icon-contract-signing-grey.png') no-repeat;
						left: 6px; } } }
			.day-zero {
				.contents {
					&::before {
						background: url('../img/delivery/icon-project-kickoff-grey.png') no-repeat;
						left: 0; } } }
			.go-live {
				.contents {
					&::before {
						background: url('../img/delivery/icon-go-live-grey.png') no-repeat;
						left: 0; } } }
			.additional {
				.contents {
					&::before {
						background: url('../img/delivery/icon-additional-phases-grey.png') no-repeat;
						left: 2px; } } }

			.contents {
				width: 290px;
				margin: 0 auto;
				float: none;
				padding: 22px 0 0 88px;
				h4 {
					font-size: 14px;
					color: $brand-grey;
					font-weight: 800;
					margin: 11px 0 6px 0;
					padding: 0; }
				h5 {
					font-size: 18px;
					color: $brand-orange;
					font-weight: 200;
					margin: 0;
					padding: 0; }
				p {
					padding: 0 22px 0 0;
					margin: 6px 0 0 0; } } }

		@include tablet {
			padding: 44px 0;
			.container {
				background: url('../img/delivery/path-container-bg.png') no-repeat center 99px;
				height: 902px; }
			.pre-contract,
			.contract-date,
			.day-zero,
			.go-live,
			.additional {
				height: 154px;
				h3 {
					color: $brand-orange;
					font-weight: 200;
					margin-bottom: 11px; }
				h4 {
					font-weight: 700;
					margin: 4px 0 22px 0;
					color: $brand-grey; }
				p {
					line-height: 1.4;
					margin: 0;
					padding-left: 22px; }
				.process-definition {
					text-align: right; }
				.process-state {
					padding-top: 4px; } }

			.go-live {
				height: 176px; } }

		@include desktop {
			padding: 66px 0;
			.pre-contract,
			.contract-date,
			.day-zero,
			.go-live,
			.additional {
				.process-definition {
					padding: 0; }
				p {
					padding-left: 0; } } }

		@include desktop-xl {
			.pre-contract,
			.contract-date,
			.day-zero,
			.go-live,
			.additional {
				p {
					padding-left: 22px; } } } }

	.services-include {
		padding: 33px 0;
		background: $brand-white url('../img/delivery/services-bg-xs.jpg') no-repeat center top;
		height: 605px;
		h2 {
			color: $brand-orange;
			text-align: left;
			margin-bottom: 11px; }
		ul {
			padding-left: 20px; }
		.contents {
			@extend .col-sm-offset-7;
			@extend .col-sm-5;
			@extend .col-lg-offset-8;
			@extend .col-lg-4; }
		@include phone {
			.contents {
				width: 320px;
				margin: 0 auto;
				float: none; } }
		@include tablet {
			padding: 44px 0;
			background: $brand-white url('../img/delivery/services-bg.jpg') no-repeat 60% top;
			height: 605px;
			h2 {
				margin-top: 33px; } }
		@include desktop {
			padding: 66px 0;
			background: $brand-white url('../img/delivery/services-bg.jpg') no-repeat 58% top;
			h2 {
				margin-top: 11px; }
			ul {
				font-size: 18px; } }
		@include desktop-xl {
			background: $brand-white url('../img/delivery/services-bg.jpg') no-repeat center bottom;
			background-size: cover;
			height: 758px; } } }


