.supplychainfinancing.cash {
	@include phone {
		.container {
			width: 320px; } }
	.hero {
		background: url(../img/cash/TS_Cash_hero_v2.png) no-repeat center center;
		background-size: cover;
		.arrow.arrow:before,
		.arrow:after {
			border-bottom: 0;
			border-top: 0; }
		h1 {
			font-size: 44px;
			line-height: 55px;
			color: #1e1e1e;
			font-weight: 700;
			span {
				color: #1e1e1e; } }
		h3 {
			font-size: 26px;
			line-height: 33px;
			color: #282828;
			font-weight: 300; }
		p {
			font-size: 18px;
			line-height: 26px;
			color: #1e1e1e;
			margin-top: 8px; }
		.left-box {
			float: right;
			padding: 0;
			padding-right: 0px; }
		@include desktop {
			sup {
				top: -1.4em;
				font-size: 14px; }
			.left-box {
				width: 38%; }
			.contents {
				padding-top: 110px; } }

		@include tablet-only {
			background: url(../img/cash/hero-image-cash-xs.jpg) no-repeat center center;
			background-size: cover;
			.contents {
				padding-top: 44px; }
			.left-box {
				float: none;
				text-align: center; } }
		@include phone {
			background: url(../img/cash/hero-image-cash-xs.jpg) no-repeat center center;
			background-size: cover;
			height: 680px;
			h1 {
				font-size: 28px;
				line-height: 33px; }
			h3 {
				font-size: 22px;
				line-height: 26px;
				padding: 11px 0; }
			p {
				font-size: 18px;
				margin-top: 5px;
				margin-bottom: 0; }
			.contents {
				padding-top: 44px; }
			.left-box {
				float: none;
				text-align: center; } } }
	.paymentPrograms {
		@include desktop {
			h2.hasPadding {
				padding: 0 200px; } }
		h4 {
			color: #fcbf12; }
		h3 {
			font-size: 24px;
			line-height: 33px;
			margin-top: 22px; }
		@include phone {
			h2 {
				font-size: 26px;
				line-height: 33px; }
			h3 {
				font-size: 14px;
				line-height: 20px; }
			h4 {
				font-size: 23px; } } }
	.testimonials {
		background: #282828;
		padding: 55px 0;
		text-align: center;
		.contents {
			@include make-xs-column(12); }
		img {
			display: block;
			margin: 0 auto;
			width: 88px;
			border-radius: 44px; }
		.contents {
			@include make-xs-column(12); }
		.quotation {
			font-size: 18px;
			padding: 11px 0 5px;
			color: $brand-white;
			text-align: center;
			font-weight: 300;
			margin-bottom: 22px;
			font-style: italic; }
		.quoted-person {
			color: $brand-white;
			font-size: 11px;
			text-align: center;
			font-weight: 400;
			span {
				color: #fcbf12; } }

		@include phone {
			img + img {
				width: 250px;
				margin-top: 22px; }

			.quotation {
				font-size: 26px;
				line-height: 37px;
				text-align: center;
				font-weight: 200;
				padding: 0;
				padding-top: 11px;
				display: block;
				margin-bottom: 22px;
				text-align: center; }
			.quoted-person {
				font-size: 18px; } }


		@include tablet {
			img.headshot-person {
				float: left;
				margin-right: 33px;
				margin-top: 22px;
				width: auto;
				border-radius: 50%;
				display: block; }
			.quotation {
				font-size: 18px;
				line-height: 30px;
				text-align: left;
				font-weight: 200;
				padding: 0;
				display: block;
				&:before {
					content: url('../img/solutions/buyv2/ts-buy-icon-quote-separator.png');
					float: left;
					position: relative;
					right: 18px;
					top: 8px; } }

			.quoted-person {
				text-align: left;
				font-size: 16px;
				line-height: 33px;
				display: table-row; } }
		@include desktop {
			.container {
				padding: 0 88px; }
			.quotation {
				font-size: 26px;
				line-height: 37px;
				text-align: left;
				font-weight: 200;
				padding: 0;
				display: block; } } }

	.preFooterSection {
		background-image: -moz-linear-gradient( 120deg, rgb(255,138,0) 0%, rgb(255,193,7) 100%);
		background-image: -webkit-linear-gradient( 120deg, rgb(255,138,0) 0%, rgb(255,193,7) 100%);
		background-image: -ms-linear-gradient( 120deg, rgb(255,138,0) 0%, rgb(255,193,7) 100%);
		.contents {
			text-align: center; }
		.btn-white {
			background: #ff5200 !important;
			color: #ffffff !important;
			display: inline-block !important; }
		@include desktop {
			p {
				padding: 0 205px; } }
		@include phone {
			h6 {
				font-weight: 900; }
			p {
				font-size: 26px !important;
				line-height: 33px; }
			.btn-white {
				font-size: 18px;
				line-height: 22px;
				padding: 6px 22px !important; } } } }




