// Custom Styling for General Pages
// Includes Thank You Page, Privacy Policy, etc.
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.general-nonav {
	.hero {
		padding: 55px 0;
		.contents {
			@extend .col-xs-12; } }
	h1 {
		font-weight: 300; }
	@include tablet {
		.hero {
			padding: 88px 0; } } }

.thank-you {
	.hero.interest {
		padding: 33px 0;
		background: url('../img/general/hero-bg-800.jpg') no-repeat 40% top;
		height: 560px;
		.contents {
			@extend .col-xs-12;
			@extend .col-lg-offset-1;
			@extend .col-lg-11; }
		h1,
		h3 {
			@extend .on-dark; }
		h1 {
			width: 270px;
			margin-bottom: 10px; }
		h3 {
			width: 240px;
			padding-bottom: 25px; }
		@include tablet {
			background: url('../img/general/hero-bg-desktop.jpg') no-repeat 30% top;
			height: 560px;
			.contents {
				padding-left: 30px; }
			h1 {
				width: 420px;
				margin-top: 55px;
				padding: 0; }
			h3 {
				width: 420px; } }
		@include desktop {
			background: url('../img/general/hero-bg-desktop.jpg') no-repeat 40% top;
			.contents {
				padding-left: 0; } }
		@include desktop-xl {
			background: url('../img/general/hero-bg-desktop.jpg') no-repeat center top;
			background-size: cover; } }

	.explore-resources {
		padding: 44px 0;
		text-align: center;
		.contents {
			@extend .col-xs-12; }
		h2 {
			margin: 0 auto; }

		p {
			margin: 20px auto; }
		.blog,
		.home-page,
		.event {
			@extend .col-xs-12;
			@extend .col-sm-6;
			&:nth-of-type(3) {
				clear: left; }
			margin-top: 22px;
			margin-bottom: 22px;
			text-align: left;

			h4 {
				font-size: 14px;
				font-weight: 600;
				text-align: left;
				margin: 22px 0 5px; }
			img {
				width: 100%;
				height: 100%; }

			.intro-text {
				text-align: left;
				font-size: 14px;
				a {
					color: $brand-black; } } }
		.event {
			button {
				margin-top: 22px; }
			.intro-text {
				a {
					color: $brand-blue; } } }
		@include tablet {
			.blog,
			.home-page,
			.event {
				overflow: hidden; } }

		@include desktop {
			p {
				padding: 0 66px; } } }

	.stay-know {
		padding: 44px 0;
		background: $brand-black-lighter;
		text-align: center;
		max-height: 820px;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-12; }
		h2 {
			color: $brand-blue;
			width: 66%;
			margin: 0 auto; }
		p {
			margin-bottom: 22px;
			padding: 0 22px;
			color: $brand-white; }
		a {
			margin-right: 20px;
			&:last-child {
				margin-right: 0; } }
		img {
			width: 50px; }

		@include tablet {
			h2 {
				width: initial; }
			img {
				width: 77px; } } }

	.take-our-word-for-it {
		padding: 44px 0;
		text-align: center;
		.contents {
			@extend .col-xs-12; }
		h2 {
			margin: 0 auto;
			width: 66%; }

		p {
			margin: 22px auto 33px;
			padding: 0 33px; }

		@include tablet {
			h2 {
				width: initial; }
			p {
				margin: 22px auto 40px; } }
		@include desktop {
			p {
				width: 830px; } } }

	.you-know-your-job {
		padding: 68px 0;
		background: url('../img/general/smb-bg-800.jpg') no-repeat center top;
		text-align: center;
		height: 462px;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-offset-6;
			@extend .col-sm-6;
			@extend .col-lg-offset-7;
			@extend .col-lg-5; }
		h2 {
			color: $brand-blue;
			margin-bottom: 22px;
			padding: 0 44px; }
		p {
			padding: 0 22px;
			margin-bottom: 66px;
			color: $brand-white; }
		@include tablet {
			background: url('../img/general/smb-bg-desktop.jpg') no-repeat 30% top;
			padding: 44px 0;
			text-align: left;
			h2 {
				padding: 0; }
			p {
				padding: 0;
				color: $brand-grey;
				margin-bottom: 22px; } }
		@include desktop {
			h2 {
				margin-top: 22px; } }
		@include desktop-xl {
			background: url('../img/general/smb-bg-desktop.jpg') no-repeat left top;
			background-size: cover; } }

	.testimonials {
		padding: 25px 0;
		background: $brand-blue;
		color: $brand-white;
		text-align: center;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-offset-1;
			@extend .col-sm-10; }
		.quotation {
			margin-top: 20px;
			font-size: 18px;
			font-weight: 300;
			margin-bottom: 30px; }
		.quoted-person {
			color: $brand-black-lighter; }
		@include tablet {
			img {
				float: left;
				margin: 40px 40px 135px 0px; }
			.quotation {
				margin-top: 0;
				font-size: 24px;
				text-align: left;
				margin-bottom: 20px; }
			.quoted-person {
				text-align: left; } }
		@include desktop {
			img {
				margin: 40px 40px 75px 0px; } }
		@include desktop-xl {
			img {
				margin: 20px 40px 50px 0px; } } } }

.thank-you-video {
	.hero {
		background: url(../img/general/video-bg.jpg) no-repeat !important;
		background-size: cover !important; } }
.custom-404 {
	background: url('../img/general/404bg.jpg') no-repeat 50% 50%;
	background-size: cover;
	padding: 33px 0;
	min-height: 800px;

	h1 {
		color: $brand-white;
		font-weight: 200;
		margin-bottom: 22px;
		text-align: center; }
	p {
		color: $brand-white;
		text-align: center;
		padding: 0 22px;
		a {
			color: $brand-white;
			font-weight: 600; } }

	.contents {
		@extend .col-xs-12;
		@extend .col-sm-offset-1;
		@extend .col-sm-10;
		@extend .col-md-offset-2;
		@extend .col-md-8; }

	.white-space {
		display: block;
		height: 110px; }

	@include tablet {
		h1 {
			margin-top: 66px; } }
	@include desktop {
		h1 {
			margin-top: 88px; } }

	/* Looking for the Marketo Form: go to _layout.sass */ }

.privacy-policy-page,
.tos-page,
.tou-page {
	padding: 33px 0;
	.contents {
		@extend .col-xs-12;
		@extend .col-sm-12;
		@extend .col-md-offset-1;
		@extend .col-md-10; }
	h2 {
		margin-bottom: 22px; }
	h3 {
		margin-bottom: 11px; }
	p,
	table {
		font-size: 14px;
		line-height: 22px; }
	ol {
		padding-left: 0;
		counter-reset: item;
		li {
			font-size: 14px;
			line-height: 22px;
			display: block;
			&::before {
				content: counters(item, ".") ".";
				counter-increment: item;
				padding-right: 7px; } }
		ol {
			padding-left: 20px;
			margin-top: 11px;
			li {
				margin-bottom: 22px;
				font-size: 14px;
				font-weight: normal;
				text-transform: none; } } }
	hr.dash {
		height: 33px;
		margin-top: 33px; } }








