// Custom Styling for eProcurement page
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.eprocurement-page {
	position: relative;
	h1,
	h2,
	h3,
	p,
	small {
		color: $brand-white; }
	h3 {
		font-weight: 300; }
	em {
		font-weight: 700;
		font-style: normal;
		text-transform: uppercase; }
	.path-white {
		width: 255px;
		height: 2481px;
		position: absolute;
		top: 413px;
		left: 0;
		right: 0;
		margin: 0 auto;
		z-index: 10;
		background: url('../img/eprocurement/path-xs.png') no-repeat;
		@include tablet {
			top: 377px;
			left: 66px;
			background: url('../img/eprocurement/path-sm.png') no-repeat;
			background-size: 690px;
			width: 690px;
			height: 2243px; }
		@include desktop {
			top: 330px;
			left: 55px;
			background: url('../img/eprocurement/path-md.png') no-repeat;
			background-size: 940px;
			width: 940px;
			height: 2243px; }
		@include desktop-xl {
			left: 88px;
			background: url('../img/eprocurement/path-lg.png') no-repeat;
			background-size: 1029px;
			width: 1029px;
			height: 2243px; } }
	.path-orange {
		position: absolute;
		top: 413px;
		left: 0;
		right: 0;
		margin: 0 auto;
		z-index: 9;
		background: url('../img/eprocurement/path-xs-orange2.png') no-repeat;
		width: 255px;
		height: 2569px;
		@include tablet {
			top: 377px;
			left: 66px;
			background: url('../img/eprocurement/path-sm-orange.png') no-repeat;
			background-size: 690px;
			width: 690px;
			height: 2243px; }
		@include desktop {
			top: 330px;
			left: 55px;
			background: url('../img/eprocurement/path-md-orange.png') no-repeat;
			background-size: 940px;
			width: 940px;
			height: 2243px; }
		@include desktop-xl {
			left: 88px;
			background: url('../img/eprocurement/path-lg-orange.png') no-repeat;
			background-size: 1029px;
			width: 1029px;
			height: 2243px; } }

	.hero.video {
		padding: 77px 0;
		height: 506px;
		position: relative;
		text-align: center;
		background: $brand-black-lighter;
		h1 {
			margin-bottom: 11px; }
		h3 {
			margin-bottom: 33px;
			padding: 0 22px; }
		p.sub {
			font-size: 14px;
			line-height: 22px;
			margin-top: 33px; }
		.contents {
			@extend .col-sm-12;
			z-index: 380;
			position: relative; }
		.video-bg {
			ul {
				@extend .list-unstyled;
				li {
					img,
					video {
						height: 100%;
						position: absolute;
						top: 0;
						opacity: 0;
						@include transition (opacity, 1s); }
					img {
						left: 50%;
						margin-left: -400px; }
					&.active {
						img,
						video {
							opacity: 1;
							@include transition (opacity, 1s); } } } }

			&::after {
				content: '';
				background: $brand-black-lighter;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				width: 100%;
				height: 100%;
				opacity: 0.6; } }
		@include phone {
			.contents {
				width: 320px;
				margin: 0 auto; } }
		@include tablet {
			padding: 88px 0;
			height: 506px;
			overflow: hidden;
			text-align: center;
			h1 {
				margin: 0 auto 11px;
				width: 528px; }
			h3 {
				margin: 0 auto 44px;
				width: 660px; }

			.video-bg {
				ul {
					li {
						img {
							margin-left: -960px;
							width: 1920px; }
						video {
							top: 0;
							left: 0;
							right: 0;
							width: auto;
							height: 100%; } } } } }

		@include desktop {
			height: 506px;
			overflow: hidden;
			text-align: center;
			h1 {
				margin-bottom: 11px;
				width: 100%; }
			h3 {
				margin-bottom: 44px;
				width: 770px; }
			.video-bg {
				ul {
					li {
						img {
							margin-left: -960px;
							width: 1920px;
							left: 50%; }
						video {
							width: 100%;
							height: auto; } } } } } }

	.step-1 {
		padding: 319px 0 0 0;
		background: $brand-navy;
		height: 671px;
		.contents {
			@extend .col-sm-offset-5;
			@extend .col-sm-7;
			@extend .col-md-offset-4;
			@extend .col-md-8;
			h3 {
				margin-bottom: 22px; }
			p {
				padding-right: 22px;
				margin-bottom: 22px;
				em {
					font-style: italic;
					font-weight: normal;
					text-transform: none; } } }
		@include phone {
			.contents {
				width: 320px;
				margin: 0 auto;
				padding-right: 33px; } }
		@include tablet {
			padding: 77px 0;
			height: 341px;
			.contents {
				h3,
				p {
					margin-bottom: 22px; }
				h3 {
					font-size: 22px; }
				p {
					font-size: 16px;
					line-height: 22px;
					width: 428px; } } }
		@include desktop {
			padding: 121px 0 0;
			height: 462px;
			.contents {
				padding-left: 22px;
				h3 {
					font-size: 33px;
					line-height: 44px; }
				p {
					font-size: 18px;
					padding-right: 0; } } }
		@include desktop-xl {
			padding: 143px 0 0;
			height: 506px;
			.contents {
				padding-left: 66px; } } }

	.step-2 {
		padding: 308px 0 0 0;
		background: $brand-mid-blue;
		height: 715px;
		.contents {
			@extend .col-sm-7;
			h3,
			p {
				margin-bottom: 22px; } }
		@include phone {
			.contents {
				width: 320px;
				margin: 0 auto;
				text-align: right;
				padding: 0 55px 0 44px; } }
		@include tablet {
			padding: 66px 0;
			height: 352px;
			.contents {
				text-align: right;
				h3 {
					font-size: 22px; }
				p {
					font-size: 16px;
					line-height: 22px;
					padding-left: 3%; } } }
		@include desktop {
			padding: 132px 0 0;
			height: 473px;
			.contents {
				h3 {
					font-size: 33px;
					line-height: 44px; }
				p {
					font-size: 18px;
					padding-left: 11%; } } }
		@include desktop-xl {
			padding: 154px 0 0;
			height: 506px;
			.contents {
				p {
					padding-left: 30%; } } } }


	.step-3 {
		padding: 297px 0 0 0;
		background: $brand-blue;
		height: 550px;
		.contents {
			@extend .col-sm-offset-5;
			@extend .col-sm-7;
			@extend .col-md-offset-4;
			@extend .col-md-8;
			h3,
			p {
				margin-bottom: 22px; } }
		@include phone {
			.contents {
				width: 320px;
				margin: 0 auto;
				text-align: left;
				padding: 0 44px 0 55px; } }
		@include tablet {
			padding: 77px 0;
			height: 330px;
			.contents {
				h3 {
					font-size: 22px;
					margin-bottom: 11px; }
				p {
					font-size: 16px;
					line-height: 22px;
					width: 418px;
					margin-bottom: 11px; } } }
		@include desktop {
			padding: 132px 0 0;
			height: 472px;
			.contents {
				h3 {
					font-size: 33px;
					line-height: 44px;
					margin-bottom: 22px; }
				p {
					font-size: 18px;
					padding-right: 0;
					margin-bottom: 22px; } } }
		@include desktop-xl {
			padding: 187px 0 0;
			height: 538px;
			.contents {
				padding-left: 66px; } } }

	.procurecon {
		background: $brand-black-lighter url('../img/eprocurement/procurecon-bg-xs.jpg') no-repeat center top;
		background-size: cover;
		position: relative;
		height: 1079px;
		padding: 66px 0 0;
		em {
			text-transform: uppercase;
			font-weight: 400;
			display: inline-block;
			font-size: 22px; }
		&::before {
			content: '';
			background: $brand-black-lighter;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			width: 100%;
			height: 100%;
			opacity: 0.6; }
		.contents {
			@extend .col-sm-12;

			h3,
			p {
				padding-right: 33px;
				margin-bottom: 22px; }
			p {
				font-size: 14px;
				line-height: 22px;
				padding-left: 0; }
			p.sub {
				font-size: 11px;
				padding: 11px 0;
				line-height: 1.2;
				text-align: center;
				z-index: 1010;
				position: relative; }
			.btn {
				margin-right: 55px;
				margin-top: 5px;
				z-index: 410;
				text-align: center !important;
				margin-bottom: 33px; }
			.tweet-for-trip,
			.twitter-feed {
				@extend .col-xs-12;
				@extend .col-sm-6;
				padding: 0; }
			.tweet-for-trip {
				padding-bottom: 11px;
				text-align: right; }
			.tweet-for-trip-wrapper {
				@extend .col-md-12;
				padding-left: 7px; }
			.twitter-feed {
				height: 340px;
				background: url('../img/eprocurement/tweets-container-bg.png') no-repeat center top;
				background-size: contain;
				z-index: 450;
				.tweets-container {
					padding: 0 22px;
					height: 240px;
					overflow-x: scroll;
					margin: 55px 22px 0;
					width: 230px;
					// show the scrollbar on overflow
					// &::-webkit-scrollbar
					//	-webkit-appearance: none
					//	width: 7px
					// &::-webkit-scrollbar-thumb
					//	border-radius: 4px
					// 	background-color: $brand-dk-grey
					// &::-webkit-scrollbar-corner
					// 	background-color: $brand-black
					ul {
						@extend .list-unstyled;
						word-wrap: break-word;
						font-size: 12px;
						line-height: 16px; }
					.user {
						img[data-scribe='element:avatar'] {
							display: none; }
						span[data-scribe='element:name'] {
							font-weight: 800;
							margin-right: 3px;
							color: $brand-white; }
						span[data-scribe='element:screen_name'] {
							color: $brand-white; } }
					.timePosted {
						color: $brand-white;
						margin: 0;
						padding: 0;
						font-size: 12px; }
					.tweet {
						font-size: 12px;
						line-height: 18px;
						font-style: italic;
						padding: 0;
						margin: 0 0 22px 0; } } }

			.tweet-and-win {
				position: absolute;
				bottom: 44px;
				left: 0;
				right: 0;
				margin: 0 auto;
				text-align: center;

				p {
					text-align: left;
					padding: 11px 15px 0;
					line-height: 18px;
					width: 320px;
					margin: 0 auto; } } }
		@include phone {
			.contents {
				position: relative;
				width: 320px;
				margin: 0 auto;
				padding: 0 15px;
				z-index: 520; } }
		@include tablet {
			padding: 44px 0 0;
			background: $brand-black-lighter url('../img/eprocurement/procurecon-bg.jpg') no-repeat center top;
			background-size: 1920px;
			height: 462px;
			position: relative;
			&::after {
				content: ' ';
				text-align: center;
				margin: 0 auto;
				width: 26px;
				height: 16px;
				border-style: solid;
				border-width: 16px 13px 0 13px;
				border-color: $brand-blue transparent transparent transparent;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				z-index: 450; }

			.contents {
				.tweet-for-trip {
					text-align: center;
					.tweet-for-trip-wrapper {
						max-width: 369px; } }
				.twitter-feed {
					height: 374px;
					.tweets-container {
						background-size: 398px;
						padding: 0;
						height: 242px;
						margin: 77px auto 0;
						width: 231px; } }
				h3 {
					font-size: 22px;
					line-height: 33px;
					padding: 0;
					margin-top: 0;
					margin-bottom: 0; }
				p {
					font-size: 14px;
					line-height: 18px;
					padding: 0; }
				.btn {
					z-index: 1010;
					position: relative;
					font-weight: 500;
					margin-right: 0;
					margin-top: 0;
					margin-bottom: 11px; }
				p.sub {
					font-size: 11px;
					line-height: 1.4;
					margin-top: 22px;
					padding: 0; } } }

		@include desktop {
			padding: 77px 0 0;
			height: 594px;
			background-size: cover;
			&::after {
				content: ' ';
				width: 64px;
				height: 32px;
				border-style: solid;
				border-width: 32px 32px 0 32px; }
			.contents {
				h3 {
					font-size: 33px;
					line-height: 44px;
					padding: 0;
					margin-top: 11px;
					em {
						font-size: 33px;
						display: block;
						letter-spacing: -0.02em; } }
				p {
					font-size: 18px;
					line-height: 22px;
					padding: 0;
					letter-spacing: -0.02em; }
				p.sub {
					padding: 0 33px; }
				.twitter-feed {
					height: 462px;
					.tweets-container {
						height: 324px;
						margin: 77px auto 0;
						width: 286px;
						ul {
							li {
								font-size: 14px;
								line-height: 22px; } }
						.tweet {
							font-size: 18px;
							line-height: 22px; } } }
				.tweet-for-trip {
					.tweet-for-trip-wrapper {
						max-width: 100%; } }
				.btn {
					margin-right: 44px;
					margin-top: 5px;
					margin-bottom: 33px; } } }
		@include desktop-xl {
			.contents {
				h3 {
					margin-top: 0; }
				p {
					padding: 0 11px; }
				.tweet-for-trip {
					.tweet-for-trip-wrapper {
						padding: 0 55px 0 0; }
					.btn {
						margin-right: 0;
						margin-top: 11px; }
					p.sub {
						margin-top: 44px; } } } } }

	.fill-out-form {
		padding: 55px 0;
		text-align: center;
		.form-subscribe {
			@extend .col-sm-12;
			form {
				padding: 22px 0;
				display: inline-block;
				width: 260px; }
			h2 {
				margin-bottom: 11px; }
			p {
				color: $brand-black-lighter;
				line-height: 22px; } }

		@include phone {
			.form-subscribe,
			.sub {
				width: 320px;
				margin: 0 auto; } }
		@include tablet {
			.form-subscribe {
				padding: 0 110px;
				p {
					font-size: 14px;
					line-height: 18px;
					padding: 0 66px; }
				form {
					padding: 11px 0;
					width: 450px; } } }
		@include desktop {
			.form-subscribe {
				p {
					padding: 0 121px; }
				form {
					padding: 22px 0 0; }
				.newsletter-subscription {
					margin-left: 0 !important;
					width: 55% !important; } } } } }


