// Custom Styling for Roles > IT Page
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.it {
	.hero.right-platform {
		background: url('../img/roles/it/hero-800.jpg') no-repeat bottom;
		height: 500px;
		.contents,
		.customer-logos {
			@extend .col-xs-12; }
		.contents {
			@extend .col-md-offset-1;
			@extend .col-md-11; }
		h1,
		h3 {
			@extend .on-dark; }
		h1 {
			width: 235px;
			margin-top: 50px;
			margin-bottom: 10px; }
		h3 {
			width: 240px;
			padding-bottom: 25px; }
		.customer-logos {
			background: url('../img/roles/it/customer-logos-800.png') no-repeat center bottom;
			background-size: contain;
			height: 100px;
			z-index: 50; }
		@include bg-customer-logos;
		&::after {
			top: 400px;
			z-index: 40; }
		@include tablet {
			background: url('../img/roles/it/hero-desktop.jpg') no-repeat center top;
			height: 560px;
			.contents {
				padding-left: 30px; }
			&::after {
				top: 460px; }
			h1 {
				width: 560px;
				margin-top: 60px;
				padding: 0; }
			h3 {
				width: 540px; }
			.customer-logos {
				background: url('../img/roles/it/customer-logos-desktop.png') no-repeat center bottom;
				background-size: contain;
				height: 110px;
				z-index: 50; } }
		@include desktop {
			.contents {
				padding-left: 0px; } }
		@include desktop-xl {
			background-size: cover; } }

	.awesome-ui {
		padding: 25px 0;
		background: $brand-white url('../img/roles/it/awesome-800.jpg') no-repeat center top;
		text-align: center;
		height: 550px;
		.contents {
			@extend .col-xs-offset-1;
			@extend .col-xs-10;
			@extend .col-sm-12; }
		h2 {
			margin: 0 auto; }
		p {
			margin: 20px auto;
			margin-bottom: 30px; }
		@include tablet {
			padding: 20px 0;
			background: $brand-white url('../img/roles/it/awesome-desktop.jpg') no-repeat center top;
			height: 635px;
			text-align: left;
			h2 {
				width: 290px;
				margin: 0;
				margin-top: 100px; }
			p {
				margin: 0;
				margin-top: 20px;
				width: 183px; } }
		@include desktop {
			p {
				width: 300px; } }
		@include desktop-xl {
			background-size: cover; } }
	.apps-customize {
		padding: 25px 0;
		background: $brand-white;
		color: $brand-black-lighter;
		text-align: left;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-12; }
		.text-intro {
			@extend .col-md-offset-6;
			@extend .col-md-6; }
		h2 {
			color: $brand-blue;
			margin-bottom: 25px; }
		p {
			margin-bottom: 20px; }
		ol {
			margin-top: 30px;
			padding-left: 20px; }
		ol > li {
			padding-left: 10px;
			margin-bottom: 10px; }
		@include tablet {
			padding: 40px 0;
			height: 770px;
			.contents {
				background: url('../img/roles/it/use-app-desktop.png') no-repeat center bottom;
				padding-bottom: 407px; } }
		@include desktop {
			padding: 60px 0;
			height: 550px;
			.contents {
				background: url('../img/roles/it/use-app-desktop.png') no-repeat 5% 0%;
				padding-bottom: 0; }
			.text-intro {
				padding-left: 100px;
				padding-right: 50px;
				margin-bottom: 50px; } } }

	.safeguard-data {
		padding: 25px 0;
		background: $brand-blue;
		color: $brand-white;
		text-align: left;
		.contents {
			@extend .col-xs-12; }
		h2 {
			@extend .on-dark;
			margin-bottom: 25px; }
		p {
			margin-bottom: 20px; }
		img {
			width: 80%; }
		@include tablet {
			.text-intro {
				float: left;
				margin-bottom: 50px; }
			h2 {
				margin: 0;
				margin-top: 40px; }
			p {
				margin: 0;
				margin-top: 20px;
				width: 340px; }
			img {
				float: right;
				width: 50%;
				margin-top: 40px;
				margin-bottom: 40px; } }
		@include desktop-xl {
			h2 {
				margin-top: 80px; }
			p {
				width: 480px; }
			img {
				width: initial; } } }
	.rapid-delivery {
		padding: 25px 0;
		background: $brand-black-lighter;
		color: $brand-white;
		text-align: center;
		max-height: 820px;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-12; }
		h2 {
			color: $brand-blue; }
		p {
			margin-bottom: 20px; }
		img {
			margin: 0 auto; }
		@include tablet {
			padding: 60px 0;
			.contents {
				background: url('../img/roles/it/rapid-desktop.png') no-repeat 50% 75%;
				background-size: 90%; }
			p {
				padding: 0px 60px 480px; } }
		@include desktop-xl {
			height: 760px;
			.contents {
				background: url('../img/roles/it/rapid-desktop.png') no-repeat 50% 60%;
				background-size: initial; }
			p {
				padding: 0 180px 560px; } } }

	.plug-play {
		padding: 380px 0 40px;
		background: $brand-lt-grey url('../img/roles/it/plug-play-800.jpg') no-repeat center top;
		color: $brand-black-lighter;
		text-align: left;
		max-height: 700px;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-offset-7;
			@extend .col-sm-5; }
		h2 {
			color: $brand-blue;
			margin-bottom: 20px; }
		@include tablet {
			padding: 40px 0;
			height: 510px;
			background: $brand-lt-grey url('../img/roles/it/plug-play-desktop.jpg') no-repeat 50% 50%; }
		@include desktop {
			h2 {
				margin-top: 40px; } } }


	.testimonials {
		padding: 25px 0;
		background: $brand-blue;
		color: $brand-white;
		text-align: center;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-offset-1;
			@extend .col-sm-10; }
		.quotation {
			margin-top: 20px;
			font-size: 18px;
			font-weight: 300;
			margin-bottom: 30px; }
		.quoted-person {
			color: $brand-black-lighter; }
		@include tablet {
			img {
				float: left;
				margin: 40px 40px 70px 0px; }
			.quotation {
				margin-top: 0;
				font-size: 24px;
				text-align: left;
				margin-bottom: 20px; }
			.quoted-person {
				text-align: left; } }
		@include desktop-xl {
			img {
				margin: 20px 40px 50px 0px; } } }

	.resources {
		padding: 33px 0;
		background: $brand-baby-blue;
		text-align: center;

		h2 {
			margin: 0 auto 11px;
			width: 280px;
			text-align: center;
			color: $brand-orange;
			margin-top: 6px; }
		h3 {
			padding: 0;
			font-weight: 200;
			font-size: 18px;
			line-height: 22px;
			text-align: center;
			width: 280px;
			margin: 0 auto; }
		p {
			text-align: left; }
		img {
			width: 100% !important; }

		.contents {
			@extend .col-xs-12; }
		.resource-wrapper {
			@extend .col-sm-4;
			margin-top: 22px; }

		@include tablet {
			h2 {
				width: 100%;
				margin-bottom: 22px; }
			p {
				width: 100%;
				padding: 0 11px 0 0;
				font-size: 16px;
				line-height: 22px; }
			p.last-of-type {
				padding-top: 11px; } }
		@include desktop {
			p {
				font-size: 18px;
				line-height: 26px; }
			img {
				max-height: 153px; } }
		@include desktop-xl {
			img {
				max-height: 188px; } } } }


/* Looking for the Marketo Form: go to _layout.sass */

.smartling-fr {
	.it {
		.hero.right-platform {
			h1 {
				width: 290px; }
			h3 {
				width: 280px; }
			.customer-logos {
				height: 60px; }
			@include tablet {
				h1 {
					width: 760px; }
				h3 {
					width: 640px; }
				.customer-logos {
					height: 110px; } } } } }





