h1,
h2,
h3,
h4,
p {
	font-family: $font-family-heading;
	font-weight: 200; }
h1 {
	font-size: 34px;
	line-height: 44px; }
h2 {
	font-size: 26px;
	line-height: 33px; }
h3 {
	font-size: 20px;
	line-height: 33px; }
p {
	font-size: 14px;
	line-height: 22px; }

@include tablet {
	h1 {
		font-size: 40px;
		line-height: 44px; }
	h2 {
		font-size: 30px;
		line-height: 44px; }
	h3 {
		font-size: 24px;
		line-height: 33px; }
	p {
		font-size: 18px;
		line-height: 33px; } }
@include desktop {
	h1 {
		font-size: 50px;
		line-height: 66px; }
	h2 {
		font-size: 40px;
		line-height: 55px; }
	h3 {
		font-size: 24px;
		line-height: 33px; }
	p {
		font-size: 18px;
		line-height: 33px; } }
@include desktop-xl {
	h1 {
		font-size: 60px;
		line-height: 77px; }
	h2 {
		font-size: 44px;
		line-height: 55px; }
	h3 {
		font-size: 34px;
		line-height: 44px; } }



