// Custom Styling for Roles - Procurement - CAO
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.roles-procurement {
	.hero {
		padding: 44px 0;
		background: $brand-white;
		position: relative;
		text-align: left;
		h1,
		h3 {
			color: $brand-black;
			margin: 0;
			padding: 0; }
		h1 {
			margin-bottom: 11px;
			em {
				color: $brand-blue;
				font-style: normal;
				font-weight: 800; } }
		h3 {
			color: $brand-black;
			letter-spacing: -0.01em;
			margin-bottom: 22px; }
		p {
			color: $brand-black; }
		.contents {
			@include make-xs-column(12) /* Server side .contents only for xs */; }

		@include phone {
			width: 290px;
			margin: 0 auto;
			float: none;
			text-align: left; }
		@include desktop {
			height: 671px;
			background: $brand-black-lighter;
			h1,
			h3 {
				color: $brand-white; }
			h3 {
				font-size: 18px;
				line-height: 33px;
				padding-right: 88px; }
			.headline-cta {
				position: relative;
				top: 44px;
				z-index: 40;
				.contents {
					@include make-md-column(6);
					@include make-lg-column(6); } }
			.case-studies {
				width: 100%;
				position: absolute;
				top: 0;
				z-index: 30;
				h2 {
					color: $brand-black-lighter;
					text-transform: uppercase;
					font-size: 26px;
					line-height: 33px;
					font-weight: 700;
					text-align: center;
					margin: 0 auto;
					padding: 0 55px;
					em {
						font-size: 18px;
						line-height: 22px;
						font-weight: 700;
						color: $brand-blue;
						display: block;
						font-style: normal;
						text-transform: uppercase; } }
				p {
					text-align: center;
					margin: 0; }
				.contents {
					@include make-md-column(12);
					padding-left: 0;
					padding-right: 0; }
				.item {
					height: 671px;
					position: relative;
					.content-wrapper {
						@include make-md-column-offset(6);
						width: 568px;
						height: 568px;
						opacity: 0.8;
						background: $brand-white;
						border-radius: 50%; }

					&.dollar-shave-club {
						background: $brand-black-lighter url('../img/roles/procurement/hero-bg-shave.jpg') no-repeat center top;
						padding: 44px 0;
						.content-wrapper {
							padding: 88px 77px; } }
					&.airbnb {
						background: $brand-black-lighter url('../img/roles/procurement/hero-bg-airbnb.jpg') no-repeat center top;
						padding: 44px 0;
						.content-wrapper {
							padding: 88px 66px; } }
					&.spacex {
						background: $brand-black-lighter url('../img/roles/procurement/hero-bg-spacex.jpg') no-repeat 40% top;
						padding: 44px 0;
						.content-wrapper {
							padding: 66px 44px; } } }
				.owl-controls {
					position: absolute;
					bottom: 22px;
					left: 0;
					right: 0;
					.owl-page {
						span {
							background: $brand-white; } } } } }
		@include desktop-xl {
			.headline-cta {
				top: 88px; }
			.case-studies {
				.item {
					.content-wrapper {
						@include make-md-column-offset(7); }
					&.spacex {
						background: $brand-black-lighter url('../img/roles/procurement/hero-bg-spacex.jpg') no-repeat center top; } } } }

		@include desktop-1920 {
			height: 770px;
			.case-studies {
				.item {
					height: 770px;
					&.dollar-shave-club {
						background: $brand-black-lighter url('../img/roles/procurement/hero-bg-shave.jpg') no-repeat center top;
						background-size: cover; }
					&.airbnb {
						background: $brand-black-lighter url('../img/roles/procurement/hero-bg-airbnb.jpg') no-repeat center top;
						background-size: cover; }
					&.spacex {
						background: $brand-black-lighter url('../img/roles/procurement/hero-bg-spacex.jpg') no-repeat center bottom;
						background-size: cover; } } } } }


	/* Case Study section/selector is for smartphone view only. On desktop it's integrated into .hero */
	.case-study-xs {
		padding: 0;
		background: $brand-black-lighter;
		position: relative;
		h2 {
			font-size: 18px;
			line-height: 22px;
			font-weight: 700;
			text-align: center;
			color: $brand-white;
			width: 264px;
			margin: 0 auto 11px;
			border-bottom: 5px solid $brand-white;
			padding: 220px 15px 5px;
			text-transform: uppercase;
			em {
				font-size: 14px;
				color: $brand-blue;
				display: block;
				font-style: normal; } }
		p {
			color: $brand-white;
			text-align: center;
			width: 300px;
			margin: 0 auto; }
		.item {
			height: 616px;
			position: relative;
			.content-wrapper {
				position: absolute;
				bottom: 66px;
				left: 0;
				right: 0; }
			&.dollar-shave-club {
				background: url('../img/roles/procurement/hero-bg-xs-shave.jpg') no-repeat center top;
				padding-bottom: 66px; }
			&.airbnb {
				background: url('../img/roles/procurement/hero-bg-xs-airbnb.jpg') no-repeat center top;
				padding-bottom: 66px; }
			&.spacex {
				background: url('../img/roles/procurement/hero-bg-xs-spacex.jpg') no-repeat center top;
				padding-bottom: 66px; } }
		.owl-carousel {
			position: relative; }
		.owl-theme {
			.owl-controls {
				margin: 0 auto;
				text-align: center;
				position: absolute;
				right: 0;
				left: 0;
				bottom: 22px;
				.owl-page {
					span {
						background: $brand-white; } } } }
		.arrow {
			position: absolute;
			top: 0;
			width: 100%;
			height: 0;
			background-color: $brand-white; }

		.arrow:before,
		.arrow:after {
			content: '';
			position: absolute;
			top: 100%;
			width: 50%;
			box-sizing: border-box; }

		.arrow:before {
			right: 50%;
			border-bottom: 22px solid transparent;
			border-right: 22px solid $brand-white; }

		.arrow:after {
			left: 50%;
			border-bottom: 22px solid transparent;
			border-left: 22px solid $brand-white; }

		@include tablet {
			p {
				width: 600px; }
			.contents {
				padding-left: 0;
				padding-right: 0; }
			.item {
				.content-wrapper {}
				&.dollar-shave-club {
					background: url('../img/roles/procurement/hero-bg-xs-shave.jpg') no-repeat center top;
					background-size: cover; }
				&.airbnb {
					background: url('../img/roles/procurement/hero-bg-xs-airbnb.jpg') no-repeat center top;
					background-size: cover; }
				&.spacex {
					background: url('../img/roles/procurement/hero-bg-xs-spacex.jpg') no-repeat center top;
					background-size: cover; } } } }

	.new-normal {
