// Custom Styling for Jobs Page - En
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.jobs-page {
	.hero {
		background: $brand-white url('../img/jobsv2/hero-bg2-xs.jpg') no-repeat 76% 50%;
		height: 462px;
		position: relative;

		h1 {
			margin-top: 88px;
			margin-bottom: 11px;
			color: $brand-white;
			text-shadow: 1px 1px #3e3f3f; }
		h3 {
			color: $brand-white; }

		.contents {
			@include make-xs-column(9);
			@include make-md-column(12); }

		@include phone {
			h1 {
				font-size: 34px;
				line-height: 42px;
				width: 220px; }
			h3 {
				width: 220px; }
			.watch-video {
				margin-bottom: 22px; } }

		@include tablet {
			background: $brand-white url('../img/jobsv2/herobg-md.jpg') no-repeat 70% 50%;
			height: 550px;
			h1 {
				margin-top: 154px; }
			h3 {
				font-weight: 300;
				width: 77%; }
			.watch-video {
				color: $brand-white;
				font-size: 18px;
				margin-top: 22px;
				font-weight: 400;
				img {
					padding-right: 11px; } } }

		@include desktop {
			background: $brand-white url('../img/jobsv2/herobg-md.jpg') no-repeat 68% 50%;
			height: 561px;
			h1 {
				margin-top: 132px; }
			h3 {
				font-size: 26px;
				line-height: 33px;
				color: $brand-white;
				width: 506px; }
			.contents {
				padding-left: 44px; } }

		@include desktop-xl {
			background-size: cover; } }

	.sticky-wrapper {
		position: relative;
		z-index: 1000; }
	.section-menu {
		padding: 11px 15px;
		box-shadow: 0px 2px 11px rgba(100,100,100,.2);
		border-bottom: 1px solid #DDD;
		display: inline-block;
		width: 100%;
		position: relative;
		z-index: 1000;
		background: $brand-white;
		&.stuck {
			position: fixed;
			top: 55px;
			background: $brand-white;
			width: 100%;
			z-index: 1000; }
		.section-nav {
			float: left;
			a {
				color: $brand-blue;
				font-size: 12px;
				font-weight: 600;
				text-transform: uppercase; } }
		.dropdown {
			position: static;
			&.open {
				position: static; }
			.dropdown-menu {
				width: 100%;
				border: 0;
				border-radius: 0;
				margin: 0;
				li {
					padding-bottom: 4px;
					a {
						padding-left: 15px; } } } }
		.open-positions {
			float: right; }
		@include tablet {
			height: 55px;
			box-shadow: 0px 5px 15px rgba(100,100,100,.25);
			.section-nav {
				ul {
					float: none;
					padding: 5px;
					li {
						float: left;
						padding-right: 22px;
						list-style: none; } } }
			.open-positions {
				margin-top: 5px; } }
		@include desktop {
			.open-positions {
				margin-top: 3px;
				margin-right: 5px; } } }

	.build-amazing {
		padding: 44px 0;
		background: $brand-white;
		position: relative;
		text-align: center;
		.contents {
			@include make-xs-column(12);
			@include make-lg-column-offset(1);
			@include make-lg-column(10); }
		h2 {
			margin-bottom: 33px;
			color: $brand-orange; }
		@include tablet {
			p {
				font-weight: 200; } }
		@include desktop {
			p {
				padding: 0;
				font-weight: 300; } } }

	.culture-thing {
		position: relative;
		background: $brand-navy;
		h2 {
			color: $brand-orange;
			margin-top: 22px; }
		p {
			color: $brand-white; }
		blockquote {
			border: none;
			padding: 0;
			margin: 0;
			font-size: 18px;
			font-weight: 200;
			color: $brand-white;
			&::before,
			&::after {
				content: '"';
				font-size: 18px; } }
		.owl-controls {
			margin: 22px auto; }
		@include phone {
			text-align: center;
			p,
			blockquote {
				width: 290px;
				margin: 0 auto;
				padding: 11px; }
			img {
				margin-left: -50%; }
			.item {
				padding: 220px 0 0 0; }
			#item1 {
				background: url('../img/jobsv2/culture-1-xs.jpg') no-repeat top center; }
			#item2 {
				background: url('../img/jobsv2/culture-2-xs.jpg') no-repeat top center; }
			#item3 {
				background: url('../img/jobsv2/culture-3-xs.jpg') no-repeat top center; }
			#item4 {
				background: url('../img/jobsv2/culture-4-xs.jpg') no-repeat top center; } }

		@include tablet {
			.imagery {
				float: left;
				width: 47%;
				overflow: hidden;
				img {
					width: 120%; } }
			.description {
				float: right;
				width: 52%;
				padding-left: 22px;
				padding-right: 11px;
				p {
					line-height: 26px; } }
			.owl-controls {
				display: inline;
				position: absolute;
				right: 36%;
				bottom: 0; } }
		@include desktop {
			.imagery {
				width: 50%;
				overflow: visible;
				img {
					width: 100%; } }
			.description {
				width: 48%;
				padding: 22px;
				p {
					line-height: 33px; } }
			blockquote {
				font-size: 22px;
				&::before,
				&::after {
					content: '"';
					font-size: 22px; } } }
		@include desktop-xl {
			.imagery {
				img {
					width: 100%; } }
			.description {
				width: 46%; }
			blockquote {
				font-size: 26px;
				padding-top: 22px;
				&::before,
				&::after {
					content: '"';
					font-size: 26px; } }
			.owl-controls {
				bottom: 44px; } } }

	.hey-gang {
		padding: 33px 0 44px;
		position: relative;
		text-align: center;
		h2 {
			color: $brand-orange; }
		h3 {
			color: $brand-blue;
			margin-top: 22px;
			font-size: 18px;
			font-weight: 200;
			line-height: 24px; }
		h4 {
			color: $brand-blue;
			text-transform: uppercase;
			font-size: 12px;
			line-height: 22px;
			font-weight: 700; }
		img {
			width: 110px;
			margin-top: 33px; }
		.contents {
			@include make-lg-column-offset(1);
			@include make-lg-column(10); }
		.tech,
		.gcs,
		.sales,
		.people-ops {
			@include make-xs-column(12);
			@include make-sm-column(6); }
		@include tablet {
			padding: 33px 0 0;
			h3 {
				font-size: 26px; }
			h4 {
				font-size: 14px;
				margin-top: 11px; }
			.tech,
			.gcs,
			.sales,
			.people-ops {
				margin-bottom: 22px;
				p {
					padding: 0 22px;
					font-size: 14px;
					line-height: 22px; } } }
		@include desktop {
			padding: 66px 0;
			.tech,
			.gcs,
			.sales,
			.people-ops {
				p {
					padding: 0 77px; } } }

		@include desktop-xl {
			.tech,
			.gcs,
			.sales {
				p {
					padding: 0 66px; } }
			.people-ops {
				p {
					padding: 0 55px; } } } }

	.office-photos {
		padding: 44px 0 0 0;
		text-align: center;
		h2 {
			color: $brand-orange;
			margin-bottom: 22px; }
		.contents,
		.offices-other {
			@extend .col-xs-12; }
		.sf,
		.cph,
		.london,
		.suzhou {
			@include make-xs-column(12);
			@include make-sm-column(6);
			img {
				width: 100%; } }
		.offices-other {
			p {
				padding: 22px 0;
				margin: 0 auto;
				text-align: center;
				background: $brand-black;
				color: $brand-white; } }

		@include phone {
			.contents,
			.sf,
			.cph,
			.london,
			.suzhou,
			.offices-other {
				padding: 0; } }

		@include tablet {
			.sf,
			.cph,
			.london,
			.suzhou {
				padding: 0; } } }

	.perks-benefits {
		padding: 44px 0;
		h2 {
			color: $brand-orange; }
		h3 {
			color: $brand-blue; }
		img {
			margin: 22px 0; }
		.contents {
			@include make-xs-column(12);
			@include make-md-column-offset(1);
			@include make-md-column(10); }
		.grow,
		.team,
		.future,
		.give-back {
			@include make-sm-column(6); }

		.us-xs-perks,
		.eu-xs-perks,
		.cn-xs-perks,
		.us-sm-perks,
		.eu-sm-perks,
		.cn-sm-perks {
			display: none;
			&.active {
				display: block; } }
		@include phone {
			text-align: center; }
		@include tablet {
			padding: 66px 0;
			text-align: center;
			.nav-tabs {
				text-align: center;
				border-bottom: 2px dotted $brand-lt-grey;
				margin-top: 33px;
				padding: 22px 0;
				li {
					text-align: center;
					display: table-cell;
					float: none;
					margin-bottom: 22px;
					&.grow-tab,
					&.team-tab,
					&.future-tab,
					&.heart-tab {
						.img-title {
							&::before {
								content: '';
								margin: 0 auto;
								text-align: center;
								height: 110px;
								display: block; } } }
					&.grow-tab {
						.img-title {
							&::before {
								background: url('../img/jobsv2/icon-grow-blue.png') no-repeat top center; } } }
					&.team-tab {
						.img-title {
							&::before {
								background: url('../img/jobsv2/icon-team-blue.png') no-repeat top center; } } }
					&.future-tab {
						.img-title {
							&::before {
								background: url('../img/jobsv2/icon-future-blue.png') no-repeat top center; } }
						.title {
							padding: 0 15px; } }
					&.heart-tab {
						.img-title {
							&::before {
								background: url('../img/jobsv2/icon-heart-blue.png') no-repeat top center; } }
						.title {
							padding: 0 15px; } }
					&.grow-tab.active {
						.img-title {
							&::before {
								background: url('../img/jobsv2/icon-grow-orange.png') no-repeat top center; } } }
					&.team-tab.active {
						.img-title {
							&::before {
								background: url('../img/jobsv2/icon-team-orange.png') no-repeat top center; } } }
					&.future-tab.active {
						.img-title {
							&::before {
								background: url('../img/jobsv2/icon-future-orange.png') no-repeat top center; } } }
					&.heart-tab.active {
						.img-title {
							&::before {
								background: url('../img/jobsv2/icon-heart-orange.png') no-repeat top center; } } }
					a {
						color: $brand-blue;
						width: 176px;
						font-size: 18px;
						border: none; }
					img {
						display: block;
						margin: 0 auto 22px;
						text-align: center; }

					&.active {
						background: none;
						a {
							color: $brand-orange; } } } }
			.tab-content {
				.tab-pane {
					&.active {
						p {
							color: $brand-orange;
							padding: 22px 33px 0;
							text-align: justify; } } } } }

		@include desktop {
			.nav-tabs {
				li {
					a {
						width: 187px; } } } }
		@include desktop-xl {
			.nav-tabs {
				li {
					a {
						width: 220px; } } } } }
	.day-in-the-life {
		padding: 44px 0;
		background: $brand-baby-blue;
		text-align: center;
		h2 {
			margin: 0 auto 22px;
			text-align: center;
			color: $brand-blue; }
		.contents {
			@include make-xs-column(12); }
		.story {
			@include make-lg-column-offset(1);
			@include make-lg-column(10);
			margin-bottom: 33px; }

		@include desktop {
			padding: 66px 0;
			img {
				width: 100%; } } }

	.footer-open-positions {
		padding: 44px 0;
		background: $brand-white;
		h2 {
			margin: 0 auto 11px;
			color: $brand-blue;
			text-align: center; }
		h3 {
			font-weight: 300;
			line-height: 24px;
			margin-bottom: 33px;
			text-align: center;
			padding: 0 15px; }
		h4 {
			font-size: 16px;
			font-weight: 600;
			margin: 22px 0 11px;
			color: $brand-black-lighter; }
		h5 {
			font-size: 12px;
			font-weight: 600;
			margin-bottom: 6px;
			color: $brand-navy; }
		.filter-review-wrapper {
			@include make-sm-column-offset(2);
			@include make-sm-column(8);
			@include make-md-column-offset(2);
			@include make-md-column(9);
			.filter-wrapper {
				@include make-xs-column(7);
				@include make-md-column(8);
				padding-left: 0;
				padding-right: 0; }
			.review-wrapper {
				@include make-xs-column(5);
				@include make-xs-column(4);
				img {
					width: 90px; } } }
		.jobs_container {
			@include make-xs-column(12);
			@include make-sm-column-offset(2);
			@include make-sm-column(9);
			min-height: 600px; }
		.jobs-listing {
			@include make-xs-column(12);
			padding-right: 0;
			padding-left: 0;
			margin-bottom: 11px;
			.jobs-link {
				display: block;
				.jobs-pos {
					@include make-xs-column(8);
					@include make-sm-column(7);
					@include make-md-column(8);
					padding-left: 0;
					padding-right: 30px; }
				.jobs-dep {
					@include make-xs-column(4);
					@include make-sm-column(5);
					@include make-sm-column(4);
					padding-right: 0;
					padding-left: 0; } } }
		@include phone {
			.contents {
				width: 320px;
				margin: 0 auto;
				float: none; } }
		@include tablet {
			h4 {
				padding-left: 15px; }
			h5 {
				font-size: 16px; }
			.filter-review-wrapper {
				.filter-wrapper {
					padding-left: 15px; } }
			.jobs-listing {
				.jobs-link {
					font-size: 16px;
					.jobs-pos {
						padding-left: 15px;
						padding-right: 60px; }
					.jobs-dep {
						padding-right: 15px;
						padding-left: 15px; } } } }
		@include desktop {
			padding: 66px 0;
			h3 {
				margin-bottom: 44px; }
			h4 {
				font-size: 18px;
				margin-top: 11px; }
			h5 {
				font-size: 18px;
				margin-bottom: 22px; }
			.filter-review-wrapper {
				margin-bottom: 22px;
				.review-wrapper {
					img {
						width: 180px; } } }
			.jobs-listing {
				float: none;
				display: inline-block; }
			.jobs-link {
				font-size: 18px;
				line-height: 33px;
				.jobs-pos {
					padding-right: 15px; } } } } }


