.ppc-template-main {
	background-image: -moz-linear-gradient( -90deg, rgb(231,231,231) 0%, rgb(255,255,255) 100%);
	background-image: -webkit-linear-gradient( -90deg, rgb(231,231,231) 0%, rgb(255,255,255) 100%);
	background-image: -ms-linear-gradient( -90deg, rgb(231,231,231) 0%, rgb(255,255,255) 100%);
	h1 {
		font-size: 34px;
		line-height: 44px;
		font-weight: 700; }
	p {
		font-weight: 300;
		&.lead {
			font-size: 24px;
			line-height: 33px; } }
	.btn-orange {
		font-size: 18px;
		line-height: 24px;
		text-transform: initial;
		font-weight: 600;
		margin: 22px auto; }
	.form-header {
		color: $brand-p-ent;
		font-size: 28px;
		line-height: 40px;
		padding-bottom: 22px;
		text-align: center; }
	.content {
		&-wrap {
			margin: 66px 0 88px;
			width: 100%; }
		&-left {
			padding-bottom: 66px;
			@include make-md-column(7);
			li {
				font-weight: 300;
				font-size: 18px;
				line-height: 26px;
				padding-bottom: 11px; } }
		&-right {
			position: relative;
			box-shadow: 0px 0px 9px 1px rgba(0, 0, 0, 0.15);
			float: right;
			min-height: 200px;
			width: 400px;
			background: $brand-white;
			padding: 33px;
			margin-top: 44px;
			.mktoButton {
				background: $brand-s-apps !important; } } }
	.fader {
		-moz-transition: opacity 0.7s linear;
		-o-transition: opacity 0.7s linear;
		-webkit-transition: opacity 0.7s linear;
		transition: opacity 0.7s linear; }
	.fader.fadedOut {
		opacity: 0; }
	.fader.fadedIn {
		opacity: 1; }
	#spinnerDiv {
		text-align: center;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%); }
	#spinner {
		font-size: 24px;
		display: inline-block;
		margin: auto;
		color: #1E1E1E; }
	.the-content {
		font-size: 18px !important;
		line-height: 26px !important;
		font-weight: 300 !important;
		padding-top: 22px;
		.dash {
			margin-top: 66px; } }
	@include tablet-and-phone {
		.content-wrap {
			margin-bottom: 33px; }
		.the-content {
			&-left {
				padding-bottom: 44px; }
			.dash {
				margin-top: 44px; } } }
	@include tablet-only {
		.content-left {
			width: 650px;
			display: block;
			margin: auto; } }
	@include phone {
		h1 {
			font-size: 24px;
			line-height: 30px; }
		p {
			font-size: 14px;
			line-height: 20px;
			.lead {
				font-size: 18px;
				line-height: 24px;
				margin-top: 11px; } }
		.content-left {
			li {
				font-size: 14px;
				line-height: 20px; }
			.the-content {
				font-size: 14px !important;
				line-height: 20px !important; } } }
	.join-the-club {
		display: block;
		content: "";
		clear: both;
		padding: 66px 15px 0 15px;
		h3 {
			padding-top: 11px;
			font-size: 36px;
			line-height: 40px; }
		.intro-text {
			float: left;
			width: 340px; }
		.purple-rect {
			background-image: -moz-linear-gradient( -37deg, rgb(166,2,97) 0%, rgb(98,2,166) 100%);
			background-image: -webkit-linear-gradient( -37deg, rgb(166,2,97) 0%, rgb(98,2,166) 100%);
			background-image: -ms-linear-gradient( -37deg, rgb(166,2,97) 0%, rgb(98,2,166) 100%);
			width: 533px;
			min-height: 353px;
			font-size: 28px;
			line-height: 38px;
			color: $brand-white;
			padding: 33px 50px;
			float: left;
			position: relative;
			left: -22px;
			z-index: 9; }
		.carousel-container {
			position: relative;
			right: 0vw; }
		.carousel-inner {
			padding-bottom: 22px; }
		#customerCarousel {
			float: left; }
		.img-carousel {
			float: left;
			position: relative;
			top: 172px;
			z-index: 10; }
		.quoted-person {
			font-size: 14px;
			line-height: 20px;
			padding-top: 11px;
			&--company {
				font-weight: 700;
				display: block;
				text-indent: 15px; } }

		// carousel indicators override
		.carousel-indicators {
			margin-left: -26%;
			width: 533px;
			li {
				background-color: #1f3d55;
				border: 1px solid #1f3d55; }
			.active {
				background-color: $brand-white;
				border: 1px solid $brand-white; }
			@include tablet {
				bottom: 22px; }
			@include desktop-sm {
				width: 400px;
				margin-left: -20%; } }
		.carousel-container {
			min-height: 375px; }
		@include desktop-sm {
			.purple-rect {
				width: 400px;
				height: auto; }
			.carousel-container {
				min-height: 590px; } }
		@include tablet-and-phone {
			.intro-text {
				float: none;
				text-align: center;
				display: block;
				margin: auto;
				padding-bottom: 11px; }
			#customerCarousel {
				float: none;
				display: block;
				margin: auto; }
			.purple-rect {
				left: 0;
				width: 375px;
				height: auto;
				float: none;
				margin: auto;
				text-align: center;
				font-size: 22px;
				line-height: 33px;
				padding: 33px 44px 66px;
				display: block; }
			.item-container {
				display: flex;
				flex-direction: column-reverse;
				align-items: center; }
			.img-carousel {
				float: none;
				display: flex;
				top: -11px;
				width: 198px; }
			.carousel-indicators {
				width: 100%;
				margin-left: -50%;
				bottom: 240px; }
			.rectangle-purple-gradient {
				display: block;
				margin: auto; }
			.quoted-person {
				&--title:after {
					content: ","; }
				&--name {
					display: inline; } } }
		@include tablet-only {
			.purple-rect {
				width: 375px;
				font-size: 22px;
				line-height: 33px; }
			.carousel-container {}
			min-height: 568px; }
		@include phone {
			padding-top: 0;
			.intro-text {
				width: 100%; }
			.purple-rect {
				width: 100%;
				font-size: 20px;
				line-height: 28px;
				padding: 22px 22px 66px; }
			.carousel-container {
				min-height: 560px; } } }
	.contact-us-form {
		text-align: left;
		h4 {
			font-weight: 700; }
		p {
			text-align: left;
			font-size: 34px !important;
			line-height: 44px !important; }
		.contents {
			position: relative;
			width: 750px;
			margin: auto;
			display: block; }
		.gartner-logo {
			position: absolute;
			right: 0;
			bottom: 11px; }
		a.btn-cta, div.btn-cta > a {
			display: inline-block;
			border-radius: 22px;
			background-color: $brand-white;
			color: #ff5200;
			padding: 8px 55px;
			font-size: 18px;
			font-weight: 600;
			margin-top: 22px; }
		@include tablet-only {
			text-align: center;
			p.lead {
				text-align: center; }
			a.btn-cta {
				margin-top: 66px !important; }
			.gartner-logo {
				right: 20%;
				bottom: 66px; }
			.contents {
				width: 600px; } }
		@include phone {
			padding: 44px 0;
			h4,
			p.lead {
				font-size: 24px !important;
				line-height: 30px !important;
				margin-top: 11px; }
			a.btn-cta {
				margin-top: 44px !important; }
			.gartner-logo {
				width: 85px;
				right: 10%;
				bottom: 25%; }
			.contents {
				width: 100%; } } } }




