// Custom Styling for Press Page
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.press-page,
.press-page-detail {
	q,
	blockquote {
		display: inline-block;
		padding: 0 11px;
		margin: 0 0 22px 0;
		font-size: 16px;
		border: none;
		quotes: "“" "”" "‘" "’";
		&::before {
			content: open-quote; }
		&::after {
			content: close-quote; } }
	.hero.making-headlines {
		padding: 44px 0 0;
		background: $brand-navy url('../img/press-ts/hero-bg-xs.jpg') no-repeat center top;
		height: 440px;
		position: relative;
		text-align: center;
		.contents,
		.press-quotes {
			@extend .col-xs-12; }
		h1 {
			margin-bottom: 22px;
			color: $brand-white; }
		h3 {
			color: $brand-white; }

		.btn-primary-cta {
			margin: 44px 0;
			cursor: pointer; }
		.press-quotes {
			@extend .col-xs-12;
			padding: 0 33px;
			.quote-list {}
			q,
			blockquote {
				color: $brand-white; }
			img {
				width: 120px; } }
		@include tablet {
			padding: 99px 0 0;
			background: $brand-navy url('../img/press-ts/hero-bg.jpg') no-repeat center top;
			height: 550px;
			.btn-primary-cta {
				margin: 44px 0 66px 0; }
			.press-quotes {
				padding: 0 110px;
				img {
					width: 160px; } } }

		@include desktop {
			padding: 122px 0 0;
			background: $brand-navy url('../img/press-ts/hero-bg.jpg') no-repeat center top;
			background-size: 1920px;
			height: 605px;
			.press-quotes {
				padding: 0 196px; } }

		@include desktop-xl {
			background-size: cover; } }

	.press-contact {
		padding: 44px 0 66px;
		background: $brand-blue;
		position: relative;
		color: $brand-white;
		text-align: center;
		h2,
		h4,
		h5 {
			color: $brand-white; }
		h2 {
			padding: 0 44px;
			margin-bottom: 44px; }
		h4 {
			font-size: 18px;
			font-weight: 600;
			line-height: 22px; }
		h5 {
			font-size: 16px;
			line-height: 22px;
			padding: 0 44px; }
		a {
			color: $brand-white;
			display: block;
			line-height: 22px; }
		img {
			border-radius: 50%;
			margin-bottom: 22px; }
		&::after {
			content: ' ';
			text-align: center;
			margin: 0 auto;
			width: 33px;
			height: 22px;
			border-style: solid;
			border-width: 22px 22px 0 22px;
			border-color: $brand-navy transparent transparent transparent;
			position: absolute;
			top: 0;
			left: 0;
			right: 0; }
		.first-contact {
			@extend .col-xs-12;
			text-align: center;
			img {
				display: block;
				margin: auto; } }
		.contact-info {
			margin-top: 22px; }
		.media-kit {
			@extend .col-xs-12;
			text-align: center;
			margin-top: 44px;
			a {
				border-bottom: 1px dashed $brand-white;
				padding-bottom: 2px;
				display: inline; } }
		@include tablet {
			h5 {
				padding: 0; }
			.second-contact {
				margin-top: 0; } } }
	.faq {
		a {
			font-size: 34px; }
		@include phone {
			font-size: 20px; } } }

.press-page {
	.press-release {
		padding: 44px 0;
		background: $brand-navy;
		text-align: center;
		h2 {
			color: $brand-white;
			text-align: center;
			margin-bottom: 44px; }
		.pr-list {
			@extend .col-xs-12;
			@extend .col-md-4;
			color: $brand-white;
			margin-bottom: 22px;
			text-align: left;
			.pr-title {
				font-weight: 600;
				font-size: 16px;
				margin-bottom: 11px;
				a {
					color: $brand-white;
					&:hover {
						color: $brand-blue; } } }
			.pr-teaser {
				a {
					white-space: nowrap; } }
			.collapsing {
				position: relative;
				height: 0;
				overflow: hidden;
				@include transition-property(height, visibility);
				@include transition-duration(2s);
				@include transition-timing-function(ease); } }
		.btn-pr {
			font-size: 14px !important;
			margin: 33px auto 0;
			clear: both;
			&::before {
				content: 'Hide all press releases'; } }

		.btn-pr.collapsed {
			&::before {
				content: 'Show all press releases'; } }
		@include tablet {
			h2 {
				margin-bottom: 11px; }
			.pr-list {
				margin-top: 33px; } }
		@include desktop {
			.pr-list {
				min-height: 222px; }
			.btn-pr {
				margin: 0 auto; } }
		@include desktop-xl {
			.pr-list {
				min-height: 200px; } } }

	.press-mention {
		padding: 44px 0;
		background: $brand-white;
		position: relative;
		&::after {
			content: ' ';
			text-align: center;
			margin: 0 auto;
			width: 33px;
			height: 22px;
			border-style: solid;
			border-width: 22px 22px 0 22px;
			border-color: $brand-navy transparent transparent transparent;
			position: absolute;
			top: 0;
			left: 0;
			right: 0; }
		h2 {
			color: $brand-blue;
			text-align: center;
			padding: 0 33px;
			margin-bottom: 44px; }

		.pm-list {
			text-align: center;
			padding: 0 22px;
			@extend .col-xs-12;
			@extend .col-sm-4;
			.pm-mentioned-by-logo {
				img {
					max-width: 180px;
					text-align: center;
					margin-bottom: 6px; } }
			q,
			blockquote {
				a {
					color: $brand-black-lighter; } } }
		@include tablet {
			.pm-list {
				height: 154px;
				.pm-mentioned-by-logo {
					height: 55px;
					img {
						margin: 0 auto 11px;
						@include vertical-align; } } } } }
	.blog-feed {
		padding: 44px 0;
		border-top: 1px dashed $brand-blue;
		h2 {
			text-align: center;
			color: $brand-blue;
			padding: 0 22px; }
		h4 {
			margin: 0;
			a {
				font-size: 16px;
				line-height: 22px;
				color: $brand-black-lighter;
				font-weight: 600; } }
		article {
			@extend .col-xs-12;
			@extend .col-sm-4;
			margin-top: 22px;
			img {
				width: 100%;
				height: auto; }
			p {
				margin: 0; }
			.pub-date {
				font-weight: 600;
				font-size: 12px;
				text-transform: uppercase;
				padding-right: 6px; }
			.author {
				font-size: 12px;
				text-transform: uppercase;
				position: relative;
				padding-left: 14px;
				&::before {
					font-family: FontAwesome;
					content: '\f111';
					font-size: 6px;
					position: absolute;
					top: -2px;
					left: 0;
					color: $brand-black-lighter;
					@include font-smoothing; } } }
		@include tablet {
			h2 {
				margin-bottom: 22px; }
			article {
				.author {
					&::before {
						top: -7px; } } } } }

	.video-feed {
		padding: 44px 0;
		border-top: 1px dashed $brand-blue;
		h2 {
			text-align: center;
			color: $brand-blue; }
		.video-list {
			@extend .col-xs-12;
			@extend .col-sm-4;
			margin-top: 44px;
			.video-tn {
				position: relative;
				img {
					cursor: pointer; }
				&::before {
					font-family: LinearIcons;
					content: '\e96a';
					position: absolute;
					bottom: 16px;
					right: 11px;
					font-size: 55px;
					color: $brand-white;
					line-height: 1;

					/* Enable Ligatures */
					-webkit-font-feature-settings: "liga";
					-moz-font-feature-settings: "liga=1";
					-moz-font-feature-settings: "liga";
					-ms-font-feature-settings: "liga" 1;
					-o-font-feature-settings: "liga";
					font-feature-settings: "liga";
					@include font-smoothing; } }
			.meta {
				margin: 22px 0 11px;
				text-transform: uppercase;
				font-size: 12px;
				.media-type {
					color: $brand-orange;
					font-weight: 600; } }
			h4 {
				margin: 0;
				font-weight: 600; }
			p {
				margin: 0;
				font-size: 14px;
				line-height: 22px; } }
		@include tablet {
			.video-list {
				h4 {
					margin-bottom: 6px; } } } }

	.press-award {
		padding: 44px 0;
		background: $brand-navy;
		position: relative;
		color: $brand-sky-blue;
		h2 {
			text-align: center;
			color: $brand-white;
			font-weight: 400; }
		h4 {
			margin: 0;
			font-size: 22px;
			line-height: 33px;
			a {
				color: $brand-sky-blue; } }
		p {
			margin: 0;
			a {
				color: $brand-white; } }
		.pa-list {
			position: relative;
			margin-top: 44px;
			padding: 0 22px 0 55px;
			@extend .col-xs-12;
			@extend .col-sm-6;
			@extend .col-md-4;

			&::before {
				content: '\e6dd';
				font-family: $font-linearicons;
				color: $brand-sky-blue;
				position: absolute;
				top: 0;
				left: 0;
				font-size: 44px;
				line-height: 1.2; } }
		@include tablet {
			.pa-list {
				height: 110px;
				p {
					padding-right: 22px; } } }
		@include desktop {
			.pa-list {
				height: 110px;
				p {
					padding-right: 0; } } }
		@include desktop-xl {
			.pa-list {
				height: 110px;
				p {
					padding-right: 22px; } } } } }

.press-page-detail {
	.hero.making-headlines {
		padding: 33px 0 0;
		height: 242px;
		@include tablet {
			padding: 66px 0 0;
			background: $brand-navy url('../img/press-ts/hero-bg.jpg') no-repeat center bottom;
			height: 330px; }

		@include desktop {
			background: $brand-navy url('../img/press-ts/hero-bg.jpg') no-repeat center bottom;
			background-size: 1920px; }

		@include desktop-xl {
			background-size: cover; } }
	.press-release-detail {
		padding: 44px 0;
		h2 {
			color: $brand-white;
			text-align: center;
			margin-bottom: 44px; }
		h3 {
			font-weight: 600; }
		h4 {
			font-weight: 700; }
		.contents {
			@extend .col-xs-12;
			@extend .col-lg-offset-1;
			@extend .col-lg-10; }
		.teaser {
			font-size: 18px;
			font-style: italic;
			padding: 11px 0 0; }
		.date {
			font-size: 12px;
			margin: 22px 0;
			color: $brand-orange; }
		@include tablet {
			.press-release-detail {
				.teaser {
					font-size: 18px; }
				.date {
					font-size: 14px; } } } }
	.press-contact {
		&::after {
			content: ' ';
			text-align: center;
			margin: 0 auto;
			width: 33px;
			height: 22px;
			border-style: solid;
			border-width: 22px 22px 0 22px;
			border-color: $brand-white transparent transparent transparent;
			position: absolute;
			top: 0;
			left: 0;
			right: 0; } } }





