// Custom Styling for Solutions - Risk
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.ts-risk2 {
	// Styling used globally on this page
	sup {
		font-size: 11px;
		top: -.8em; }

	.hero {
		padding: 22px 0 0;
		background: $brand-lt-grey url('../img/solutions/risk/hero-bg-xs.jpg') no-repeat center top;
		height: 495px;
		position: relative;
		text-align: left;
		h1 {
			color: $brand-orange;
			text-shadow: 0px 0px 0px rgba(0, 0, 0, 0.2);
			margin: 0;
			padding: 0;
			sup {
				font-weight: normal;
				top: -1.2em; } }
		h3 {
			color: $brand-black-lighter;
			font-size: 18px;
			line-height: 22px;
			font-weight: 200;
			padding: 11px 0 22px; }
		.contents {
			@include make-xs-column(12); }
		.button-wrapper,
		.content-wrapper {
			@include make-sm-column(6); }
		.button-wrapper {
			@include make-sm-column(8);
			@include make-lg-column-offset(1);
			@include make-lg-column(7); }
		.content-wrapper {
			@include make-md-column(4); }

		.row.cta {
			.btn {
				&:hover {
					@include transition (all 1s); }
				&:focus,
				&:active,
				&:visited, {
					color: $brand-white !important;
					background-color: $brand-orange !important; } } }
		.row.play-btn {
			.contents {
				text-align: center; }
			.btn {
				margin: 88px auto;
				opacity: .9; } }


		@include phone {
			.contents {
				.content-wrapper {
					width: 320px;
					margin: 0 auto;
					text-align: left;
					.btn-primary-cta {
						height: 33px !important;
						padding: 2px 22px 3px 22px !important; } }
				.btn-play-blue {
					position: absolute;
					top: 88px;
					left: 0;
					right: 0;
					margin: 0 auto; } } }
		@include tablet {
			background: $brand-white url('../img/solutions/risk/hero-bg.jpg') no-repeat 64% top;
			background-size: 1600px;
			height: 559px;
			h1 {
				sup {
					font-size: 14px;
					top: -2em; } }
			h3 {
				font-size: 22px;
				line-height: 33px; }
			.contents {
				position: relative;
				.button-wrapper {
					.btn {
						height: 82px !important;
						margin-top: 165px;
						padding-left: 100%; } }
				.content-wrapper {
					// position: absolute
					// top: 44px
					// left: 440px
					padding: 5% 0 0 8%;
					text-align: left; } } }
		@include desktop {
			background: $brand-white url('../img/solutions/risk/hero-bg.jpg') no-repeat center top;
			background-size: 1920px;
			height: 671px;
			.contents {
				.button-wrapper {
					.btn {
						margin-top: 198px; } }
				.content-wrapper {
					padding: 44px 0 0 44px; } } }
		@include desktop-xl {
			.contents {
				.content-wrapper {
					padding: 55px 0 0 33px; } } } }

