			.container {
				.screenshot {
					margin-top: -33px; } }
			.content-wrapper {
				padding: 0;
				.steps {
					position: relative;
					img.step-screenshot {
						width: 100%;
						position: absolute;
						top: 66px;
						left: 0;
						right: 0;
						opacity: 0;
						width: 830px;
						margin: 0 auto;
						@include transition (all 200ms cubic-bezier(0.455, 0.030, 0.515, 0.955));
