					&.active {
						opacity: 1;
						@include transition (all 200ms cubic-bezier(0.455, 0.030, 0.515, 0.955)); }
					&.inactive {
						opacity: 0;
						@include transition (all 200ms cubic-bezier(0.455, 0.030, 0.515, 0.955)); }
					&.callout1 {
						top: 277px;
						right: 88px;
						z-index: 110; }
					&.callout2 {
