// BASE  | src: /less/_base.sass
// Base rules are applied to elements. | html, body, a, etc.; No class or ID selectors; Pseudo OK

html,
body {
	overflow-x: hidden; }
body {
	font-family: $font-family-sans-serif; }
*,
*::after,
*::before {
	box-sizing: border-box; }
a:hover,
a:focus {
	color: inherit;
	text-decoration: none; }



