// Icons
// --------------------------------------------------

.icons {
	width: 34px;
	height: 34px;
	border-radius: 17px;
	padding: 0;
	margin: 0;
	display: inline-block;
	text-align: center;
	padding-top: 7px; }

.icons-footer {
	background: $brand-accent-grayscale-5;
	color: $brand-white;
	.fa {
		font-size: 20px; }
	&:hover {
		color: inherit;
		text-decoration: none;
		@include transition(background 300ms ease-in);
		&.facebook {
			background: $facebook; }
		&.twitter {
			background: $twitter; }
		&.linkedin {
			background: $linkedin; }
		&.google-plus {
			background: $google-plus; } } }

.icons-colored {
	color: $brand-white;
	margin-right: 10px;
	.fa {
		font-size: 20px; }
	&:hover {
		text-decoration: none;
		color: $brand-lt-grey; } }

// Spinner

.spinner {
	margin: 11px 0 0;
	width: 80px;
	text-align: center;
	opacity: 0; }

.spinner > div {
	width: 11px;
	height: 11px;
	background-color: #fff;
	border-radius: 100%;
	display: inline-block;
	animation: sk-bouncedelay 1.7s infinite ease-in-out both; }

.spinner--go > div {
	background: $brand-p-go; }

.spinner .spinner__item1 {
	animation-delay: -0.60s; }

.spinner .spinner__item2 {
	animation-delay: -0.40s; }

.spinner .spinner__item3 {
	animation-delay: -0.20s; }

@keyframes sk-bouncedelay {
	0%, 80%, 100% {
		transform: scale(0);
		opacity: 0; }
	40% {
		transform: scale(1.0);
		opacity: 1; } }

// Z-Index Scale (private vars)
// --------------------------------------------------
$zIndex-1:   100;
$zIndex-2:   200;
$zIndex-3:   300;
$zIndex-4:   400;
$zIndex-5:   500;
$zIndex-6:   600;
$zIndex-7:   700;
$zIndex-8:   800;
$zIndex-9:   900;
$zIndex-10: 1000;

// Z-Index Applications
// --------------------------------------------------
$zIndex-1--screenForeground:        $zIndex-1;
$zIndex-2--imagePickerControl:		$zIndex-2;

// HR
// --------------------------------------------------

hr {
	@extend .container;
	padding-left: 15px;
	padding-right: 15px; }
hr.dash {
	background: url('../img/hr-dash.png') repeat-x top center;
	height: 22px;
	margin-top: 22px;
	margin-bottom: 0;
	border-top: none;
	width: 100%;
	padding: 0; }

hr.dotted {
	background: url('../img/hr-dotted.png') repeat-x top center;
	height: 44px;
	margin-top: 44px;
	margin-bottom: 0;
	border-top: none;
	width: 100%;
	padding: 0; }

hr.dots {
	height: 66px;
	padding: 0;
	margin-top: 0;
	margin-bottom: 0;
	border-bottom: 2px dotted #CCCCCC;
	border-top: none;
	width: 100%; }

hr.dashed {
	height: 2px;
	margin-top: 0;
	margin-bottom: 0;
	border-bottom: 1px dashed $brand-grey;
	border-top: none;
	width: 100%; }

// Gradients (Bless this dev: http://www.colorzilla.com/gradient-editor/)
// -----------------------------------------------------------------------

.gradient-white100-white55 {
	background: -moz-linear-gradient(top,  rgba(255,255,255,1) 0%, rgba(255,255,255,0.55) 100%) /* FF3.6+ */;
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(255,255,255,1)), color-stop(100%,rgba(255,255,255,0.55))) /* Chrome,Safari4+ */;
	background: -webkit-linear-gradient(top,  rgba(255,255,255,1) 0%,rgba(255,255,255,0.55) 100%) /* Chrome10+,Safari5.1+ */;
	background: -o-linear-gradient(top,  rgba(255,255,255,1) 0%,rgba(255,255,255,0.55) 100%) /* Opera 11.10+ */;
	background: -ms-linear-gradient(top,  rgba(255,255,255,1) 0%,rgba(255,255,255,0.55) 100%) /* IE10+ */;
	background: linear-gradient(to bottom,  rgba(255,255,255,1) 0%,rgba(255,255,255,0.55) 100%) /* W3C */;
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#8cffffff',GradientType=0 ) /* IE6-9 */; }

.gradient-black70-white10 {
	background: -moz-linear-gradient(-45deg,  rgba(0,0,0,0.7) 0%, rgba(229,229,229,0.1) 100%) /* FF3.6+ */;
	background: -webkit-gradient(linear, left top, right bottom, color-stop(0%,rgba(0,0,0,0.7)), color-stop(100%,rgba(229,229,229,0.1))) /* Chrome,Safari4+ */;
	background: -webkit-linear-gradient(-45deg,  rgba(0,0,0,0.7) 0%,rgba(229,229,229,0.1) 100%) /* Chrome10+,Safari5.1+ */;
	background: -o-linear-gradient(-45deg,  rgba(0,0,0,0.7) 0%,rgba(229,229,229,0.1) 100%) /* Opera 11.10+ */;
	background: -ms-linear-gradient(-45deg,  rgba(0,0,0,0.7) 0%,rgba(229,229,229,0.1) 100%) /* IE10+ */;
	background: linear-gradient(135deg,  rgba(0,0,0,0.7) 0%,rgba(229,229,229,0.1) 100%) /* W3C */;
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#80000000', endColorstr='#1ae5e5e5',GradientType=1 ) /* IE6-9 fallback on horizontal gradient */; }

.gradient-customer-logos {
	background: -moz-linear-gradient(top,  rgba(255,255,255,0) 0%, rgba(0,0,0,0.5) 100%) /* FF3.6+ */;
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(255,255,255,0)), color-stop(100%,rgba(0,0,0,0.5))) /* Chrome,Safari4+ */;
	background: -webkit-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(0,0,0,0.5) 100%) /* Chrome10+,Safari5.1+ */;
	background: -o-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(0,0,0,0.5) 100%) /* Opera 11.10+ */;
	background: -ms-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(0,0,0,0.5) 100%) /* IE10+ */;
	background: linear-gradient(to bottom,  rgba(255,255,255,0) 0%,rgba(0,0,0,0.5) 100%) /* W3C */;
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1affffff', endColorstr='#80000000',GradientType=0 ) /* IE6-9 */; }

.gradient-black-mandarin {
	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#000000+0,000000+48,f6ae00+48,f6ae00+48,f6ae00+100 */
	background: -moz-linear-gradient(left,  #000000 0%, #000000 48%, #f6ae00 48%, #f6ae00 48%, #f6ae00 100%) /* FF3.6-15 */;
	background: -webkit-linear-gradient(left,  #000000 0%,#000000 48%,#f6ae00 48%,#f6ae00 48%,#f6ae00 100%) /* Chrome10-25,Safari5.1-6 */;
	background: linear-gradient(to right,  #000000 0%,#000000 48%,#f6ae00 48%,#f6ae00 48%,#f6ae00 100%) /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */;
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#f6ae00',GradientType=1 ) /* IE6-9 */; }

