.img-partner {
  background: $brand-accent-p url('../img/partner/hero-img.svg') no-repeat center bottom / contain;
  margin-top: 60px;
  margin-bottom: 20px;
  height: 600px;

  @include phone {
    padding: 22px 0 0 0;
    background: $brand-accent-p url('../img/partner/hero-img.svg') no-repeat center top / cover; }

  @include tablet {
    padding: 44px 0 0 0;

    p {
      font-size: 18px;
      line-height: 26px;
      font-weight: 200; }
    .container {
      padding-top: 55px; } }

  @include desktop {
    padding: 44px 0 20px 0;
    height: 721px; }

  .panel {
    padding: 30px 20px;
    margin: auto;

    @include tablet {
      width: 480px; }

    @include desktop {
      max-width: unset; }

    @include phone-to-desktop {
      max-width: 294px;
      margin: auto; } }

  h2 {
    font-size: 34px;
    font-weight: 700;
    color: black;

    @include phone-to-desktop {
      font-size: 24px;
      line-height: 28px; } } }

hr.brand-p-go {
  height: 4px;
  max-width: 500px;
  width: calc(100% - 30px); // 30px to match the negative margin sizes on .container
  background-image: linear-gradient( 90deg, rgb(166,2,97) 0%, rgb(98,2,166) 100%); }

.bg-brand-accent-p {
  background-color: $brand-accent-p; }

.bg-white {
  background-color: white; }

.partner-logo-block {
  width: 100%;
  margin-bottom: 98px; }

.panel-fixed-width-sm {

  @include phone-to-desktop {
    max-width: 294px;
    margin: auto;
    p {
      font-size: 14px;
      line-height: 20px; } }

  @include tablet {
    padding-bottom: 20px; }

  @include desktop {
    max-width: unset;
    margin-bottom: 50px; } }

.partner-panel {
  max-width: 270px;
  height: 100%;
  margin: auto;

  ~ .partner-logo-block {
    width: calc(100% - 20px); // 20px from the .partner-panel margins
    margin: 0 auto 25px auto;
    max-width: 270px; } // Matches .partner-panel

  .partner-panel-body {
    padding: 22px 20px;
    text-align: center;

    h3 {
      font-size: 20px;
      line-height: 24px;
      font-weight: 200;
      margin-top: 20px; }

    p {
      font-size: 14px;
      line-height: 20px;
      font-weight: 400; }

    .icon {
      height: 50px; }

    @include desktop {
      text-align: left;


      .icon {
        margin-bottom: 10px; } } } }

.equal-height-row-md {
  @include desktop {
    align-items: stretch;
    display: flex; } }

.gradient-orange {
  background-image: linear-gradient( 300deg, rgb(255,138,0) 0%, rgb(255,193,7) 100%); }

.prefooter {
  padding: 44px 0 110px;
  position: relative;
  height: 520px;

  @include desktop {
    padding: 88px 0 110px;
    height: 400px; }

  h3 {
    font-size: 26px;
    font-weight: 200;
    line-height: 36px;
    margin-bottom: 18px;
    max-width: 200px;
    margin-left: auto;
    margin-right: auto;

    @include desktop {
      font-size: 44px;
      line-height: 50px;
      text-align: center; } }

  .partner-badge {
    height: 400px;
    position: absolute;
    top: 0;

    @include tablet {
      right: 0; }
    @include desktop {
      right: calc(50% - 450px); } } }

.text-bold {
  font-weight: 700; }

.partner-title {
  margin-top: 20px;

  @include phone {
    font-size: 26px;
    line-height: 33px;
    font-weight: 200; }
  @include tablet {
    font-size: 26px;
    line-height: 33px;
    font-weight: 200; }
  @include desktop {
    margin-top: 40px;
    font-size: 44px;
    line-height: 55px;
    font-weight: 200; } }

.badge-container {
  position: absolute;
  justify-content: center;
  align-items: center;

  @include phone {
    display: flex !important; }

  img {
    margin-top: 15px; } }

.extra-wide-btn {
  text-align: center !important;
  width: 45%;
  max-width: 150px;
  margin-right: auto;
  margin-left: auto;
  display: block; }



