// Custom Styling for Roles > Overview Page
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.roles-overview {
	.hero.we-understand {
		background: $brand-blue;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-12; }
		h1,
		h3,
		p,
		.title-role {
			color: $brand-white; }
		h1 {
			margin: 50px auto 0;
			text-align: center; }
		h3 {
			padding: 20px 20px 50px;
			text-align: center; }
		@include tablet {
			margin-bottom: 30px;
			.contents {
				text-align: center; }
			h1 {
				margin: 40px auto;
				width: 645px; }
			h3 {
				margin: 0 auto;
				padding: 0 20px 50px 0;
				width: 740px; } }
		@include desktop {
			margin-bottom: 30px;
			.contents {
				text-align: center; } }
		@include desktop-xl {
			background-size: cover; } }
	.role-list {
		.container {
			padding-left: 0;
			padding-right: 0; }
		.executives,
		.procure-to-pay,
		.finance,
		.accounts-payable,
		.information-technology, {
			@extend .col-sm-6;
			padding-left: 0;
			padding-right: 0;
			.title-role {
				font-size: 34px;
				font-weight: 300;
				color: $brand-white;
				text-align: center;
				position: relative;
				height: 176px;
				margin: 0 auto;
				padding-top: 60px; }
			h2 {
				margin-top: 40px;
				margin-bottom: 0;
				a {
					margin: 0; } }
			p {
				margin-top: 5px;
				color: $brand-black-lighter; }
			a {
				color: $brand-blue;
				margin-top: 8px; }
			.intro-text {
				padding: 20px;
				text-align: center;
				h2,
				p,
				a {
					margin: 0; }
				h2 {
					font-size: 26px;
					display: none; }
				p {
					font-size: 16px;
					line-height: 24px;
					margin-bottom: 10px;
					text-align: center; } } }
		.executives {
			.title-role {
				background: url('../img/roles/overview/exec-bg-800.jpg') no-repeat center top;
				background-size: cover; } }
		.procure-to-pay {
			.title-role {
				background: url('../img/roles/overview/p2p-bg-800.jpg') no-repeat 70% top;
				background-size: cover; } }
		.finance {
			.title-role {
				background: url('../img/roles/overview/finance-bg-desktop.jpg') no-repeat center top;
				background-size: cover; } }
		.accounts-payable {
			.title-role {
				background: url('../img/roles/overview/ap-bg-desktop.jpg') no-repeat right top;
				background-size: cover; } }
		.information-technology {
			.title-role {
				background: url('../img/roles/overview/it-bg-desktop.jpg') no-repeat center top;
				background-size: cover; } }
		.knowledge-is-power {
			@extend .col-sm-6;
			padding: 30px 60px;
			text-align: center; }

		@include tablet {
			.executives,
			.procure-to-pay,
			.finance,
			.accounts-payable,
			.information-technology,
			.knowledge-is-power {
				padding-left: 15px;
				padding-right: 15px;
				.intro-text {
					padding: 20px 30px; }
				.title-role {
					height: 176px;
					padding-top: 60px; } }
			.finance {
				.title-role {
					background: url('../img/roles/overview/finance-bg-tablet.jpg') no-repeat center top;
					background-size: cover; }
				.intro-text {
					padding: 20px 40px; } }
			.knowledge-is-power {
				padding-top: 0; } }

		@include desktop {
			.executives {
				.title-role {
					background: none; }
				.imagery {
					background: url('../img/roles/overview/exec-bg-desktop.jpg') no-repeat right top; } }
			.procure-to-pay {
				.title-role {
					background: none; }
				.imagery {
					background: url('../img/roles/overview/p2p-bg-desktop.jpg') no-repeat right top; } }
			.finance {
				.title-role {
					background: none; }
				.imagery {
					background: url('../img/roles/overview/finance-bg-desktop.jpg') no-repeat right top; } }
			.accounts-payable {
				.title-role {
					background: none; }
				.imagery {
					background: url('../img/roles/overview/ap-bg-desktop.jpg') no-repeat right top; } }
			.information-technology {
				.title-role {
					background: none; }
				.imagery {
					background: url('../img/roles/overview/it-bg-desktop.jpg') no-repeat 50% top; } }
			.executives,
			.procure-to-pay,
			.finance,
			.accounts-payable,
			.information-technology {
				margin-bottom: 30px;
				&:hover {
					cursor: pointer;
					.intro-text {
						opacity: 1;
						@include transition(opacity .5s ease-in-out); } }
				.imagery {
					height: 280px; }
				.title-role {
					padding-top: 95px; }
				.intro-text {
					padding: 20px 30px;
					position: absolute;
					width: 80%;
					top: 30px;
					left: 10%;
					background: $brand-white;
					opacity: 0;
					-webkit-user-select: none;
					-webkit-touch-callout: none;
					h2 {
						display: inline-block; } } }
			.procure-to-pay {
				.intro-text {
					padding-top: 15px; } }
			.information-technology {
				.intro-text {
					padding: 20px 40px; } } }

		@include desktop-xl {
			.executives,
			.procure-to-pay,
			.finance,
			.accounts-payable,
			.information-technology {
				.imagery {
					background-size: cover; }
				.intro-text {
					height: 220px;
					padding: 20px 40px; } }

			.information-technology {
				.intro-text {
					padding: 20px 60px; } } } }


	/* Looking for the Marketo Form: go to _layout.sass */ }





