// Custom Styling for Compliance Page
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.compliance-page {
	.hero {
		padding: 66px 0 88px;
		background: $brand-white url('../img/compliance/hero-bg-xs.jpg') no-repeat center top;
		text-align: center;
		position: relative;

		h1 {
			color: $brand-blue; }
		h3 {
			margin-top: 22px;
			font-weight: 200;
			color: $brand-black-lighter; }
		.contents {
			@extend .col-xs-12; }

		@include phone {
			.contents {
				width: 300px;
				margin: 0 auto;
				float: none; } }

		@include tablet {
			padding: 66px 0 88px;
			background: $brand-white url('../img/compliance/hero-bg.jpg') no-repeat center top;
			text-align: left;
			h1 {
				width: 660px; }
			h3 {
				width: 627px; } }
		@include desktop {
			padding: 99px 0 154px;
			background: $brand-white url('../img/compliance/hero-bg.jpg') no-repeat left top;
			h3 {
				width: 814px; } }
		@include desktop-xl {
			padding: 134px 0 198px;
			h1 {
				width: 100%; }
			h3 {
				margin-top: 11px; } }
		@include desktop-1920 {
			background-size: cover; } }

	.standard {
		padding: 22px 0 44px;
		background: $brand-white url('../img/compliance/desk-bg-xs.jpg') no-repeat center bottom;
		text-align: center;
		position: relative;
		height: 605px;
		margin-top: -17px;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-7;
			@extend .col-md-6; }
		.arrow {
			position: absolute;
			top: 0;
			width: 100%;
			height: 0;
			background-color: $brand-white; }

		.arrow:before,
		.arrow:after {
			content: '';
			position: absolute;
			bottom: 100%;
			width: 50%;
			box-sizing: border-box; }

		.arrow:before {
			right: 50%;
			border-bottom: 16px solid $brand-white;
			border-right: 16px solid transparent; }

		.arrow:after {
			left: 50%;
			border-bottom: 16px solid $brand-white;
			border-left: 16px solid transparent; }
		@include phone {
			.contents {
				width: 320px;
				margin: 0 auto;
				float: none; } }

		@include tablet {
			padding: 44px 0;
			background: $brand-white url('../img/compliance/desk-bg.jpg') no-repeat 45% top;
			height: 506px;
			text-align: left;
			margin-top: -22px;
			.arrow:before {
				right: 50%;
				border-bottom: 22px solid $brand-white;
				border-right: 20px solid transparent; }
			.arrow:after {
				left: 50%;
				border-bottom: 22px solid $brand-white;
				border-left: 20px solid transparent; } }
		@include desktop {
			padding: 44px 0;
			background: $brand-white url('../img/compliance/desk-bg.jpg') no-repeat 53% top;
			height: 506px;
			text-align: left;
			.arrow:before {
				content: '';
				position: absolute;
				bottom: 100%;
				width: 75%;
				box-sizing: border-box; }
			.arrow:after {
				content: '';
				position: absolute;
				bottom: 100%;
				width: 25%;
				box-sizing: border-box; }
			.arrow:before {
				right: 25%;
				border-bottom: 22px solid $brand-white;
				border-right: 22px solid transparent; }
			.arrow:after {
				left: 75%;
				border-bottom: 22px solid $brand-white;
				border-left: 22px solid transparent; } }
		@include desktop-xl {
			padding: 44px 0;
			background: $brand-white url('../img/compliance/desk-bg.jpg') no-repeat 53% top;
			.contents {
				padding-right: 77px; }
			.arrow:before {
				content: '';
				position: absolute;
				bottom: 100%;
				width: 72%;
				box-sizing: border-box; }
			.arrow:after {
				content: '';
				position: absolute;
				bottom: 100%;
				width: 28%;
				box-sizing: border-box; }
			.arrow:before {
				right: 28%;
				border-bottom: 22px solid $brand-white;
				border-right: 22px solid transparent; }
			.arrow:after {
				left: 72%;
				border-bottom: 22px solid $brand-white;
				border-left: 22px solid transparent; } }
		@include desktop-1500 {
			.arrow:before {
				width: 66%; }
			.arrow:after {
				width: 34%; }
			.arrow:before {
				right: 34%; }
			.arrow:after {
				left: 66%; } } }
	.assurance-intro {
		padding: 11px 0;
		background: $brand-blue;
		text-align: center;
		color: $brand-white;
		p {
			font-size: 18px;
			font-weight: 200; }
		.contents {
			@extend .col-xs-12; }
		@include phone {
			.contents {
				width: 300px;
				margin: 0 auto;
				float: none; } }
		@include tablet {
			p {
				font-size: 26px;
				line-height: 33px; } }
		@include desktop {
			padding: 22px 0; } }

	.assurance-list.soc1 {
		position: relative;
		&::after {
			content: ' ';
			text-align: center;
			margin: 0 auto;
			width: 22px;
			height: 22px;
			border-style: solid;
			border-width: 22px 20px 0 20px;
			border-color: $brand-blue transparent transparent transparent;
			position: absolute;
			top: 0;
			left: 0;
			right: 0; } }
	.assurance-list {
		padding: 44px 0;
		h2 {
			font-size: 26px;
			margin-bottom: 22px;
			font-weight: 200; }
		&.soc1,
		&.iso {
			background: $brand-baby-blue; }
		.imagery {
			@extend .col-xs-12;
			@extend .col-sm-5; }
		.copy {
			@extend .col-xs-12;
			@extend .col-sm-7; }
		@include phone {
			text-align: center;
			.imagery,
			.copy {
				width: 300px;
				margin: 0 auto;
				float: none; }
			p:first-of-type {
				margin-top: 22px; }
			img {
				width: 138px; } }
		@include tablet {
			h2 {
				font-size: 34px; }
			.imagery {
				padding-top: 77px;
				text-align: center; } }
		@include desktop {
			padding: 66px 0; }
		@include desktop-xl {
			&.soc1 {
				.imagery {
					padding-top: 176px; }
				.copy {
					padding-right: 44px; } }
			&.soc2 {
				.imagery {
					padding-top: 154px; }
				.copy {
					padding-right: 44px; } }
			&.iso {
				.imagery {
					padding-top: 77px; }
				.copy {
					padding-right: 55px; } } } }


	/* Looking for the Marketo Form: go to _layout.sass */ }





