// Custom Styling for Partners pages (C2FO, Quyntess, etc.)
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css
// Each partner page has custom styling.  We use the name of the partner as parent selector *-page
// .partners at the BOD is the parent selector targeted for default, common styling across partner pages

.partners-overview {
	.hero {
		padding: 99px 0 0;
		color: $brand-white;
		text-align: left;
		height: 330px;
		background: $brand-blue url('../img/partnersv2/hero-bg-xs.jpg') no-repeat center top;
		background-size: cover;

		h1 {
			color: $brand-white;
			margin-bottom: 11px; }
		h3 {
			color: $brand-white;
			margin-bottom: 22px; }
		.contents {
			@include make-xs-column(12);
			@include make-sm-column-offset(1);
			@include make-sm-column(6);
			@include make-md-column-offset(1);
			@include make-md-column(5);
			@include make-lg-column(4); }
		.btn-primary-cta {
			padding: 8px 33px 10px !important; }

		@include phone {
			.contents {
				width: 290px;
				float: none;
				margin-left: 15px; }
			.btn-primary-cta {
				padding: 3px 33px 4px !important;
				height: 33px !important; } }
		@include tablet {
			padding: 88px 0;
			height: 484px;
			background: $brand-blue url('../img/partnersv2/hero-bg.jpg') no-repeat 42% top;
			background-size: 1600px; }
		@include desktop {
			padding: 165px 0 0;
			height: 649px;
			background: $brand-blue url('../img/partnersv2/hero-bg.jpg') no-repeat center top;
			background-size: 1920px; }
		@include desktop-1920 {
			padding: 132px 0;
			height: 682px;
			background-size: cover; } }
	.connected {
		padding: 44px 0;
		background: $brand-baby-blue;
		text-align: center;
		h2 {
			text-align: center; }
		.contents {
			@include make-sm-column(12); }
		.wrapper-left {
			@include make-md-column(4); }
		.wrapper-right {
			@include make-md-column-offset(4);
			@include make-md-column(4); }
		@include phone {
			.contents {
				width: 290px;
				float: none;
				margin: 0 auto;
				padding-right: 0;
				padding-left: 0; }
			img {
				width: 100%;
				margin: 22px auto; } }
		@include tablet;

		@include desktop {
			padding: 44px 0 66px;
			text-align: left;
			background: $brand-baby-blue url('../img/partnersv2/connected-min.png') no-repeat center 99px;
			background-size: 308px;

			.wrapper-left,
			.wrapper-right {
				margin-top: 22px; } }

		@include desktop-xl {
			padding: 44px 0 165px;
			background-size: 433px;
			.wrapper-left,
			.wrapper-right {
				margin-top: 110px; }

			.wrapper-left {
				padding-right: 22px; }
			.wrapper-right {
				padding-right: 0; } } }
	.partner-program {
		padding: 44px 0;
		h2,
		h3 {
			text-align: center; }
		h3 {
			font-size: 26px;
			font-weight: 200;
			margin-top: 11px;
			margin-bottom: 22px; }
		h4 {
			margin-top: 22px;
			font-weight: 400; }
		img {
			margin: 0 auto 11px; }
		.contents {
			@include make-sm-column(12);
			@include make-md-column-offset(1);
			@include make-md-column(10);
			@include make-lg-column-offset(2);
			@include make-lg-column(8); }
		.distribution {
			margin: 22px 0;
			text-align: center;
			h3 {
				color: $brand-mandarin; } }
		.app {
			margin: 44px 0 22px;
			text-align: center;
			h3 {
				color: $brand-orange; } }
		.testimonial-partners {
			blockquote {
				@include make-xs-column(12);
				@include make-md-column-offset(1);
				@include make-md-column(10); } }


		@include phone {
			h4,
			p {
				text-align: left; }
			.contents {
				width: 290px;
				float: none;
				margin: 0 auto; }
			.overview {
				img {
					width: 100%;
					margin: 22px auto;
					text-align: center; } }
			.distribution {
				p {
					letter-spacing: -0.02em; }
				blockquote {
					margin-left: -15px;
					footer {
						padding: 0 15px; } } }
			.testimonial-partners {
				text-align: center; }

			blockquote {
				width: 290px;
				float: none;
				margin: 0 auto 22px;
				text-align: center;
				border: none;
				padding: 0;
				img.partner-xerox {
					width: 148px; }
				img.partner-quyntess {
					width: 144px; }
				p {
					color: $brand-blue;
					font-size: 18px;
					font-weight: 200;
					text-align: center; }
				footer {
					color: $brand-navy;
					font-size: 12px;
					text-align: center; } } }

		@include tablet {
			text-align: center;
			h4 {
				margin-top: 0;
				font-size: 22px;
				font-weight: 200; }
			.overview {
				img {
					margin: 11px 0; } }
			.distribution {
				margin: 22px 0 0;
				a:hover {
					cursor: pointer; }
				blockquote {
					margin: 0 auto 22px;
					text-align: center;
					border: none;
					padding: 0;
					img.partner-xerox {
						width: 160px;
						margin-top: 22px; }
					p {
						color: $brand-blue;
						font-size: 26px;
						font-weight: 200;
						text-align: center;
						margin: 0 0 11px; }
					footer {
						color: $brand-navy;
						font-size: 12px;
						text-align: center; } } }
			.testimonial-partners {
				text-align: center;
				margin: 0 auto;
				blockquote {
					float: none;
					margin: 22px auto;
					text-align: left;
					border: none;
					padding: 0 15px;
					img.partner-xerox {
						width: 148px;
						float: left;
						margin-top: 33px; }
					img.partner-quyntess {
						width: 144px;
						float: left;
						margin-top: 11px; }
					p {
						color: $brand-blue;
						font-size: 18px;
						font-weight: 200;
						text-align: left;
						padding: 0 0 0 180px;
						line-height: 26px;
						margin-bottom: 11px; }
					footer {
						color: $brand-navy;
						background: $brand-white;
						font-size: 14px;
						text-align: left;
						padding: 0 0 0 180px; } } } }

		@include desktop {
			h4 {
				margin-top: 0; }
			.distribution {
				margin: 44px 0 0; }
			.app {
				margin: 44px 0 11px;
				a:hover {
					cursor: pointer; } }
			.testimonial-partners {
				blockquote {
					padding: 0;
					img.partner-quyntess {
						width: 132px; }
					p {
						font-size: 26px;
						line-height: 33px;
						padding: 0 0 0 154px; }
					footer {
						padding: 0 0 0 154px; } } } }

		@include desktop-xl; }

	.partner-tiers {
		padding: 44px 0;
		text-align: center;
		background: $brand-lt-gray;
		img {
			text-align: center; }
		.overview {
			.contents {
				@include make-sm-column-offset(1);
				@include make-sm-column(10);
				@include make-md-column-offset(1);
				@include make-md-column(10); } }
		.tier-matrix {
			.contents {
				@include make-sm-column(12);
				@include make-lg-column(11); } }
		@include phone {
			p {
				padding: 0 15px; }
			.contents {
				width: 320px;
				margin: 0 auto;
				float: none; }
			.seal {
				margin-top: 33px;
				margin-bottom: 22px; }
			.tier-value {
				width: 320px; } }
		@include tablet {
			.tier-matrix {
				table {
					border: none;
					margin-top: 22px;
					th {
						text-align: center;
						font-size: 22px;
						text-transform: uppercase;
						padding-bottom: 11px;
						img {
							margin-bottom: 22px; } }
					tr {
						text-align: left;
						border-top: 1px solid $brand-white; }
					tr:first-of-type {
						border-top: none; }

					.row-title {
						vertical-align: top;
						padding: 11px;
						text-align: left;
						font-weight: 700;
						font-size: 18px;
						color: $brand-grey; }
					td.gold-tier,
					td.silver-tier,
					td.bronze-tier {
						vertical-align: top;
						padding: 11px;
						text-align: left; }
					.gold-tier {
						color: $brand-mandarin; }
					.silver-tier {
						color: $brand-grey; }
					.bronze-tier {
						color: $brand-brown; }
					ul {
						padding-left: 11px;
						li {
							list-style-type: none;
							position: relative;
							padding-left: 11px;
							&::before {
								content: '-';
								position: absolute;
								left: 0; } } } } } }


		@include desktop-xl {
			.overview {
				.contents {
					p {
						padding: 0 55px; } } }
			.tier-matrix {
				.contents {
					width: 87%; }
				table {
					th:first-of-type {
						width: 202px; }
					td.gold-tier,
					td.silver-tier,
					td.bronze-tier {
						padding: 11px 0; } } } } }

	.partner-logos {
		padding: 44px 0;
		text-align: center;
		h2 {
			margin-bottom: 22px; }
		.case-studies {
			.contents {
				border-top: 1px solid $brand-sky-blue;
				border-bottom: 1px solid $brand-sky-blue;
				@include make-xs-column(12);

				&::before {
					content: 'Click on logo';
					color: $brand-blue;
					font-size: 10px;
					position: absolute;
					top: 2px;
					left: 5px; } }
			.logo-wrapper {
				@include make-sm-column(12); }
			.logo-ecovadis,
			.logo-wipro,
			.logo-c2fo,
			.logo-quyntess,
			.logo-determine {
				width: 20%;
				float: left; } }
		@include phone {
			.case-studies {
				.contents {
					width: 320px;
					float: none;
					margin: 0 auto 22px;
					display: inline-block;
					padding-top: 33px;
					padding-bottom: 0;
					&::before {
						content: 'Tap on logo'; } }
				.logo-ecovadis,
				.logo-wipro,
				.logo-c2fo {
					@include make-xs-column(4);
					padding: 0; }
				.logo-quyntess,
				.logo-determine {
					height: 55px;
					@include make-xs-column(6); }
				.logo-ecovadis {
					img {
						width: 99px;
						margin-left: -9px; }
					padding-top: 5px; }
				.logo-wipro {
					img {
						width: 48px;
						margin-left: 8px; } }
				.logo-c2fo {
					img {
						width: 68px; }
					padding-top: 5px; }
				.logo-quyntess {
					img {
						width: 77px; }
					margin-top: 11px; }
				.logo-determine {
					img {
						width: 80px; }
					margin-top: 11px; } }
			.contents {
				width: 290px;
				float: none;
				margin: 0 auto; }
			.show-initial,
			.show-more {
				img {
					width: 290px; } }
			.show-more-trigger {
				text-align: center;
				color: $brand-blue;
				text-transform: uppercase;
				height: 66px;
				position: relative;
				margin: 11px 0 22px;
				border: none;
				background: none;
				&:focus {
					outline: none; }
				&::after {
					content: '\f107';
					font-family: FontAwesome;
					position: absolute;
					color: $brand-blue;
					bottom: 0;
					left: 0;
					right: 0;
					margin: 0 auto;
					text-align: center;
					display: inline-block;
					font-size: 22px; } } }

		@include tablet {
			.case-studies {
				.contents {
					padding-top: 22px;
					padding-bottom: 11px;
					&::before {
						font-size: 12px; } }
				.logo-ecovadis {
					img {
						width: 120px; }
					padding-top: 5px; }
				.logo-wipro {
					img {
						width: 60px; } }
				.logo-c2fo {
					img {
						width: 88px; }
					padding-top: 7px; }
				.logo-quyntess {
					img {
						width: 99px; }
					padding-top: 5px; }
				.logo-determine {
					img {
						width: 99px; }
					padding-top: 5px; } }
			.logos-sm {
				width: 100%;
				margin: 33px -15px 33px 0; } }

		@include desktop {
			.case-studies {
				.logo-ecovadis {
					img {
						width: 188px; } }
				.logo-c2fo {
					img {
						width: 136px;
						margin-left: 33px; } }
				.logo-wipro {
					img {
						width: 97px; } }
				.logo-quyntess {
					img {
						width: 154px; } }
				.logo-determine {
					img {
						width: 160px; } } }
			.logos-md {
				width: 100%;
				margin: 33px 0; }
			.logos-rest {
				max-width: 1173px; } }

		@include desktop-xl {
			.logos-md {
				width: 1189px; } } }

	.partners-form {
		text-align: center;
		padding: 33px 0;
		background: $brand-sky-blue;
		h2 {
			margin: 0 auto;
			color: $brand-blue;
			margin-bottom: 22px;
			text-align: center; }
		.contents {
			@include make-sm-column(12);
			@include make-md-column-offset(1);
			@include make-md-column(10);
			@include make-lg-column-offset(2);
			@include make-lg-column(8); }
		#mktoForm_302.mktoForm {
			padding: 11px !important;
			label {
				@include label-default; }
			input {
				@include input-fields-default;
				background-color: $brand-white !important;
				width: 100% !important;
				margin-left: 0 !important;
				margin-right: 0 !important;
				font-size: 14px !important; }
			.mktoFormCol {
				margin-bottom: 0 !important; }
			.mktoFormRow {
				margin-left: 33px !important; }
			.privacy {
				color: $brand-black-lighter; }
			.mktoButtonRow {
				text-align: center !important;
				width: 100% !important;
				margin-top: 22px !important;
				.mktoButtonWrap.mktoSimple {
					margin: 0 auto !important;
					text-align: center !important;
					.mktoButton {
						@extend .btn-primary-cta;
						padding: 0;
						font-family: $font-family-heading !important;
						background-image: none !important;
						background-color: $brand-orange !important;
						width: 100% !important;
						text-align: center !important;
						float: none !important; } } }
			.mktoError {
				right: initial !important;
				bottom: -11px !important;
				left: 0 !important; } }

		@include phone {
			.contents {
				width: 290px;
				float: none;
				margin: 0 auto; }
			#mktoForm_302.mktoForm {
				.mktoFormRow {
					margin-left: 0 !important; } } }
		@include tablet {
			#mktoForm_302.mktoForm {
				.privacy {
					text-align: center !important;
					width: 80% !important;
					margin: 0 auto !important; }
				.mktoHtmlText.mktoHasWidth {
					width: 100% !important; }
				.mktoButtonRow {
					.mktoButtonWrap.mktoSimple {
						.mktoButton {
							width: 198px !important;
							font-size: 18px !important;
							margin: 0 !important; } } } } }

		@include desktop {
			#mktoForm_302.mktoForm {
				padding: 11px 33px !important; } }

		@include desktop-xl; } }

.partners {
	.modal {
		.mktoForm {
			.mktoFieldWrap {
				margin-bottom: 0px !important; }
			.mktoFormRow {
				float: none !important; }
			.mktoRequiredField {
				label.mktoLabel {
					width: 200px !important; } } } } }
