// Custom Styling for Solutions > Overview Page
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css


.solutions-overview {
	// Global components on the overview page
	h1,h2,h3,h4,p {}
	color: #282828;
	sup {
		top: -0.99em !important;
		font-size: 40% !important; }
	@include phone {
		margin-top: 55px;
		.contents {
			width: 320px;
			margin: auto; }
		.topBar, .topBarBlue {
			width: 270px !important;
			display: block !important;
			margin: auto !important; }
		p {
			font-size: 14px;
			line-height: 20px; } }
	.solution {
		&-box {
			background: $brand-white;
			box-shadow: 1px 2px 6px 1px rgba(0, 0, 0, 0.25);
			float: left;
			margin-right: 30px;
			padding: 22px;
			width: 283px;
			height: 274px;
			h3 {
				font-size: 20px !important;
				line-height: 20px !important;
				font-weight: 700 !important;
				text-align: left !important;
				margin-bottom: 11px !important; }
			p {
				font-size: 14px !important;
				line-height: 20px !important;
				text-align: left !important;
				margin-bottom: 11px !important; }
			a {
				font-size: 14px !important;
				line-height: 20px !important;
				color: #00b0ff !important;
				text-align: left !important;
				display: table !important;
				font-weight: 400 !important; }
			&:last-of-type {
				margin-right: 0px !important; } }
		&-buy {
			h3 {
				color: #00b0ff !important; } }
		&-pay {
			h3 {
				color: #4fc610 !important; } }
		&-go {
			h3 {
				color: #a60261 !important; } }
		&-supply-chain {
			h3 {
				color: #ff9000 !important; } }

		@include tablet-only {
			&-box {
				width: 300px;
				display: block;
				float: none;
				margin: auto !important;
				margin-bottom: 22px !important;
				&:last-of-type {
					margin-right: auto !important; }
				h3,p {
					text-align: center !important;
					width: auto !important; }
				a {
					display: table !important;
					margin: auto; } } }
		@include phone {
			&-box {
				width: 300px;
				height: auto;
				padding: 22px 50px 22px 50px;
				display: block;
				float: none;
				margin: auto !important;
				margin-bottom: 22px !important;
				&:last-of-type {
					margin-right: auto !important; }
				h3,p {
					text-align: center !important;
					width: auto !important; }
				h3 {
					font-size: 18px !important;
					line-height: 26px !important;
					font-weight: 700 !important; }
				a {
					display: table !important;
					margin: auto; } } } }

	.hero.rethink {
		height: 640px;
		padding: 44px 0;
		background: #00173a;
		background-size: contain;
		color: $brand-white;
		overflow: hidden;
		position: relative;
		z-index: 9;

		h1,
		h3 {
			color: $brand-white;
			position: relative;
			z-index: 11; }
		h3 {
			margin-top: 22px;
			font-weight: 400;
			margin-bottom: 33px; }
		.contents {
			position: relative; }
		.globe {
			position: absolute;
			z-index: 10; }
		.btn {
			background: #00b0ff !important;
			color: #ffffff !important;
			padding: 8px 44px;
			margin-top: 0px;
			text-transform: inherit !important;
			font-weight: 600 !important;
			font-size: 18px !important;
			z-index: 11; }
		@include tablet-only {
			background: #000000 url(../img/solutions/solutions-globe-xs.png) no-repeat center bottom;
			background-size: 992px;
			height: 610px;
			padding-top: 44px;

			h1 {
				margin: auto;
				text-align: center;
				margin-bottom: 22px; }

			h3 {
				margin: auto;
				text-align: center;
				margin-bottom: 33px; }
			.contents {
				text-align: center; }
			.btn {
				font-size: 16px !important; } }
		@include phone {
			background: #000000 url(../img/solutions/solutions-globe-xs.png) no-repeat center bottom;
			height: 500px;
			h1 {
				text-align: center;
				font-size: 26px;
				line-height: 33px; }
			h3 {
				line-height: 24px;
				text-align: center;
				margin-bottom: 15px; }
			.contents {
				text-align: center; }
			.btn {
				font-size: 16px !important; } }

		@include tablet {
			padding: 66px 0;
			h1 {
				width: 600px;
				font-size: 36px;
				line-height: 47px; }
			h3 {
				width: 480px;
				font-size: 24px;
				line-height: 32px; } }
		@include desktop {
			padding: 0px 0;
			h1 {
				width: 600px;
				font-size: 36px;
				line-height: 47px; }
			h3 {
				width: 480px;
				font-size: 24px;
				line-height: 32px; }
			.container {
				padding-left: 0px; }
			.content {
				display: flex;
				flex-direction: column;
				justify-content: center;
				position: absolute;
				height: 632px; }
			.btn {
				width: 320px; }
			.globe {
				top: 5px;
				right: -389px; } }
		@include desktop-xl {
			.globe {
				top: 5px;
				right: -190px; } } }

	.solutions-list {
		padding: 44px 0 0;
		background: #f2f2f2;
		h3 {
			font-size: 22px;
			margin-bottom: 11px;
			font-weight: 200; }
		p {
			width: 280px;
			margin: 0 auto 22px; }
		img {}

		.lamp {
			position: absolute; }

		.row {
			margin-bottom: 44px; }

		.container {
			position: relative; }

		.contents {
			@include make-xs-column(12);
			text-align: center; }

		.p2p {
			h3 {
				font-weight: 400; } }
		.buy {
			h2 {
				color: #282828; }
			h3 > a {
				color: $brand-blue; } }

		.supplier-engagement {
			padding-bottom: 0px !important;
			.contents {
				max-height: 840px; }
			.laptop-coffee {
				position: absolute;
				z-index: 1; }
			.wrapper-right {
				position: relative;
				float: right;
				text-align: left;
				margin-top: 66px;
				z-index: 2; } }
		@include phone {
			padding-top: 66px;
			.container {
				background: url(../img/solutions/lamp-xs.png) no-repeat center bottom -155px;
				background-size: 1050px; }
			.buy {
				h2 {
					margin-bottom: 11px; }
				p {
					line-height: 20px !important;
					margin-bottom: 0px; }
				img.center {
					width: auto;
					margin: auto;
					margin-bottom: 11px; }
				&.row {
					margin-bottom: 33px; } }
			.p2p {
				h3 {
					font-size: 20px;
					font-weight: 600 !important;
					line-height: 33px; }
				p {
					margin-bottom: 0px; } }
			.supplier-engagement {
				padding-top: 88px;
				margin-bottom: 0px !important;
				h3 {
					font-size: 20px;
					line-height: 26px;
					font-weight: 600;
					padding: 0 33px;
					margin-top: 22px; }
				h4 {
					font-size: 18px;
					line-height: 20px;
					font-weight: 600;
					margin-bottom: 11px; }
				a {
					font-weight: 400; }
				img {
					position: relative !important;
					width: auto;
					margin-top: 33px; }
				.wrapper-right {
					float: none !important;
					text-align: center !important;
					margin: 66px auto 0; }
				.outer {
					width: 300px;
					margin: 0 auto;
					overflow: visible; }
				.inner {
					display: inline-block;
					position: relative;
					max-height: 288px;
					right: -50%; }
				.inner img {
					position: relative;
					left: -45%; } } }
		@include tablet-only {
			.container {
				background: url(../img/solutions/lamp-xs.png) no-repeat center bottom -242px; }
			.buy {
				h2 {
					font-size: 36px;
					line-height: 45px;
					padding-top: 11px;
					padding-bottom: 6px; }
				p {
					padding: 0 110px;
					margin-bottom: 0px; } }
			.p2p {
				p {
					margin-bottom: 0px; }
				.topBar {
					margin-bottom: 30px; } }
			.solution-box {}

			.wrapper-right {
				float: none !important;
				margin:  198px auto 22px !important;
				* {
					text-align: center !important;
					margin-left: auto;
					margin-right: auto; }
				a {
					display: table;
					margin: auto; }
				.topBar {
					margin-bottom: 22px !important; } }
			.supplier-engagement {
				margin-bottom: 0px; }
			.laptop-coffee {
				position: relative !important;
				display: block;
				margin: auto;
				width: 992px; } }
		@include tablet {
			padding: 44px 0 0;

			h3 {
				font-size: 28px; }
			p {
				font-size: 16px;
				line-height: 24px;
				margin: 0 0 22px 0;
				width: 100%; }
			.lamp {
				top: -44px;
				left: 530px; }
			.wrapper-right {
				width: 475px;
				h3, p, h4 {
					text-align: left; }
				h3 {
					font-size: 28px;
					line-height: 40px;
					font-weight: 400; }
				p {
					font-size: 18px;
					line-height: 20px;
					margin-bottom: 11px; }
				h4 {
					font-size: 18px;
					line-height: 20px;
					font-weight: 600;
					margin-top: 28px;
					margin-bottom: 14px;
					+ p {
						font-size: 14px;
						line-height: 20px; } }
				a {
					color: #00b0ff !important; }

				.topBar {
					margin-bottom: 11px; } } }
		@include desktop {
			padding-top: 55px;
			p {
				font-size: 18px;
				line-height: 26px; }
			.buy {
				h2 {
					font-size: 44px;
					line-height: 55px;
					padding-top: 6px;
					padding-bottom: 6px; }
				p {
					padding: 0 132px; } }
			.p2p {
				h3 {
					font-size: 28px;
					line-height: 40px;
					margin-bottom: 4px; } }
			.lamp {
				max-width: 95%;
				top: -110px;
				left: 440px; }
			.supplier-engagement {
				height: 373px; }
			.laptop-coffee {
				left: -255px; } }
		@include desktop-xl {
			padding: 88px 0 0;
			.buy {
				p {
					padding: 0 188px; } }
			.solutions .contents {
				width: 78%;
				padding: 0px;
				display: block;
				margin: auto;
				float: none; }
			.lamp {
				max-width: 82%;
				top: -148px;
				left: 535px; }
			.laptop-coffee {
				left: -121px; } } }

	.solutions-financial {
		background: #e6e7e8;
		position: relative;
		padding: 88px 0;

		.wrapper-left {
			float: left;
			text-align: left; }
		@include phone {
			padding-top: 66px;
			padding-bottom: 44px;
			.wrapper-left {
				float: none;
				margin-bottom: 22px;
				text-align: center;
				h3 {
					font-size: 20px;
					line-height: 26px;
					font-weight: 600;
					margin-top: 22px;
					padding: 0 22px; }
				p {
					margin-top: 11px; } } }
		@include tablet-only {
			.wrapper-left {
				display: block;
				float: none;
				margin: 0 auto 22px !important;
				h3 {
					font-weight: 600; }
				* {
					text-align: center !important; }
				a, .topBar {
					display: table;
					margin: auto; } } }
		@include tablet {
			.wrapper-left {
				width: 460px;
				margin-right: 110px;
				h3, p {
					text-align: left; }
				h3 {
					font-size: 28px;
					line-height: 40px;
					font-weight: 400; }
				p,a {
					font-size: 18px;
					line-height: 26px; }
				a {
					color: #00b0ff; }

				.topBar {
					margin-bottom: 11px; } } }
		@include desktop-sm {
			.wrapper-left {
				width: 360px;
				margin-right: 44px;
				.topBar {
					width: 333px; }
				h3 {
					font-size: 24px; }
				p {
					font-size: 16px; } }
			.solution-box {
				width: 270px;
				margin-right: 22px; } }
		@include desktop-xl; }

	.solutions-sellers {
		background: #1e1e1e;
		padding: 55px 0 88px;
		h2, .lead {
			color: $brand-white;
			text-align: center; }
		.lead {
			margin-bottom: 60px; }
		.seller-opportunities {
			@include make-xs-column(12);
			@include make-md-column(6);
			h3 {
				font-weight: 400;
				color: #00b0ff; }
			h4 {
				font-size: 14px;
				line-height: 20px;
				font-weight: 700;
				color: #ff9000;
				margin-top: 22px; }
			p {
				color: $brand-white;
				font-weight: 300; }
			.small_text {
				font-size: 14px;
				line-height: 20px; }
			a {
				font-size: 18px;
				line-height: 26px;
				font-weight: 400;
				color: #00b0ff;
				&:hover {
					color: white;
					text-decoration: underline; }
				&.finance-link {
					font-size: 14px;
					line-height: 20px; } }
			.finalScreenShot {
				position: absolute;
				top: -77px;
				left: -55px; }
			&.financial {
				margin-top: 50px;
				clear: both; } }

		.center {
			display: block;
			margin: auto; }
		.topBarBlue {
			background-image: -moz-linear-gradient( 180deg, rgb(0,78,255) 0%, rgb(0,176,255) 100%);
			background-image: -webkit-linear-gradient( 180deg, rgb(0,78,255) 0%, rgb(0,176,255) 100%);
			background-image: -ms-linear-gradient( 180deg, rgb(0,78,255) 0%, rgb(0,176,255) 100%);
			display: block;
			margin-bottom: 11px;
			width: 385px;
			height: 5px; }
		@include phone {
			padding: 66px 0;
			overflow: hidden;
			img.center {
				width: auto; }
			h2 {
				margin-top: 11px; }
			.lead {
				font-weight: 400;
				padding: 0 22px; }
			.seller-opportunities {
				text-align: center;
				margin-bottom: 44px;
				h3 {
					font-size: 20px;
					line-height: 33px;
					font-weight: 600;
					margin-top: 22px; }
				p {
					padding: 0 22px;
					margin-top: 11px; }
				a {
					font-size: 14px;
					line-height: 20px; }
				&.financial {
					margin-top: 0px;
					margin-bottom: 11px; } }
			.outer {
				width: 300px;
				margin: 0 auto;
				overflow: visible; }
			.inner {
				display: inline-block;
				position: relative;
				max-height: 288px;
				right: -50%; }
			.inner img {
				position: relative !important;
				left: -44% !important;
				width: 500px; }
			.finalScreenShot {
				left: 0px !important;
				top: 0px !important; } }

		@include tablet-only {
			padding: 88px 0 0;
			text-align: center;
			overflow: hidden;
			p {
				padding: 0 88px;
				margin-bottom: 8px; }
			.seller-opportunities {
				margin-bottom: 44px;
				&.financial {
					margin-top: 0px; }
				&:last-of-type {
					margin-bottom: 0px;
					margin-top: -88px; } }
			.finalScreenShot {
				position: relative !important;
				top: 69px !important;
				left: 0px !important;
				width: 80%; }
			.topBarBlue {
				margin: 0 auto 22px; } }

		@include desktop {
			h2 {
				font-size: 44px;
				line-height: 55px; }
			p {
				font-size: 18px;
				line-height: 26px; }
			.seller-opportunities {
				h3 {
					font-size: 28px;
					line-height: 40px;
					font-weight: 400;
					color: #00b0ff; }
				p {
					font-size: 18px;
					line-height: 26px; }
				.lead {
					padding: 0 100px; } } }

		@include desktop-xl {
			.lead {
				padding: 0 210px; }
			.seller-opportunities {
				padding-right: 110px; } } }

	/* Looking for the Marketo Form: go to _layout.sass */ }





