// Custom Styling for Roles > Accounts Payable Page
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.ap {
	.hero.go-paperless {
		background: url('../img/roles/ap/hero-go-paperless.jpg') no-repeat center top;
		height: 360px;
		.contents,
		.customer-logos {
			@extend .col-xs-12; }
		.contents {
			@extend .col-md-offset-1;
			@extend .col-md-11; }
		h1 {
			margin-top: 80px;
			margin-bottom: 10px;
			width: 66%;
			@extend .on-dark; }
		h3 {
			margin-bottom: 15px;
			width: 250px;
			@extend .on-dark; }
		.customer-logos {
			background: url('../img/roles/ap/customer-logos-800.png') no-repeat center bottom;
			background-size: contain;
			height: 70px;
			z-index: 50; }
		@include bg-customer-logos;
		&::after {
			top: 260px;
			z-index: 40; }
		@include tablet {
			background: url('../img/roles/ap/hero-desktop.jpg') no-repeat center top;
			height: 560px;
			.contents {
				padding-left: 30px; }
			&::after {
				top: 460px; }
			h1 {
				margin-top: 130px;
				width: 580px; }
			h3 {
				padding-bottom: 20px;
				width: 380px; }
			.customer-logos {
				background: url('../img/roles/ap/customer-logos-desktop.png') no-repeat center bottom;
				background-size: contain;
				height: 110px;
				z-index: 50; } }
		@include desktop {
			.contents {
				padding-left: 0;
				padding-right: 0; } }
		@include desktop-xl {
			background-size: cover; } }
	.get-more-done {
		background: #E7E7E7 url('../img/roles/ap/get-more-done-laptop.png') no-repeat 100% 90%;
		color: $brand-black-lighter;
		text-align: center;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-12;
			padding: 20px 0; }
		.product-screenshot {
			@extend .col-sm-6;
			@extend .col-md-7;
			@extend .hidden-xs;
			.laptop {
				@extend .pull-right; } }
		.text-intro {
			@extend .col-sm-5;
			@extend .col-md-5; }
		p {
			padding: 0 10px;
			margin-bottom: 310px; }
		@include phoneplus {
			background: #E7E7E7 url('../img/roles/ap/get-more-done-laptop.png') no-repeat 50% 90%; }
		@include tablet {
			padding: 33px 0;
			background: #E7E7E7;
			height: 572px;
			.product-screenshot {
				text-align: right;
				.laptop {
					margin-right: -120px; } }
			.text-intro {
				padding-top: 70px;
				text-align: left; }
			p {
				padding: 0;
				margin-bottom: 0; } }
		@include desktop {
			.text-intro {
				padding-top: 110px; } } }
	.simplify-your-entire-process {
		background: $brand-black-lighter url('../img/roles/ap/simplify-process-mobile.png') no-repeat center bottom;
		color: $brand-lt-grey;
		text-align: center;
		.contents {
			@extend .col-xs-12;
			padding: 22px 0; }
		.simplify {
			@extend .col-sm-3; }
		.process {
			@extend .col-sm-offset-2;
			@extend .col-sm-5; }
		h2 {
			margin: 0 auto; }
		h3 {
			margin-top: 22px;
			color: $brand-lt-grey;
			padding-bottom: 8px; }
		hr {
			background: url('../img/hr-dash.png') repeat-x top center;
			height: 11px;
			margin-top: 11px;
			margin-bottom: 0;
			border-top: none;
			padding: 0;
			width: 92%; }
		p {
			padding: 0px 10px; }
		p.sub {
			margin-bottom: 190px; }
		@include tablet {
			background: $brand-black-lighter url('../img/roles/ap/simplify-process-desktop.png') no-repeat 50% 100%;
			background-size: contain;
			height: 476px;
			.contents {
				padding-top: 44px;
				padding-left: 30px; }
			p.sub {
				margin-bottom: 0; }
			h2 {
				font-size: 28px;
				line-height: 33px;
				margin: 0; }
			.simplify {
				text-align: left;
				p {
					padding: 0;
					font-size: 15px;
					line-height: 22px; } }
			.process {
				text-align: center;
				p.sub {
					font-size: 14px;
					line-height: 18px; }
				h3 {
					font-size: 18px;
					margin: 0 auto;
					padding: 5px 0; } } }
		@include desktop {
			.contents {
				padding-left: 0; }
			.simplify {
				p {
					font-size: 18px;
					line-height: 30px; } } }
		@include desktop-xl {
			h2 {
				font-size: 34px;
				line-height: 45px; }
			.simplify {
				p {
					font-size: 18px; } } } }
	.track-every-transaction {
		background: $brand-white url('../img/roles/ap/track-every-transaction-800.jpg') no-repeat center bottom;
		color: $brand-black-lighter;
		position: relative;
		text-align: center;
		height: 506px;
		z-index: 10;
		.bg-treatment {
			@extend .hidden-xs;
			@include bg-treatment-white; }
		.contents {
			@extend .col-xs-12;
			padding: 20px 0; }
		p {
			@extend .col-sm-offset-1;
			@extend .col-sm-10; }
		@include tablet {
			background: $brand-white url('../img/roles/ap/track-every-transaction-desktop-bg.jpg') no-repeat center bottom;
			height: 800px;
			.contents {
				padding: 40px 0;
				/* Use styling below when you have a background treatment */
				position: relative;
				top: 0;
				right: 0;
				left: 0;
				width: 100%;
				height: 100%;
				z-index: 30; }
			p {
				margin-bottom: 20px; } }
		@include desktop {
			height: 770px; }
		@include desktop-xl {
			background-size: cover; } }

	.one-place-to-communicate {
		padding: 20px 0;
		background: $brand-white url('../img/roles/ap/one-place-mobile.png') no-repeat 50% 90%;
		color: $brand-black-lighter;
		text-align: center;
		.contents {
			@extend .col-xs-12; }
		p {
			padding: 0 20px;
			margin-bottom: 340px; }
		@include tablet {
			padding: 40px 0;
			background: $brand-white url('../img/roles/ap/one-place-desktop.gif') no-repeat 50% 85%;
			p {
				margin: 20px 110px 280px 110px; } } }
	.testimonials {
		background: $brand-blue;
		padding: 20px 0;
		text-align: center;
		.contents {
			@extend .col-xs-11;
			@extend .col-sm-offset-1;
			@extend .col-sm-10; }
		img {
			display: block;
			padding: 10px 0;
			margin: 0 auto;
			width: 77px; }
		.contents {
			@extend .col-xs-12; }
		.quotation {
			font-size: 18px;
			padding: 0 0 5px;
			color: $brand-white; }
		.quoted-person {
			color: $brand-navy;
			font-size: 12px; }
		@include tablet {
			padding: 40px 0;
			img {
				float: left;
				margin-right: 20px; }
			.quotation {
				font-size: 26px;
				line-height: 1.2;
				text-align: left; }
			.quoted-person {
				text-align: left;
				font-size: 14px; } } }

	.no-more-phone-calls {
		padding: 30px 0 20px;
		background: $brand-white url('../img/roles/ap/no-more-phone-calls-800.jpg') no-repeat center top;
		background-size: cover;
		position: relative;
		color: $brand-white;
		min-height: 346px;
		z-index: 10;
		.bg-treatment {
			@extend .hidden-xs;
			@include bg-treatment-dark; }
		.contents {
			@extend .col-xs-12;
			@extend .col-md-offset-1;
			@extend .col-md-11; }
		.text-intro {
			@extend .col-xs-7;
			@extend .col-sm-1;
			@extend .col-sm-4;
			h2 {
				@extend .on-dark; } }
		.happy-supplier {
			@extend .col-sm-7; }
		@include tablet {
			padding: 0;
			background: $brand-lt-grey url('../img/roles/ap/no-more-phone-calls-bg2.jpg') no-repeat center top;
			height: 548px;
			.contents {
				/* Use styling below when you have a background treatment */
				position: relative;
				top: 0;
				right: 0;
				left: 0;
				width: 100%;
				height: 100%;
				z-index: 30; }
			.text-intro {
				padding-top: 66px; } }
		@include desktop-xl {
			background-size: cover;
			.text-intro {
				padding-top: 132px; } } }

	.achieve-faster-roi {
		padding: 20px 0 40px;
		background: $brand-blue url('../img/roles/ap/achieve-faster-mobile.png') no-repeat 50% 95%;
		color: $brand-white;
		text-align: center;
		.contents {
			@extend .col-xs-12; }
		h2 {
			color: $brand-white; }
		h3 {
			margin: 20px auto 5px;
			color: $brand-navy-light;
			@extend .col-sm-12; }
		hr {
			@extend hr.dash;
			@extend .col-xs-offset-1;
			@extend .col-xs-10;
			@extend .col-sm-offset-3;
			@extend .col-sm-6; }
		p {
			padding: 0px 20px;
			@extend .col-sm-offset-2;
			@extend .col-sm-8; }
		p.sub {
			padding: 0px 35px;
			@extend .col-sm-offset-2;
			@extend .col-sm-8;
			margin-bottom: 190px;
			font-size: 14px; }
		@include tablet {
			padding: 60px 0 40px;
			background: $brand-blue;
			.contents {
				background: $brand-blue url('../img/roles/ap/achieve-faster-roi-desktop.png') no-repeat 50% 95%;
				background-size: 90%; }
			p.sub {
				margin-bottom: 250px;
				font-size: 14px;
				line-height: 1.4;
				padding: 0 7.5%; } }
		@include desktop-xl {
			background-size: initial; } }

	.resources {
		padding: 33px 0;
		background: $brand-baby-blue;
		text-align: center;

		h2 {
			margin: 0 auto 11px;
			width: 280px;
			text-align: center;
			color: $brand-orange;
			margin-top: 6px; }
		h3 {
			padding: 0;
			font-weight: 200;
			font-size: 18px;
			line-height: 22px;
			text-align: center;
			width: 280px;
			margin: 0 auto; }
		p {
			text-align: left; }
		img {
			width: 100% !important; }

		.contents {
			@extend .col-xs-12; }
		.resource-wrapper {
			@extend .col-sm-4;
			margin-top: 22px; }

		@include tablet {
			h2 {
				width: 100%;
				margin-bottom: 22px; }
			p {
				width: 100%;
				padding: 0 11px 0 0;
				font-size: 16px;
				line-height: 22px; }
			p.last-of-type {
				padding-top: 11px; } }
		@include desktop {
			p {
				font-size: 18px;
				line-height: 26px; }
			img {
				max-height: 153px; } }
		@include desktop-xl {
			img {
				max-height: 188px; } } }


	/* Looking for the Marketo Form: go to _layout.sass */ }


.smartling-fr {
	.hero.go-paperless {
		h1 {
			margin-top: 11px;
			width: 99%; }
		h3 {
			width: 300px; }
		@include tablet {
			h1 {
				margin-top: 130px;
				width: 780px; }
			h3 {
				width: 530px; } } }
	.simplify-your-entire-process {
		@include desktop {
			.simplify {
				p {
					font-size: 15px; } } } } }





