// Custom Styling for Roles > Finance Page
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.finance {
	.hero.control-cost {
		background: url('../img/roles/finance/hero-800.jpg') no-repeat center bottom;
		height: 405px;
		color: $brand-white;
		.contents,
		.customer-logos {
			@extend .col-xs-12; }
		.contents {
			@extend .col-md-offset-1;
			@extend .col-md-11; }
		h1 {
			margin-top: 30px;
			margin-bottom: 15px;
			width: 204px;
			color: $brand-white; }
		h3 {
			margin-bottom: 25px;
			width: 220px;
			color: $brand-white; }
		.customer-logos {
			background: url('../img/roles/finance/customer-logos-800.png') no-repeat center bottom;
			background-size: contain;
			height: 65px;
			z-index: 50; }
		@include bg-customer-logos;
		&::after {
			top: 325px;
			z-index: 40;
			height: 80px; }
		@include tablet {
			background: url('../img/roles/finance/hero-desktop.jpg') no-repeat 50% 90%;
			height: 560px;
			.contents {
				padding-left: 30px; }
			&::after {
				top: 479px; }
			h1 {
				margin-top: 60px;
				width: 340px; }
			h3 {
				width: 420px; }
			.customer-logos {
				background: url('../img/roles/finance/customer-logos-desktop.png') no-repeat center bottom;
				background-size: contain;
				height: 80px;
				z-index: 50; } }
		@include desktop {
			h1 {
				width: 500px; }
			.contents {
				padding-left: 0px; }
			.customer-logos {
				background: url('../img/roles/finance/customer-logos-desktop.png') no-repeat center bottom;
				background-size: contain;
				height: 135px;
				z-index: 50; } }
		@include desktop-xl {
			height: 700px;
			background: url('../img/roles/finance/hero-desktop.jpg') no-repeat 50% 65%;
			background-size: cover;
			&::after {
				top: 619px; }
			.customer-logos {
				background: url('../img/roles/finance/customer-logos-desktop.png') no-repeat center bottom;
				background-size: contain;
				height: 200px;
				z-index: 50; }
			h1 {
				margin-top: 120px; }
			h3 {
				margin-bottom: 40px; } } }

	.get-insights-you-need {
		background: #E7E7E7 url('../img/roles/finance/get-more-done-laptop.png') no-repeat 100% 90%;
		color: $brand-black-lighter;
		text-align: center;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-12;
			padding: 20px 0; }
		.product-screenshot {
			@extend .col-sm-6;
			@extend .hidden-xs;
			.laptop {
				@extend .pull-right; } }
		.text-intro {
			@extend .col-sm-5; }
		p {
			padding: 0 10px;
			margin-bottom: 310px; }
		@include phoneplus {
			background: #E7E7E7 url('../img/roles/finance/get-more-done-laptop.png') no-repeat 50% 90%; }
		@include tablet {
			padding: 33px 0;
			background: #E7E7E7;
			height: 590px;
			.product-screenshot {
				text-align: right;
				.laptop {
					margin-right: -120px; } }
			.text-intro {
				padding-top: 70px;
				text-align: left; }
			p {
				padding: 0;
				margin-bottom: 0; } }
		@include desktop {
			.text-intro {
				padding-top: 90px; } } }

	.take-risk-out {
		padding: 20px 0;
		background: $brand-white url('../img/roles/finance/take-risk-bg-800.jpg') no-repeat center bottom;
		color: $brand-black-lighter;
		text-align: center;
		height: 610px;
		.contents {
			@extend .col-xs-12; }
		.intro-text {
			@extend .col-sm-6; }
		.persona {
			@extend .col-sm-6; }
		p {
			margin-bottom: 30px;
			padding: 0 10px; }
		.play-video {
			margin: 0 auto;
			text-align: center;
			width: 100px;
			p.sub {
				font-size: 12px;
				padding: 0;
				margin: 0;
				line-height: 1.4; } }
		@include tablet {
			padding: 20px 0 0;
			background: $brand-white url('../img/roles/finance/take-risk-out-bg.jpg') no-repeat center bottom;
			background-size: cover;
			height: 597px;
			text-align: left;
			h2 {
				margin-top: 50px;
				font-size: 34px; }
			p {
				padding: 0;
				margin-bottom: 40px; }
			.play-video {
				width: 200px; } } }

	.get-better-compliance {
		padding: 20px 0;
		background: $brand-black-lighter url('../img/roles/finance/map-800.png') no-repeat center 95%;
		text-align: center;
		.contents {
			@extend .col-xs-12; }
		h2 {
			width: 80%;
			margin: 0 auto; }
		p {
			color: $brand-lt-grey;
			padding: 0px 5px;
			margin-bottom: 210px; }
		@include tablet {
			padding: 60px 0;
			background: $brand-black-lighter url('../img/roles/finance/get-better-map-desktop.png') no-repeat center 85%;
			h2 {
				width: 100%; }
			p {
				padding: 0px 100px;
				margin-bottom: 520px; } } }

	.create-successful-epp {
		padding: 20px 0 40px;
		background: $brand-blue url('../img/roles/finance/create-bg-800.jpg') no-repeat center bottom;
		color: $brand-white;
		text-align: center;
		height: 650px;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-offset-2;
			@extend .col-sm-8; }
		h2 {
			width: 80%;
			margin: 0 auto;
			color: $brand-white; }
		p {
			padding: 0px 20px;
			color: $brand-white;
			margin-bottom: 45px; }
		.play-video {
			margin: 0 auto;
			text-align: center;
			width: 220px;
			p.sub {
				font-size: 12px;
				padding: 0;
				margin: 0;
				line-height: 1.4;
				color: $brand-white; } }
		@include tablet {
			padding: 40px 0;
			background: $brand-blue url('../img/roles/finance/create-epp-bg-desktop.png') no-repeat center bottom;
			height: 615px;
			h2 {
				width: 100%; }
			p {
				margin: 20px auto 40px;
				padding: 0;
				color: $brand-black-lighter; }
			.play-video {
				width: 180px; } }

		@include desktop-xl {
			height: 700px;
			background-size: cover; } }

	.make-p2p-effective {
		padding: 20px 0;
		background: $brand-white url('../img/roles/finance/make-p2p-chart-800.png') no-repeat 50% 90%;
		text-align: center;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-offset-2;
			@extend .col-sm-8; }
		h2 {
			margin: 0 auto;
			width: 86%; }
		p {
			margin: 20px auto;
			width:  86%;
			margin-bottom: 160px; }
		@include tablet {
			padding: 60px 0;
			background: $brand-white;
			background-size: cover;
			height: 540px;
			h2 {
				width: 100%; }
			p {
				margin-top: 20px;
				width: 100%;
				margin-bottom: 40px; }
			img {
				width: 100%;
				max-width: 850px; } } }
	.resources {
		padding: 33px 0;
		background: $brand-baby-blue;
		text-align: center;

		h2 {
			margin: 0 auto 11px;
			width: 280px;
			text-align: center;
			color: $brand-orange;
			margin-top: 6px; }
		h3 {
			padding: 0;
			font-weight: 200;
			font-size: 18px;
			line-height: 22px;
			text-align: center;
			width: 280px;
			margin: 0 auto; }
		p {
			text-align: left; }
		img {
			width: 100% !important; }

		.contents {
			@extend .col-xs-12; }
		.resource-wrapper {
			@extend .col-sm-4;
			margin-top: 22px; }

		@include tablet {
			h2 {
				width: 100%;
				margin-bottom: 22px; }
			p {
				width: 100%;
				padding: 0 11px 0 0;
				font-size: 16px;
				line-height: 22px; }
			p.last-of-type {
				padding-top: 11px; } }
		@include desktop {
			p {
				font-size: 18px;
				line-height: 26px; }
			img {
				max-height: 153px; } }
		@include desktop-xl {
			img {
				max-height: 188px; } } } }

/* Looking for the Marketo Form: go to _layout.sass */

.smartling-fr {
	.finance {
		.hero.control-cost {
			h1 {
				width: 290px; }
			h3 {
				width: 280px; }
			@include tablet {
				h1 {
					width: 680px; }
				h3 {
					width: 620px; } } }
		.get-insights-you-need {
			@include tablet {
				.text-intro {
					padding-top: 11px; } } } } }







