// Custom Styling for Contact Us Page - En
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.contact-us-pagev2 {
	// Import the new theme for TS headers/copy
	@import "_themev2_textstyles";
	.hero {
		padding: 44px 0 88px;
		@include linear-gradient(150deg, $brand-p-ent 20%, $brand-s-ent 100%);
		position: relative;
		text-align: center;
		.arrow {
			position: absolute;
			bottom: 0;
			width: 100%;
			height: 0;
			background-color: $brand-white; }

		.arrow:before,
		.arrow:after {
			content: '';
			position: absolute;
			bottom: 100%;
			width: 50%;
			box-sizing: border-box; }

		.arrow:before {
			right: 50%;
			border-bottom: 20px solid $brand-white;
			border-right: 18px solid transparent; }

		.arrow:after {
			left: 50%;
			border-bottom: 20px solid $brand-white;
			border-left: 18px solid transparent; }
		h1,
		h3 {
			color: $brand-white; }
		h1 {
			margin-bottom: 11px; }

		.contents {
			@include make-xs-column(12); }
		@include phone {
			.contents {
				width: 320px;
				margin: 0 auto;
				float: none; } }
		@include tablet {
			padding: 66px 0 99px; }
		@include desktop {
			padding: 88px 0 132px; } }

	.contact-form {
		padding: 44px 0 0;
		h2,
		h4 {
			color: $brand-black-lighter;
			margin: 0 auto;
			text-align: center; }
		h2 {
			margin-bottom: 11px; }
		h4 {
			margin-bottom: 22px; }

		.contents {
			@include make-xs-column(12);
			@include make-sm-column-offset(2);
			@include make-sm-column(8);
			@include make-lg-column-offset(3);
			@include make-lg-column(6); }
		// Customize WP Contact Form 7
		.wpcf7-form {
			position: relative;
			.menu-710 {
				&:after {
					position: absolute;
					top: 0;
					right: 10px;
					font-family: 'FontAwesome';
					content: '\f107';
					z-index: 10;
					font-size: 28px;
					color: $brand-accent-grayscale-4;
					pointer-events: none; } }
			label {
				color: $brand-p-ent;
				text-transform: uppercase;
				font-size: 12px;
				line-height: 1;
				font-weight: 200;
				&:hover {
					cursor: pointer; } }
			input,
			select,
			textarea {
				width: 100%;
				background-color: $brand-accent-p;
				font-weight: 300;
				font-size: 18px;
				text-align: left;
				letter-spacing: 0.8px;
				color: $brand-black-lighter;
				border-radius: 0;
				border: none;
				box-shadow: none;
				height: 44px;
				font-family: $font-family-sans-serif;
				padding: 6px 12px;
				margin-bottom: 11px;
				&::-webkit-input-placeholder {
					color: $brand-accent-grayscale-4; } }
			select {
				color: $brand-accent-grayscale-4;
				font-size: 14px;
				@include appearance(none);
				&::-ms-expand { // For IE11
					display: none; } }
			textarea {
				height: 220px; }
			input[type="submit"] {
				background: $brand-p-ent;
				color: $brand-white !important;
				font-weight: 800;
				font-size: 12px;
				text-transform: uppercase;
				margin: 0 auto;
				display: block;
				text-align: center;
				width: 160px;
				height: 33px;
				border-radius: 22px;
				border: none;
				padding: 0;
				cursor: pointer; }
			.wpcf7-mail-sent-ok {
				padding: 15px;
				font-size: 16px;
				font-family: $font-family-sans-serif;
				font-weight: 200;
				@include transition(all 500ms ease-in);
				background: $brand-sky-blue !important;
				border: none; }

			.wpcf7-form-control-wrap {
				display: inline-block;
				width: 100%; }
			.wpcf7-list-item {
				width: 30%;
				float: left;
				margin-right: 11px;
				margin-bottom: 0;
				margin-left: 0;
				padding: 0;
				&:nth-of-type(3),
				&:nth-of-type(6) {
					margin-right: 0; }
				label {
					margin: 0;
					width: 100%; }
				.wpcf7-list-item-label {
					background: $brand-accent-p;
					color: $brand-black-lighter;
					font-weight: 800;
					width: 100%;
					text-align: center;
					display: inline-block;
					padding: 15px 0px; }
				input[type="radio"] {
					display: none; }
				input:checked ~ span.wpcf7-list-item-label {
					background: $brand-p-ent;
					color: $brand-white; } }
			div#group-672 {
				padding-bottom: 44px;
				text-align: center;
				a {
					font-size: 18px;
					font-weight: 300;
					line-height: 33px; } } }

		// End CF7 Customization
		@include phone {
			.contents {
				width: 320px;
				margin: 0 auto;
				float: none; } }
		@include tablet {
			.wpcf7-form {
				.wpcf7-form-control-wrap {
					padding-right: 15px;
					&.radio-394 {
						padding-right: 0; } }
				.wpcf7-list-item {
					width: 31%; } } }
		@include desktop {
			.wpcf7-form {
				.wpcf7-form-control-wrap {
					padding-right: 20px; }
				.wpcf7-list-item {
					width: 16%;
					margin-right: 0;
					.wpcf7-list-item-label {
						border-left: 1px solid $brand-accent-grayscale-6; }
					&.first {
						.wpcf7-list-item-label {
							border-left: none; } }
					&:hover {
						cursor: pointer; } } } } }

	.offices {
		padding: 44px 0;
		background: $brand-accent-p;
		h2,
		h3 {
			color: $brand-black-lighter;
			text-align: center; }
		h2 {
			padding: 0 60px 11px; }
		h3 {
			padding: 0 30px 22px; }
		.contents {
			@include make-xs-column(12);
			@include make-lg-column-offset(1);
			@include make-lg-column(10); }
		.office-addresses {
			background: transparent;
			margin-bottom: 22px;
			@include make-xs-column(12);
			@include make-sm-column(3); }
		@include phone {
			.container {
				padding: 0; }
			.contents {
				max-width: 320px;
				margin: 0 auto 22px;
				float: none;
				padding: 0; }
			.office-addresses {
				height: 170px;
				padding-top: 22px;
				&.sf {
					background: $brand-white url('../img/contact/v2/sf-xs.png') no-repeat center top; }
				&.ny {
					background: $brand-white url('../img/contact/v2/ny-xs.png') no-repeat center top; }
				&.cph {
					background: $brand-white url('../img/contact/v2/cph-xs.png') no-repeat center top; }
				&.london {
					background: $brand-white url('../img/contact/v2/london-xs.png') no-repeat center top; }
				&.paris {
					background: $brand-white url('../img/contact/v2/paris-xs.png') no-repeat center top; }
				&.suzhou {
					background: $brand-white url('../img/contact/v2/suzhou-xs.png') no-repeat center top; }
				&.tokyo {
					background: $brand-white url('../img/contact/v2/tokyo-xs.png') no-repeat center top; }
				&.munich {
					background: $brand-white url('../img/contact/v2/munich-xs.png') no-repeat center top; }
				&.sydney {
					background: $brand-white url('../img/contact/v2/sydney-v2-xs.jpg') no-repeat center top; }
				&.romania {
					background: $brand-white url('../img/contact/v2/romania-xs.jpg') no-repeat center top; }
				&.sweden {
					background: $brand-white url('../img/contact/v2/sweden-bromma-xs.jpg') no-repeat center top; }
				&.frankfurt {
					background: $brand-white url('../img/contact/v2/germany-frankfurt-xs.jpg') no-repeat center top; }
				&.oslo {
					background: $brand-white url('../img/contact/v2/norway-oslo-xs.jpg') no-repeat center top; }
				&.shanghai {
					background: $brand-white url('../img/contact/v2/shanghai-xs.png') no-repeat center top; }
				&.malaysia {
					background: $brand-white url('../img/contact/v2/kuala-xs.png') no-repeat center top; }
				h4 {
					text-align: right;
					font-weight: 800;
					font-size: 18px;
					line-height: 1;
					color: $brand-white; }
				a[itemprop="name"] {
					display: inline-block;
					color: $brand-blue;
					text-align: left;
					line-height: 22px;
					margin-top: 44px;
					font-weight: 600;
					position: relative;
					vertical-align: bottom;
					padding-left: 25px;
					&::before {
						content: '';
						background: url('../img/contact/v2/icon-droppin.png') no-repeat left bottom;
						background-size: 16px;
						position: absolute;
						bottom: 4px;
						left: 0;
						width: 18px;
						height: 44px; } }
				div[itemprop="address"] {
					text-align: left; } } }

		@include tablet {
			padding: 66px 0;
			h3 {
				padding: 0 60px 44px; }
			.office-addresses {
				width: 29%;
				height: 308px;
				margin: 0 15px 30px;
				text-align: center;
				position: relative;
				padding: 0;
				@include perspective(1000px);
				&.sf {
					.office-photo {
						background: $brand-white url('../img/contact/v2/sf.png') no-repeat center top;
						background-size: cover; } }
				&.ny {
					.office-photo {
						background: $brand-white url('../img/contact/v2/ny.png') no-repeat center top;
						background-size: cover; } }
				&.cph {
					.office-photo {
						background: $brand-white url('../img/contact/v2/cph.png') no-repeat center top;
						background-size: cover; } }
				&.london {
					.office-photo {
						background: $brand-white url('../img/contact/v2/london.png') no-repeat center top;
						background-size: cover; } }
				&.paris {
					.office-photo {
						background: $brand-white url('../img/contact/v2/paris.png') no-repeat center top;
						background-size: cover; } }
				&.suzhou {
					.office-photo {
						background: $brand-white url('../img/contact/v2/suzhou.png') no-repeat center top;
						background-size: cover; } }
				&.tokyo {
					.office-photo {
						background: $brand-white url('../img/contact/v2/tokyo.png') no-repeat center top;
						background-size: cover; } }
				&.munich {
					.office-photo {
						background: $brand-white url('../img/contact/v2/munich.png') no-repeat center top;
						background-size: cover; } }
				&.sydney {
					.office-photo {
						background: $brand-white url('../img/contact/v2/sydney-v2.jpg') no-repeat center top;
						background-size: cover; } }
				&.romania {
					.office-photo {
						background: $brand-white url('../img/contact/v2/romania.png') no-repeat center top;
						background-size: cover; } }
				&.sweden {
					.office-photo {
						background: $brand-white url('../img/contact/v2/sweden-bromma.jpg') no-repeat center top;
						background-size: cover; } }
				&.frankfurt {
					.office-photo {
						background: $brand-white url('../img/contact/v2/germany-frankfurt.jpg') no-repeat center top;
						background-size: cover; } }
				&.oslo {
					.office-photo {
						background: $brand-white url('../img/contact/v2/norway-oslo-v2.jpg') no-repeat center top;
						background-size: cover; } }
				&.shanghai {
					.office-photo {
						background: $brand-white url('../img/contact/v2/shanghai.png') no-repeat center top;
						background-size: cover; } }
				&.malaysia {
					.office-photo {
						background: $brand-white url('../img/contact/v2/kuala.png') no-repeat center top;
						background-size: cover; } }
				&:hover,
				&.hover {
					.office-cards {
						@include transform(rotateY(180deg)); } }
				.office-cards {
					@include transition(1s);
					@include transform-style(preserve-3d);
					position: relative; }
				.office-photo,
				.office-address {
					@include backface-visibility(hidden);
					width: 100%;
					height: 308px;
					position: absolute;
					top: 0;
					left: 0; }
				.office-photo {
					font-size: 28px;
					font-weight: 800;
					color: $brand-white;
					padding-top: 231px;
					z-index: 2;
					@include transform(rotateY(0deg)); }
				.office-address {
					padding: 110px 0 0;
					background: $brand-white;
					@include transform(rotateY(180deg));
					&::before {
						content: '';
						background: url('../img/contact/v2/icon-droppin.png') no-repeat center top;
						background-size: 32px;
						position: absolute;
						margin: 0 auto;
						top: 38px;
						left: 0;
						right: 0;
						width: 32px;
						height: 50px; }
					div[itemprop="name"] {
						font-weight: 600;
						margin: 11px 0; }
					div[itemprop="address"] {
						font-weight: 200;
						line-height: 22px; } } } }
		@include desktop {
			h3 {
				padding: 0 120px 66px; }
			h4 {
				font-size: 30px; }
			.office-addresses {
				height: 330px;
				.office-photo,
				.office-address {
					height: 330px; }
				.office-photo {
					font-size: 34px;
					padding-top: 253px; }
				.office-address {
					div[itemprop="name"] {
						font-size: 18px;
						line-height: 33px;
						margin: 11px 0 0; }
					div[itemprop="address"] {
						font-size: 18px;
						line-height: 33px; } } } }

		@include desktop-xl {
			.contents {
				padding: 0; }
			.office-addresses {
				&:nth-of-type(1),
				&:nth-of-type(4),
				&:nth-of-type(7),
				&:nth-of-type(10),
				&:nth-of-type(13) {
					margin-left: 30px; }
				&:nth-of-type(3),
				&:nth-of-type(6),
				&:nth-of-type(9),
				&:nth-of-type(12) {
					margin-right: 0; } } } } }









