// Custom Styling for Events and Webinar Page
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.events-webinars-page {
	.hero.ts-live {
		padding: 33px 0 0;
		background: $brand-white;
		text-align: center;
		.contents {
			@extend .col-xs-12;
			@extend .col-md-offset-2;
			@extend .col-md-8;
			@extend .col-lg-offset-2;
			@extend .col-lg-8; }
		h1 {
			color: $brand-blue;
			font-size: 28px;
			line-height: 33px;
			padding: 0 11px;
			span {
				color: $brand-black;
				display: block; } }

		h3 {
			color: $brand-black;
			margin-top: 22px;
			font-size: 18px;
			line-height: 22px;
			padding: 0 11px;
			font-weight: 300; }
		hr {
			margin-top: 33px; }

		@include tablet {
			padding: 66px 0 0;
			h1 {
				font-size: 48px;
				line-height: 55px;
				margin-bottom: 33px; }
			h3 {
				font-size: 26px;
				line-height: 33px;
				padding: 0 33px; }
			hr {
				margin-top: 66px;
				width: 100%; } } }

	.featured-event {
		padding: 33px 0;
		text-align: center;
		.contents {
			@extend .col-xs-12; }
		.featured-image {
			@extend .col-sm-7;
			@extend .col-md-7;
			@extend .col-lg-offset-1;
			@extend .col-lg-6; }
		.featured-info {
			@extend .col-sm-5;
			@extend .col-md-5;
			@extend .col-lg-4;
			.meta {
				font-size: 12px;
				font-weight: 600;
				text-transform: uppercase;
				color: $brand-grey;
				margin-top: 33px;
				text-align: left; }
			.type.webinars {
				color: $brand-orange;
				padding-right: 7px; }
			.type.events {
				color: $brand-blue;
				padding-right: 7px; }
			.event-location {
				padding-right: 7px;
				&::before {
					content: '|';
					padding-right: 7px; } }
			.date-posted {
				&::before {
					content: '|';
					padding-right: 7px; } } }
		h2 {
			margin-bottom: 33px;
			text-align: center; }
		h4 {
			text-align: left; }
		img {
			@extend .img-responsive; }
		p {
			text-align: left;
			max-height: 66px;
			overflow: hidden;
			margin-bottom: 22px; }
		hr {
			margin-top: 44px; }
		.featured-image {
			&.webinars,
			&.events {
				position: relative;
				&::before {
					font-family: LinearIcons;
					position: absolute;
					bottom: 16px;
					right: 31px;
					font-size: 46px;
					color: $brand-white;
					line-height: 1;
					@include font-smoothing; } }
			&.webinars {
				&::before {
					content: '\e6f0'; } }
			&.events {
				&::before {
					content: '\e61b'; } } }

		@include tablet {
			padding: 44px 0;
			text-align: left;
			h3 {
				text-align: left;
				color: $brand-orange; }
			h4 {
				font-size: 22px;
				line-height: 28px; }
			p {
				font-size: 14px;
				line-height: 22px; }
			hr.dashed {
				margin-top: 44px; }
			.featured-info {
				.meta {
					margin-top: 0; } } }
		@include desktop {
			padding: 55px 0;
			.featured-info {
				.meta {
					margin-top: 22px; } }
			h4 {
				font-size: 24px;
				line-height: 33px; }
			hr {
				margin-top: 55px; } } }

	.events-intro {
		padding: 33px 0;
		height: 377px;
		.container {
			.row {
				background: url('../img/events-webinars/events-webinars-start.jpg') no-repeat center bottom;
				height: 320px; } } }

	.events-filters-listings {
		padding: 22px 0;
		text-align: center;
		.events-search,
		.events-search-filter {
			h2 {
				margin-bottom: 33px;
				color: $brand-blue;
				font-size: 26px;
				text-align: center;
				padding-left: 15px; }
			h3 {
				font-size: 18px;
				line-height: 22px;
				color: $brand-dk-grey;
				text-align: left;
				margin-bottom: 11px; }
			.contents {
				@extend .col-lg-offset-1;
				@extend .col-lg-10;
				.search {
					@extend .col-xs-12;
					@extend .col-sm-4;
					margin-bottom: 22px; }
				.filter {
					@extend .col-xs-12;
					@extend .col-sm-12;
					.option-set.by-locations,
					.option-set.by-types {
						@extend .col-xs-12;
						@extend .col-sm-6; }
					.option-set.by-locations {
						margin-bottom: 22px; } }

				.search {
					.input-group {
						input[type=text] {
							background: $brand-black-lighter;
							padding: 0;
							border: none;
							font-size: 14px !important;
							height: 44px;
							border-radius: 0;
							width: 100%;
							color: $brand-white !important;
							&:focus,
							&:active {
								outline: none; } }
						.input-group-addon {
							border-radius: 0;
							border: none;
							vertical-align: middle;
							padding: 0;
							background: $brand-black-lighter;
							&::after {
								font-family: FontAwesome;
								content: "\f002";
								padding: 11px;
								color: #7B7B7B !important;
								font-size: 22px;
								background: transparent;
								border-radius: 0;
								border: none; } } } }

				.filter {
					.option-set {
						width: 100%; }
					.option-set.by-locations {
						z-index: 900; }
					.option-set.by-types {
						z-index: 800; }
					.checkbox-dropdown {
						width: 100%;
						background: $brand-black-lighter;
						border: 1px solid $brand-black-lighter;
						cursor: pointer;
						padding: 11px 22px;
						position: relative;
						text-align: left;
						color: $brand-white;
						z-index: 1000;
						-webkit-touch-callout: none;
						-webkit-user-select: none;
						-khtml-user-select: none;
						-moz-user-select: none;
						-ms-user-select: none;
						user-select: none;
						&:hover {
							cursor: pointer; }

						/* Display CSS arrow to the right of the dropdown text */
						&:after {
							content: "";
							height: 0;
							position: absolute;
							width: 0;
							border: 6px solid transparent;
							border-top-color: $brand-white;
							top: 50%;
							right: 22px;
							margin-top: -3px; }

						/* Reverse the CSS arrow when the dropdown is active */
						&.is-active:after {
							border-bottom-color: $brand-black-lighter;
							border-top-color: $brand-white;
							margin-top: -3px; }

						.checkbox-dropdown-list {
							list-style: none;
							color: $brand-white;
							margin: 0;
							padding: 0;
							position: absolute;
							background: $brand-black-lighter;
							border: none;
							z-index: 10000;
							top: 100% /* align the dropdown right below the dropdown text */;
							left: -1px /* align the dropdown to the left */;
							right: -1px /* align the dropdown to the right */;
							opacity: 0 /* hide the dropdown */;
							-webkit-transition: opacity 0.4s ease-in-out;
							-moz-transition: opacity 0.4s ease-in-out;
							-o-transition: opacity 0.4s ease-in-out;
							-ms-transition: opacity 0.4s ease-in-out;
							transition: opacity 0.4s ease-in-out;
							pointer-events: none /* avoid mouse click events inside the dropdown */;
							&:hover {
								cursor: pointer; }
							li {
								position: relative;
								label {
									display: block;
									padding: 11px 11px 11px 22px;
									margin: 0;
									border-bottom: 1px solid $brand-lt-grey; }
								&:last-child {
									label {
										border-bottom: none; } }
								input[type=checkbox] {
									display: none; } } }

						&.is-active .checkbox-dropdown-list {
							opacity: 1 /* display the dropdown */;
							pointer-events: auto /* make sure that the user still can select checkboxes */;
							background: $brand-black-lighter;
							z-index: 10000;
							li {
								position: relative;
								input[type=checkbox] {
									display:none {} }
								label {
									display: block;
									padding: 11px 11px 11px 22px;
									margin: 0;
									border-bottom: 1px solid $brand-lt-grey;
									transition: all 0.2s ease-out;
									&::after {
										font-family: FontAwesome;
										content: '\f111';
										font-size: 22px;
										position: absolute;
										top: 6px;
										right: 22px;
										color: $brand-dk-grey;
										@include font-smoothing; }
									&:hover {
										background: $brand-blue;
										color: $brand-black;
										cursor: pointer;
										&::after {
											font-family: FontAwesome;
											content: '\f058';
											font-size: 22px;
											position: absolute;
											top: 6px;
											right: 22px;
											color: $brand-white;
											@include font-smoothing; } } }
								&:last-child {
									label {
										border-bottom: none; } }
								input[type=checkbox] + label {
									background: $brand-black-lighter;
									color: $brand-white;
									&::after {
										font-family: FontAwesome;
										content: '\f111';
										font-size: 22px;
										position: absolute;
										top: 6px;
										right: 22px;
										color: $brand-dk-grey;
										@include font-smoothing; } }
								input[type=checkbox] + label:hover {
									background: $brand-mid-blue;
									color: $brand-white;
									&::after {
										font-family: FontAwesome;
										content: '\f111';
										font-size: 22px;
										position: absolute;
										top: 6px;
										right: 22px;
										color: $brand-dk-grey;
										@include font-smoothing; } }
								input[type=checkbox]:checked + label {
									background: $brand-blue;
									color: $brand-white;
									&::after {
										font-family: FontAwesome;
										content: '\f058';
										font-size: 22px;
										position: absolute;
										top: 6px;
										right: 22px;
										color: $brand-white;
										@include font-smoothing; } }
								input[type=checkbox]:checked + label:hover {
									background: $brand-mid-blue;
									color: $brand-white;
									&::after {
										font-family: FontAwesome;
										content: '\f058';
										font-size: 22px;
										position: absolute;
										top: 6px;
										right: 22px;
										color: $brand-white;
										@include font-smoothing; } } } } } } } }

		.events-listing {
			margin-top: 33px;
			.contents {
				@extend .col-lg-offset-1;
				@extend .col-lg-10; }
			.events-list {
