					&.webinars {
						&::before {
							content: '\e6f0'; } }
					&.events {
						&::before {
							content: '\e789'; } } }
				.meta {
					font-size: 12px;
					font-weight: 600;
					text-transform: uppercase;
					color: $brand-grey;
					margin-top: 22px;
					text-align: left; }
				.locations,
				.type {
					color: $brand-orange;
					padding-right: 7px; }
				.type.webinars {
					color: $brand-orange;
					padding-right: 7px; }
				.type.events {
					color: $brand-blue;
					padding-right: 7px; }
				.event-location {
					padding-right: 7px;
					&::before {
						content: '|';
