				h4 {
					text-align: left;
					margin-bottom: 22px;
					a {
						padding-left: 4px;
						color: $brand-blue;
						&:hover {
							color: $brand-mid-blue; } } } }
			@include tablet {
				.events-list {
					min-height: 360px;
					img {
						max-height: 191px !important;
						height: 191px !important; } } }
			@include desktop {
				.events-list {
					min-height: 330px;
					img {
						max-height: 246px !important;
						height: 246px !important; } } } } }

	.past-webinars {
