.seller-experience {

	.contents {
		padding: 0 22px; }
	[v-cloak] {
		display: none; }
	@include phone {
		.container {
			width: 320px; } }
	.hero {
		position: relative;
		@include flex();
		@include flex-align(center);
		h1 {
			color: #1e1e1e;
			padding-top: 0px; }
		p {
			color: #646464; }
		.lead {
			font-weight: 300;
			color: #282828; }
		.white-card {
			background: #ffffff; }
		.btn {
			border-radius: 22px;
			background-color: rgb(255, 82, 0);
			color: #ffffff;
			font-weight: 600;
			transition: all 0.3s ease 0s;
			&:hover {
				background-color: #FF9002!important; } }
		.section-view {}
		.icon-down-arrow {
			font-size: 16px;
			font-family: $font-family-sans-serif;
			color: $brand-white;
			font-weight: 300;
			position: absolute;
			bottom: 30px;
			left: 0;
			right: 0;
			margin: 0 auto;
			text-align: center;
			width: 240px;
			height: 40px;
			cursor: pointer;
			&::after {
				content: '';
				background: url(../img/mainv2/down-arrow.png) no-repeat bottom center;
				width: 35px;
				height: 16px;
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				margin: 0 auto; }

			@include animation(bouncy 6s infinite);
			@include keyframes(bouncy) {
				0%, 20%, 50%, 80%, 100% {
					@include transform(translateY(0)); }
				40% {
					@include transform(translateY(-22px)); }
				60% {
					@include transform(translateY(-11px)); } } }
		@include phone {
			background: #212d37 url(../img/seller-experience/hero-xs.png) no-repeat center center;
			background-size: cover;
			height: 500px;
			margin-top: 66px;
			h1 {
				font-size: 24px;
				line-height: 30px; }
			p {
				line-height: 20px;
				margin-top: 0px;
				font-size: 14px; }
			.lead {
				margin: 11px 0;
				line-height: 24px;
				font-size: 18px; }
			.white-card {
				width: 290px;
				margin-top: 0px;
				padding: 22px 18px; }
			.btn {
				padding: 8px 22px; } }
		@include tablet {
			background: #212d37 url(../img/seller-experience/hero.jpg) no-repeat center center;
			background-size: cover;
			height: 720px;
			margin-top: 66px;
			p {
				margin: 5px 0; }
			.btn {
				padding: 8px 25px;
				margin-top: 11px;
				font-size: 18px; }
			h1 {
				font-size: 34px;
				line-height: 40px;
				font-weight: 700; }
			p {
				font-size: 18px;
				line-height: 26px; }
			.lead {
				font-size: 24px;
				line-height: 36px; }
			.white-card {
				width: 468px;
				overflow: hidden;
				padding: 40px; } }
		@include desktop {
			background: #212d37 url(../img/seller-experience/hero.jpg) no-repeat center center;
			background-size: cover; } }
	.section-2 {
		background: #f2f2f2;
		h2,
		h3 {
			color: #282828;
			font-weight: 300;
			text-align: center; }
		h3 {
			margin-bottom: 22px; }
		.rectangle {
			display: block;
			margin: auto;
			clear: both;
			&.green {
				background-image: linear-gradient( 180deg, rgb(48,117,11) 0%, rgb(79,198,16) 100%); }
			&.purple {
				background-image: linear-gradient( 180deg, rgb(98,2,166) 0%, rgb(166,2,97) 100%); }
			&.lightBlue {
				background-image: linear-gradient( 180deg, rgb(0,78,255) 0%, rgb(0,176,255) 100%); } }

		.lead {
			color: #282828;
			font-weight: 400; }
		.invoicing {
			.center-image-fixed {
				display: flex;
				justify-content: center; }
			.sellers-benefits {
				@include make-md-column-offset(1); }
			.benefit-item {
				@include make-sm-column(4);
				@include make-xs-column(12);
				margin-top: 22px;
				&:nth-child(4) {
					clear: left; }
				&:nth-child(5) {
					width: 388px !important; }
				&::before {
					content: "";
					width: 71px;
					height: 4px;
					display: block;
					margin-bottom: 11px;
					background-image: -moz-linear-gradient( 180deg, rgb(0,0,0) 0%, rgb(48,117,11) 0%, rgb(64,158,14) 27%, rgb(79,198,16) 100%);
					background-image: -webkit-linear-gradient( 180deg, rgb(0,0,0) 0%, rgb(48,117,11) 0%, rgb(64,158,14) 27%, rgb(79,198,16) 100%);
					background-image: -ms-linear-gradient( 180deg, rgb(0,0,0) 0%, rgb(48,117,11) 0%, rgb(64,158,14) 27%, rgb(79,198,16) 100%); }
				.benefit {
					font-weight: 700;
					font-size: 18px;
					line-height: 26px;
					margin: 0; }
				.description {
					font-size: 18px;
					line-height: 26px;
					font-weight: 400;
					margin-top: 0px; } } }
		.electronic-invoicing {
			padding-top: 110px;
			clear: both; }
		@include phone {
			padding-top: 44px;
			h2 {
				text-align: left;
				padding-right: 22px; }
			h3 {
				font-size: 20px;
				line-height: 28px;
				text-align: left; }
			.lead {
				font-size: 14px;
				line-height: 20px;
				font-weight: 400;
				padding-right: 22px;
				margin-bottom: 0px;
				span {
					font-weight: 700; } }
			.rectangle {
				height: 4px;
				margin: 0 0 11px;
				width: 261px;
				&.lightBlue {
					margin: auto auto 11px; } }
			.invoicing {
				.benefit-item {
					padding-left: 0px !important;
					&:nth-child(5) {
						width: 100% !important; } } }
			.electronic-invoicing {
				padding-top: 88px; }
			.panel .cta {
				width: auto;
				margin: auto;
				display: table!important; }
			.benefit,
			.description {
				font-size: 14px !important;
				line-height: 20px !important;
				margin-bottom: 0; }
			.plans-pricing {
				padding-top: 110px;
				h3 {
					text-align: center;
					font-size: 26px;
					line-height: 33px; } } }
		@include tablet {
			padding-top: 66px;
			padding: 44px 0;
			.rectangle {
				width: 501px;
				height: 4px;
				margin-bottom: 15px; }
			.lead {
				font-size: 18px;
				line-height: 26px;
				width: auto;
				margin: auto;
				text-align: center;
				span {
					font-weight: 700; } }
			.center-image-fixed {
				img {
					margin-left: 40%;
					margin-top: 33px; } }
			.plans-pricing {
				padding-top: 66px; }
			.plan-name {
				height: 50px; } }
		@include desktop {
			padding: 88px 0;
			h2 {
				font-size: 44px;
				line-height: 55px;
				margin-bottom: 18px; }
			h3 {
				font-size: 34px;
				line-height: 44px; }
			.lead {
				width: 750px; }
			.center-image-fixed {
				width: 100%; }
			.col-12 {
				font-size: 12px; }
			.electronic-invoicing {
				padding-top: 121px;
				.integration {
					margin: 44px auto auto;
					display: block; } }
			.plans-pricing {
				h3, .rectangle {
					margin: auto; }
				.cta {
					padding: 0 22px; } }
			.plan-name {
				height: 80px; } }
		@include desktop-xl {
			.center-image-fixed {
				width: 100%;
				img {
					margin-left: 30%; } }
			.benefit-item {
				width: 290px !important;
				margin-right: 50px; }
			.col-12 {
				font-size: 14px; }
			.cta {
				padding: 0 28px; } } }
	.section-3 {
		background: #f2f2f2;
		h2 {
			text-align: center; }
		.container {
			background: #ffffff;
			border-radius: 8px;
			padding-bottom: 22px; }
		.plans-pricing {
			.rootTable {
				@include make-xs-column(12);
				padding-left: 0px;
				padding-right: 0px;
				margin-top: 44px; }
			.flex {
				@include flex; }
			.col-2 {
				width: 15%;
				display: flex; }
			.col-4 {
				width: 25%;
				display: flex; }
			.col-12 {
				width: 100%; }
			.column {
				color: #000000;
				line-height: 2.5;
				font-weight: 600; }
			.plan-name {
				background-color: rgb(30, 30, 30);
				border-radius: 8px 8px 0 0;
				font-size: 18px;
				line-height: 26px;
				&.free {
					color: #e2f6ff; }
				&.silver {
					color: #bce8fc; }
				&.gold {
					color: #75d3fd; }
				&.platinum {
					color: #34bffe; }
				&.platinum-x {
					color: #00b0ff; } }
			.col-free {
				background-color: rgb(226, 246, 255); }
			.col-silver {
				background-color: #bce8fc; }
			.col-gold {
				background-color: #75d3fd; }
			.col-platinum {
				background-color: #34bffe; }
			.col-platinum-x {
				background-color: #00b0ff; }
			.price {
				font-size: 34px;
				font-weight: 300; }
			.periodic {
				font-weight: 300;
				line-height: 1.5; }
			.cta {
				background-color: #ff5200;
				border-radius: 22px;
				color: #ffffff;
				transition: all 0.3s ease 0s;
				cursor: pointer;
				&:hover {
					background-color: #FF9002!important; } }
			.fa {
				font-size: 20px;
				&-check-circle:before {
					color: #004ee2; } }
			.pricing {
				height: 200px; }
			.is-row {
				&.badges {
					padding-bottom: 22px; }
				.column {
					@include flex;
					@include flex-align(center);
					@include flex-justify(center);
					flex-direction: column;
					margin-left: 1%;
					text-align: center;
					&.no-margin {
						margin-left: 0;
						background-color: rgb(244, 252, 255); }
					&.text-left {
						align-items: baseline;
						color: #282828;
						font-weight: 400; }
					&.heading-is-blue {
						color: #00b0ff;
						font-weight: 600;
						font-size: 14px;
						padding-top: 11px; }
					&.rounded-bottom {
						border-radius: 0 0 8px 8px;
						padding-bottom: 22px;
						display: block; }
					&.additional-rates {
						background: none !important;
						font-size: 11px;
						line-height: 20px;
						color: #3c3c3c;
						font-weight: 400;
						display: block;
						text-align: left;
						padding-top: 11px;
						&:first-of-type {
							width: 25%;
							padding-left: 11px; }
						&:nth-of-type(2) {
							width: 30%; }
						&:last-of-type {
							width: 45%; } } } }
			.plans .col-4.column.no-margin {
				border-radius: 8px 8px 0 0; }
			.col-4.column.no-margin.no-badge {
				background: none !important; }
			.parent-row:hover > .col-2 {
				opacity: 0.65; }
			.col-12:hover .col-4:not(.heading-is-blue) {
				color: gray; }

			.heading {
				padding-left: 11px;
				position: relative;

				&.underline {
					&::after {
						content: '';
						border-bottom: dotted 1px #00b0ff;
						position: absolute;
						width: calc(100% - 11px);
						bottom: 5px;
						left: 11px; } }

				&[data-toggle="tooltip"] {
					cursor: pointer; } }
			.tooltip-inner {
				padding: 12px;
				text-align: left; } }
		.accordion {}
		.panel-body {
			padding-top: 0px;
			padding-bottom: 22px; }
		.panel-group .panel {
			margin-bottom: 11px;
			border: none;
			ul {
				padding-left: 22px;
				padding-bottom: 18px; }
			.cta {
				text-align: center;
				height: auto;
				padding: 8px 55px;
				display: inline; } }
		.accordion-preview {
			position: relative;
			height: 66px;
			padding: 0 22px;
			background: #f2f2f2;
			.price {
				min-height: 85px;
				font-size: 24px !important;
				line-height: 28px !important; }
			.expand {
				position: absolute;
				right: 22px;
				bottom: 11px; }
			.name-secondary {
				padding-top: 11px; }
			.fa.fa-angle-down {
				font-size: 27px;
				font-weight: 600;
				color: #ff5200;
				transition: all 1s ease; }
			a {
				transition: all 0.5s ease; }
			a[aria-expanded="true"] {
				transform: rotate(180deg); }
			a[aria-expanded="false"] {
				transform: rotate(0deg); } }
		.panel-content {
			background: #f2f2f2 !important; }
		@include phone {
			padding-top: 44px;
			.invoicing {
				.benefit-item {
					padding-left: 0px !important;
					&:nth-child(5) {
						width: 100% !important; } } }
			.panel .cta {
				width: auto;
				margin: auto;
				display: table!important; }
			.benefit,
			.description {
				font-size: 14px !important;
				line-height: 20px !important;
				margin-bottom: 0; }
			.plans-pricing {
				padding-top: 77px;
				h2 {
					padding-bottom: 22px; }
				h3 {
					text-align: center;
					font-size: 26px;
					line-height: 33px; } } }
		@include tablet {
			padding-top: 66px;
			padding: 44px 0;
			.lead {
				font-size: 18px;
				line-height: 26px;
				width: auto;
				margin: auto;
				text-align: center;
				span {
					font-weight: 700; } }
			.plans-pricing {
				padding-top: 66px; }
			.plan-name {
				height: 50px; } }
		@include desktop {
			padding: 88px 0;
			.lead {
				width: 750px; }
			.col-12 {
				font-size: 12px; }
			.plans-pricing {
				h3, .rectangle {
					margin: auto; }
				.cta {
					padding: 0 22px; } }
			.plan-name {
				height: 80px; } }
		@include desktop-xl {
			.col-12 {
				font-size: 14px; }
			.cta {
				padding: 0 28px; } } }
	.prefooter {
		background-image: linear-gradient( 120deg, rgb(255,138,0) 0%, rgb(255,193,7) 100%);
		height: 374px;
		display: flex;
		align-items: center;
		text-align: center;

		h4 {
			font-size: 44px;
			line-height: 55px;
			color: #ffffff;
			font-weight: 300; }
		.btn-cta {
			border-radius: 22px;
			background-color: #ffffff;
			color: #ff5200;
			padding: 8px 55px;
			font-size: 18px;
			margin-top: 22px; }
		@include phone {
			h4 {
				font-size: 34px;
				line-height: 44px; }
			.btn-cta {
				font-size: 16px;
				line-height: 26px;
				font-weight: 600; } } } }




