// Custom Styling for Resources Page
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.resources-page {
	.hero.learn-more {
		padding: 33px 0 0;
		background: $brand-white;
		text-align: center;
		.contents {
			@extend .col-xs-12;
			@extend .col-md-offset-2;
			@extend .col-md-8;
			@extend .col-lg-offset-2;
			@extend .col-lg-8; }
		h1 {
			color: $brand-blue;
			font-size: 28px;
			line-height: 33px;
			padding: 0 11px; }

		h3 {
			color: $brand-black;
			margin-top: 22px;
			font-size: 18px;
			line-height: 22px;
			padding: 0 11px;
			font-weight: 300; }
		hr {
			margin-top: 33px; }
		@include tablet {
			padding: 66px 0 0;
			h1 {
				font-size: 48px;
				line-height: 55px;
				margin-bottom: 33px; }
			h3 {
				font-size: 26px;
				line-height: 33px; }
			hr {
				margin-top: 66px; } } }

	.featured-resource {
		padding: 33px 0 0;
		text-align: center;
		.contents {
			@extend .col-xs-12; }
		.featured-image {
			@extend .col-xs-12;
			@extend .col-sm-7;
			@extend .col-md-7;
			@extend .col-lg-offset-1;
			@extend .col-lg-6; }
		.featured-info {
			@extend .col-xs-12;
			@extend .col-sm-5;
			@extend .col-md-5;
			@extend .col-lg-4;
			.meta {
				font-size: 12px;
				font-weight: 600;
				text-transform: uppercase;
				color: $brand-grey;
				margin-top: 33px;
				text-align: left; }
			.media-type {
				color: $brand-orange;
				padding-right: 7px; }
			.date-posted {
				&::before {
					content: '|';
					padding-right: 7px; } } }
		h2 {
			margin-bottom: 33px;
			text-align: center; }
		h4 {
			text-align: left; }
		img {
			@extend .img-responsive; }
		p {
			text-align: left;
			max-height: 66px;
			overflow: hidden;
			margin-bottom: 22px; }
		hr {
			margin-top: 33px; }
		.featured-image {
			&.analyst-reports,
			&.case-study,
			&.ebook,
			&.infographic,
			&.partner-brief,
			&.product-brief,
			&.solution-overview,
			&.video,
			&.webinar,
			&.whitepapers {
				position: relative;
				&::before {
					font-family: LinearIcons;
					position: absolute;
					bottom: 16px;
					right: 31px;
					font-size: 46px;
					color: $brand-white;
					line-height: 1;
					@include font-smoothing; } }
			&.analyst-reports {
				&::before {
					content: '\e7fb'; } }
			&.case-study {
				&::before {
					content: '\e911'; } }
			&.ebook {
				&::before {
					content: '\e715'; } }
			&.infographic {
				&::before {
					content: '\e7f8'; } }
			&.partner-brief {
				&::before {
					content: '\e913'; } }
			&.product-brief {
				&::before {
					content: '\e90c'; } }
			&.solution-overview {
				&::before {
					content: '\e6d4'; } }
			&.video {
				&::before {
					content: '\e96a'; } }
			&.webinar {
				&::before {
					content: '\e6f0'; } }
			&.whitepapers {
				&::before {
					content: '\e61b'; } } }

		@include tablet {
			padding: 44px 0 0;
			text-align: left;
			h3 {
				text-align: left;
				color: $brand-mid-blue; }
			h4 {
				font-size: 22px;
				line-height: 28px; }
			p {
				font-size: 14px;
				line-height: 22px; }
			hr.dashed {
