			.featured-info {
				.meta {
					margin-top: 0; } } }
		@include desktop {
			padding: 55px 0 0;
			.featured-info {
				.meta {
					margin-top: 22px; } }
			h4 {
				font-size: 24px;
				line-height: 33px; }
