			.contents {
				@extend .col-lg-offset-1;
				@extend .col-lg-10;
				.search {
					@extend .col-xs-12;
					@extend .col-sm-4;
					margin-bottom: 22px; }
				.filter {
					@extend .col-xs-12;
					@extend .col-sm-8;
					.option-set.by-roles,
					.option-set.by-media-type {
						@extend .col-xs-12;
						@extend .col-sm-6; }
					.option-set.by-roles {
						margin-bottom: 22px; } }

				.search {
					.input-group {
						input[type=text] {
							background: $brand-black-lighter;
							padding: 0 22px;
							border: none;
							font-size: 14px !important;
							height: 44px;
							border-radius: 0;
							width: 100%;
							color: $brand-white !important;
							box-shadow: inset 0 10px 5px -10px rgba(0,0,0,0.1);
							&:focus,
							&:active {
								outline: none; } }
						.input-group-addon {
							border-radius: 0;
							border: none;
							vertical-align: middle;
							padding: 0;
							input[type=submit] {
								font-family: FontAwesome;
								padding: 11px;
								color: #7B7B7B !important;
								font-size: 22px;
								background: #3C3C3C;
								box-shadow: inset 0 10px 5px -10px rgba(0,0,0,0.1);
								border-radius: 0;
								border: none;
								&:hover {
									background: #D2D2D2; }
								&:active {
									background: #B8B8B8; } } } } }

				.filter {
					.option-set {
						width: 100%; }
					.option-set.by-roles {
						z-index: 900; }
					.option-set.by-media-type {
						z-index: 800; }
					.checkbox-dropdown {
						width: 100%;
						background: $brand-black-lighter;
						border: 1px solid $brand-black-lighter;
						cursor: pointer;
						padding: 11px 22px;
						position: relative;
						text-align: left;
						color: $brand-white;
						z-index: 1000;
						-webkit-touch-callout: none;
						-webkit-user-select: none;
						-khtml-user-select: none;
						-moz-user-select: none;
						-ms-user-select: none;
						user-select: none;
						&:hover {
							cursor: pointer; }

						/* Display CSS arrow to the right of the dropdown text */
						&:after {
							content: "";
							height: 0;
							position: absolute;
							width: 0;
							border: 6px solid transparent;
							border-top-color: $brand-white;
							top: 50%;
							right: 22px;
							margin-top: -3px; }

						/* Reverse the CSS arrow when the dropdown is active */
						&.is-active:after {
							border-bottom-color: $brand-black-lighter;
							border-top-color: $brand-white;
							margin-top: -3px; }

						.checkbox-dropdown-list {
							list-style: none;
							color: $brand-white;
							margin: 0;
							padding: 0;
							position: absolute;
							background: $brand-black-lighter;
							border: none;
							z-index: 10000;
							top: 100% /* align the dropdown right below the dropdown text */;
							left: -1px /* align the dropdown to the left */;
							right: -1px /* align the dropdown to the right */;
							opacity: 0 /* hide the dropdown */;
							-webkit-transition: opacity 0.4s ease-in-out;
							-moz-transition: opacity 0.4s ease-in-out;
							-o-transition: opacity 0.4s ease-in-out;
							-ms-transition: opacity 0.4s ease-in-out;
							transition: opacity 0.4s ease-in-out;
							pointer-events: none /* avoid mouse click events inside the dropdown */;
							&:hover {
								cursor: pointer; }
							li {
								position: relative;
								label {
									display: block;
									padding: 11px 11px 11px 22px;
									margin: 0;
									font-weight: 400;
									border-bottom: 1px solid #7B7B7B; }
								&:last-child {
									label {
										border-bottom: none; } }
								input[type=checkbox] {
									display: none; } } }


						&.is-active .checkbox-dropdown-list {
							opacity: 1 /* display the dropdown */;
							pointer-events: auto /* make sure that the user still can select checkboxes */;
							background: $brand-black-lighter;
							z-index: 10000;
							li {
								position: relative;
								input[type=checkbox] {
									display:none {} }
								label {
									display: block;
									padding: 11px 11px 11px 22px;
									margin: 0;
									font-weight: 400;
									border-bottom: 1px solid #7B7B7B;
									transition: all 0.2s ease-out;
									&::after {
										font-family: FontAwesome;
										content: '\f111';
										font-size: 22px;
										position: absolute;
										top: 6px;
										right: 22px;
										color: $brand-dk-grey;
										@include font-smoothing; }
									&:hover {
										background: $brand-blue;
										color: $brand-black;
										cursor: pointer;
										&::after {
											font-family: FontAwesome;
											content: '\f058';
											font-size: 22px;
											position: absolute;
											top: 6px;
											right: 22px;
											color: $brand-white;
											@include font-smoothing; } } }
								&:last-child {
									label {
										border-bottom: none; } }
								input[type=checkbox] + label {
									background: $brand-black-lighter;
									color: $brand-white;
									&::after {
										font-family: FontAwesome;
										content: '\f111';
										font-size: 22px;
										position: absolute;
										top: 6px;
										right: 22px;
										color: $brand-dk-grey;
										@include font-smoothing; } }
								input[type=checkbox] + label:hover {
									background: $brand-mid-blue;
									color: $brand-white;
									&::after {
										font-family: FontAwesome;
										content: '\f111';
										font-size: 22px;
										position: absolute;
										top: 6px;
										right: 22px;
										color: $brand-dk-grey;
										@include font-smoothing; } }
								input[type=checkbox]:checked + label {
									background: $brand-blue;
									color: $brand-white;
									&::after {
										font-family: FontAwesome;
										content: '\f058';
										font-size: 22px;
										position: absolute;
										top: 6px;
										right: 22px;
										color: $brand-white;
										@include font-smoothing; } }
								input[type=checkbox]:checked + label:hover {
									background: $brand-mid-blue;
									color: $brand-white;
									&::after {
										font-family: FontAwesome;
										content: '\f058';
