// Custom Styling for About Page - En
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.about-page {
	.hero.story-intro {
		background: $brand-blue url('../img/about-ts/hero-bg-800.jpg') no-repeat center top;
		height: 383px;
		position: relative;
		.contents {
			@extend .col-xs-9;
			@extend .col-md-12;
			@extend .col-lg-offset-1;
			@extend .col-lg-10; }
		h1 {
			margin-top: 231px;
			margin-bottom: 11px;
			color: $brand-white;
			font-weight: 200; }
		h3 {
			color: $brand-white; }
		.arrow {
			position: absolute;
			bottom: 0;
			width: 100%;
			height: 0;
			background-color: $brand-blue; }

		.arrow:before,
		.arrow:after {
			content: '';
			position: absolute;
			bottom: 100%;
			width: 50%;
			box-sizing: border-box; }

		.arrow:before {
			right: 50%;
			border-bottom: 16px solid $brand-blue;
			border-right: 14px solid transparent; }

		.arrow:after {
			left: 50%;
			border-bottom: 16px solid $brand-blue;
			border-left: 14px solid transparent; }
		@include tablet {
			background: $brand-blue url('../img/about-ts/hero-bg-tablet.jpg') no-repeat center top;
			background-size: 1040px;
			height: 498px;
			h3 {
				font-weight: 300;
				width: 55%; } }

		@include desktop {
			background: $brand-blue url('../img/about-ts/hero-bg.jpg') no-repeat center top;
			background-size: 1920px;
			height: 550px;
			h1 {
				margin-top: 132px; }
			h3 {
				font-size: 34px;
				line-height: 44px;
				color: $brand-white;
				width: 55%; }
			blockquote {
				border-left: none;
				color: $brand-white;
				margin: 88px 0 0 0;
				padding: 0;
				p {
					font-size: 26px;
					line-height: 33px;
					font-weight: 300;
					margin: 0; }
				p + p {
					margin-top: 0; }
				small {
					color: $brand-white;
					font-size: 18px;
					line-height: 33px; } } }
		@include desktop-xl {
			background: $brand-blue url('../img/about-ts/hero-bg.jpg') no-repeat center top;
			background-size: cover;
			h3 {
				width: 44%; } } }

	.story-rotating-quotes {
		padding: 33px 0 22px;
		position: relative;
		background: $brand-blue;
		h2 {
			color: $brand-white; }
		p {
			color: $brand-white; }
		small {
			color: $brand-white; }

		.story {
			@extend .col-xs-12;
			@extend .col-sm-7;
			@extend .col-md-6;
			@extend .col-lg-offset-1;
			@extend .col-lg-5;
			p {
				font-weight: 300; }
			@include tablet {
				p {
					line-height: 22px; } } }
		.owl-carousel {
			@extend .col-xs-12;
			@extend .col-sm-5;
			@extend .col-md-6;
			@extend .col-lg-5;
			.quote-wrapper {
				width: 100%;
				padding: 33px 22px;
				text-align: center;
				.founder {
					border-radius: 50%;
					margin-bottom: 66px; }
				.person-title {
					color: $brand-white;
					font-weight: 300;
					font-size: 14px;
					margin-bottom: 11px; }
				.quote {
					color: $brand-sky-blue;
					font-weight: 300;
					font-size: 14px;
					line-height: 22px;
					position: relative; } } }

		.owl-wrapper-outer {
			z-index: 900 !important; }
		.owl-page {
			span {
				background: $brand-white !important; } }
		.owl-controls {
			position: absolute !important;
			top: 244px !important;
			right: 0 !important;
			left: 0 !important;
			z-index: 1000 !important; }

		@include tablet {
			padding: 44px 0;
			.owl-carousel {
				.quote-wrapper {
					padding: 11px 22px;
					.quote-mark {
						display: block;
						margin: 22px auto; }
					.person-title {
						font-size: 18px;
						margin-bottom: 11px;
						line-height: 22px; }
					.quote {
						font-size: 18px;
						line-height: 26px;
						&::before,
						&::after {
							content: '';
							background: none;
							display: none; } } } }
			.owl-controls {
				top: 211px !important; } }
		@include desktop {
			.owl-carousel {
				.quote-wrapper {
					padding: 11px 44px; } } } }

	.bigger-team {
		padding: 0;
		position: relative;
		.contents {
			@extend .col-xs-12;
			padding: 0; }
		.same-vision {
			width: 100%;
			background: $brand-white; }
		h2 {
			text-align: center;
			color: $brand-blue;
			margin-top: 33px;
			margin-bottom: 22px; }
		h5 {
			font-size: 14px;
			line-height: 14px;
			font-weight: 700;
			margin: 22px 22px 11px; }
		p {
			color: $brand-grey;
			padding: 0 22px; }
		ul {
			@extend .list-unstyled;
			padding: 0 22px 33px;
			li {
				color: $brand-grey;
				display: table-row;
				&::before {
					content: '\2013';
					padding-right: 5px;
					display: table-cell; } } }
		&::after {
			content: ' ';
			text-align: center;
			margin: 0 auto;
			width: 26px;
			height: 16px;
			border-style: solid;
			border-width: 16px 13px 0 13px;
			border-color: $brand-blue transparent transparent transparent;
			position: absolute;
			top: 0;
			left: 0;
			right: 0; }

		@include tablet {
			padding: 66px 0;
			background: $brand-blue url('../img/about-ts/bigger-team-bg.jpg') no-repeat center top;
			background-size: cover;
			.contents {
				padding: 33px 66px; }
			.same-vision {
				padding: 33px 66px; }
			h2 {
				text-align: center;
				margin-bottom: 0;
				margin-top: 0; }
			h5 {
				margin: 0 0 11px; }
			p {
				padding: 0;
				font-size: 14px;
				line-height: 22px;
				margin-bottom: 33px; }
			ul {
				padding: 0;
				li {
					display: inline-table;
					width: 49%;
					margin-bottom: 11px;
					padding-right: 33px; } } }

		@include desktop {
			padding: 132px 0;
			.contents {
				padding: 0;
				.same-vision {
					padding: 33px 66px;
					margin: 0 auto; } }
			h2 {
				margin-top: 0; }
			ul {
				padding: 0 22px 0; } }

		@include desktop-xl {
			.contents {
				padding: 0 164px; } } }

	.brief-review {
		padding: 44px 0;
		position: relative;
		.timeline-contents {
			@extend .col-xs-12; }
		.timeline-wrapper {
			margin: 330px 0 132px;
			text-align: justify;
			background: url('../img/about-ts/circle-bg.png') repeat-x 50% 45%;
			position: relative;
			&::after {
				content: '';
				width: 100%;
				display: inline-block; }
			.table {
				th {
					font-size: 18px;
					font-weight: 300;
					border: none;
					padding: 0; }
				th.y2005,
				th.y2007 {
					color: #00b0ff; }
				th.y2009,
				th.y2010 {
					color: #4fc610; }
				th.y2011,
				th.y2012 {
					color: #c70013; }
				th.y2013 {
					color: #ff5122; }
				th.y2014 {
					color: #fcbf12; }
				th.y2015 {
					color: #18648f; }
				th.y2016 {
					color: #4fc610; }
				th.y2017 {
					color: #a60261; }
				th.y2018 {
					color: #00b0ff; }
				td {
					border: none; } }
			.btn-timeline {
				display: inline-block; }
			.popover {
				border-radius: 0;
				padding: 11px;
				max-width: 450px;
				box-shadow: none;
				@include transition(opacity .5s ease-in-out);
				&.top {
					margin-top: -44px; }
				&.bottom {
					margin-top: 22px; } }
			.popover-title {
				background: transparent;
				border-radius: 0;
				border-bottom: none;
				font-size: 18px;
				padding: 0 0 0 11px;
				font-family: $font-family-sans-serif; }
			.popover-content {
				font-family: $font-family-sans-serif; }
			.popover.blue {
				border: 1px solid #00b0ff;
				.popover-title {
					color: #00b0ff; }
				&.top {
					.arrow {
						border-top-color: #00b0ff; } }
				&.bottom {
					.arrow {
						border-bottom-color: #00b0ff; } } }
			.popover.green {
				border: 1px solid #4fc610;
				.popover-title {
					color: #4fc610; }
				&.top {
					.arrow {
						border-top-color: #4fc610; } }
				&.bottom {
					.arrow {
						border-bottom-color: #4fc610; } } }
			.popover.red {
				border: 1px solid #c70013;
				.popover-title {
					color: #c70013; }
				&.top {
					.arrow {
						border-top-color: #c70013; } }
				&.bottom {
					.arrow {
						border-bottom-color: #c70013; } } }
			.popover.orange {
				border: 1px solid #ff5122;
				.popover-title {
					color: #ff5122; }
				&.top {
					.arrow {
						border-top-color: #ff5122; } }
				&.bottom {
					.arrow {
						border-bottom-color: #ff5122; } } }
			.popover.gold {
				border: 1px solid #18648f;
				.popover-title {
					color: #18648f; }
				&.top {
					.arrow {
						border-top-color: #18648f; } }
				&.bottom {
					.arrow {
						border-bottom-color: #18648f; } } }
			.popover.blue.2018 {
				min-width: 300px !important;
				border: 1px solid #00b0ff;
				&.top {
					.arrow {
						border-top-color: #00b0ff; } }
				&.bottom {
					.arrow {
						border-bottom-color: #00b0ff; } } }
			.btn-yellow {
				background-color: #fcbf12; } } }
	hr {
		@extend .container;
		padding-left: 15px;
		padding-right: 15px; }
	.awards {
		padding: 44px 0;
		background: $brand-white;
		.contents {
			@extend .col-xs-12; }
		h2 {
			text-align: center;
			margin-bottom: 22px; }
		dl {
			text-align: center;
			dd {
				margin-bottom: 22px; } }
		@include tablet {
			background: url('../img/about-ts/awards-bg.gif') no-repeat center 50%;
			background-size: 90%; }
		@include desktop {
			background-size: 880px; } }

	.executives {
		padding: 44px 0;
		background: $brand-blue;
		.contents {
			@extend .col-xs-12; }
		.role {
			margin-bottom: 22px;
			display: inline-block;
			@extend .col-xs-12;
			@extend .col-sm-6;
			@extend .col-md-4;
			padding: 0;
			img {
				width: 100%; }
			.person {
				text-align: center;
				padding: 11px 0;
				background: $brand-white;
				.name {
					font-weight: 600; } } }
		h2 {
			color: $brand-white;
			text-align: center;
			margin-bottom: 22px; }
		@include tablet {
			.role {
				padding: 11px;
				margin-bottom: 0; } }
		@include desktop {
			.role {
				padding: 0;
				margin: 0;
				position: relative;
				.person {
					padding: 33px;
					position: absolute;
					top: 0;
					width: 100%;
					height: 100%;
					background: rgba(0,0,0,.5);
					opacity: 0;
					&:hover {
						opacity: 1;
						@include transition(opacity .5s ease-in-out); }
					.name span {
						color: $brand-white;
						font-size: 26px;
						font-weight: 400;
						border-bottom: 1px solid $brand-white;
						padding-bottom: 6px; }
					.jobtitle {
						color: $brand-white;
						font-size: 14px;
						text-transform: uppercase;
						margin-top: 11px;
						font-weight: 400; } } } } }
	.board {
		padding: 44px 0;
		background: #f2f2f2;
		position: relative;
		text-align: center;
		h2 {
			margin-bottom: 22px; }
		.contents {
			@extend .col-xs-12; }
		.board-member {
			@extend .col-xs-12;
			@extend .col-sm-6;
			@extend .col-md-4;
			margin: 0 auto 33px; }
		.name {
			font-size: 18px; }
		.title-affiliation {
			padding-top: 6px;
			&::before {
				content: '';
				border-bottom: 1px solid $brand-grey;
				height: 2px;
				width: 44%;
				display: block;
				margin: 0 auto 6px; } }

		&::after {
			content: ' ';
			text-align: center;
			margin: 0 auto;
			width: 26px;
			height: 16px;
			border-style: solid;
			border-width: 16px 13px 0 13px;
			border-color: $brand-blue transparent transparent transparent;
			position: absolute;
			top: 0;
			left: 0;
			right: 0; } }

	.investors {
		padding: 44px 0;
		background: $brand-white;
		position: relative;
		text-align: center;
		.contents {
			@extend .col-xs-12; }
		h2 {
			margin-bottom: 55px; }
		.logo {
			@extend .col-xs-12;
			@extend .col-sm-4;
			margin-bottom: 44px; }
		&::after {
			content: ' ';
			text-align: center;
			margin: 0 auto;
			width: 26px;
			height: 16px;
			border-style: solid;
			border-width: 16px 13px 0 13px;
			border-color: #f2f2f2 transparent transparent transparent;
			position: absolute;
			top: 0;
			left: 0;
			right: 0; }
		@include desktop {
			.logo {
				margin-bottom: 66px; } } } }

