// Custom Styling for Tradeshift Pro
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.header-ts-pro {
	.row {
		position: relative; }
	.brand {
		position: absolute;
		z-index: 1010;
		top: 44px;
		left: 15px; } }
.ts-pro {
	z-index: 1000;
	.hero.hello-ts-pro {
		position: relative;
		text-align: center;
		padding: 88px 0;
		background: $brand-navy url('../img/ts-pro/hero-bg-xs.jpg') no-repeat top center;
		.contents {
			@extend .col-xs-12; }
		h1 {
			color: $brand-white;
			font-weight: 600;
			font-size: 34px;
			line-height: 44px;
			em {
				color: $brand-blue;
				font-weight: 800;
				font-style: normal;
				display: block; } }
		h3 {
			color: $brand-white; }
		.btn-play-ts-pro {
			margin: 33px 0;
			background: url('../img/ts-pro/btn-ts-pro.png') no-repeat top center;
			width: 122px;
			height: 122px;
			text-align: center; }
		.hero-lead {
			color: $brand-white;
			text-align: center;
			font-size: 18px;
			line-height: 22px;
			padding: 0;
			margin-bottom: 44px; }
		.btn-primary-cta {
			text-align: center;
			margin: 0 auto; }
		@include tablet {
			padding: 187px 0 352px;
			background: $brand-navy url('../img/ts-pro/hero-bg.jpg') no-repeat top center;
			h1 {
				font-size: 48px;
				margin: 0 auto 22px;
				em {
					display: inline; } }
			h3 {
				width: 77%;
				margin: 0 auto; }
			.hero-lead {
				width: 77%;
				margin: 0 auto;
				font-size: 18px;
				line-height: 22px;
				margin-bottom: 66px; } }

		@include desktop {
			.hero-lead {
				width: 68%; } }
		@include desktop-xl {
			background: $brand-white url('../img/ts-pro/hero-bg.jpg') no-repeat bottom center;
			background-size: cover;
			h3 {
				width: 55%; }
			.hero-lead {
				width: 55%; } } }

	.value {
		padding: 66px 0 11px;
		position: relative;
		text-align: center;
		.get-rid-paper,
		.process-faster,
		.easy-use {
			@extend .col-xs-12;
			@extend .col-sm-4;
			position: relative;
			&::before {
				content: ' ';
				font-family: 'LinearIcons';
				font-size: 38px;
				color: $brand-blue;
				position: absolute;
				top: 5px;
				left: 50%;
				margin-left: -19px; }
			&::after {
				content: '';
				width: 66px;
				height: 66px;
				border-radius: 33px;
				border: 2px solid $brand-blue;
				position: absolute;
				top: 0;
				left: 50%;
				margin-left: -33px; } }

		.get-rid-paper {
			&::before {
				content: '\e64a'; } }
		.process-faster {
			&::before {
				content: '\e785'; } }
		.easy-use {
			&::before {
				content: '\e723'; } }

		h4 {
			font-size: 26px;
			line-height: 34px;
			margin-bottom: 11px;
			margin-top: 22px;
			padding: 66px 33px 0; }
		p {
			font-size: 18px;
			line-height: 22px;
			margin-bottom: 55px;
			padding: 0 48px; }
		&::before {
			content: ' ';
			text-align: center;
			margin: 0 auto;
			width: 33px;
			border-style: solid;
			border-width: 22px;
			border-color: $brand-navy transparent transparent transparent;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;

			@include font-smoothing; }
		@include tablet {
			.get-rid-paper {
				h4 {
					padding: 66px 0 0; } }
			.process-faster {
				h4 {
					padding: 66px 22px 0; } }
			.easy-use {
				h4 {
					padding: 66px 0 0; } }
			padding: 11px 0;
			&::before {
				border: none; }

			p {
				padding: 0 22px; } }
		@include desktop {
			.get-rid-paper {
				h4 {
					padding: 66px 33px 0; } }
			.process-faster {
				h4 {
					padding: 66px 66px 0; } }
			.easy-use {
				h4 {
					padding: 66px 33px 0; } }
			p {
				padding: 0 55px; } }
		@include desktop-xl {
			.process-faster {
				h4 {
					padding: 66px 88px 0; } }
			.easy-use {
				h4 {
					padding: 66px 66px 0; } } } }
	.key-features {
		padding: 44px 0 0;
		.contents {
			@extend .col-xs-12; }
		h2 {
			text-align: center;
			margin-bottom: 55px;
			em {
				display: block;
				font-weight: 600; } }
		.pricing-wrapper {
			@extend .col-sm-5;
			@extend .col-lg-5;
			margin: 0 -30px; }
		.pricing {
			width: 100%;
			padding: 33px 0;
			background: #173d54;
			text-align: center;
			em {
				font-style: normal;
				font-weight: 600;
				white-space: nowrap; }
			h4 {
				color: $brand-white;
				font-size: 34px;
				line-height: 48px;
				border-bottom: 1px solid $brand-white;
				font-weight: 700;
				display: inline-block; }
			p {
				color: $brand-white;
				font-size: 18px;
				line-height: 22px;
				padding: 0 55px; }
			.price {
				font-size: 77px;
				line-height: 66px;
				color: $brand-white;
				text-rendering: optimizeLegibility;
				font-weight: 300;
				margin-left: -11px;
				letter-spacing: -.04em; }
			.btn {
				margin: 11px 0 33px; } }
		.feature-list-wrapper {
			@extend .col-sm-7;
			@extend .col-lg-5; }
		.feature-list {
			padding: 66px;
			ul {
				text-align: center;
				@extend .list-unstyled;
				li {
					font-weight: 700;
					font-size: 18px;
					&:first-child {
						&::before {
							content: ''; } }
					&:before {
						display: block;
						content: '+';
						font-size: 44px;
						line-height: 66px;
						font-weight: 300; }
					a {
						color: $brand-black;
						cursor: default; } } } }

		.download-data-sheet {
			padding:  22px 66px;
			margin: 0 -30px;
			background: $brand-blue;
			color: $brand-white;
			text-align: center;
			position: relative;
			a {
				color: $brand-white; }
			em {
				font-style: normal;
				font-weight: 800; }
			&::after {
				font-family: 'LinearIcons';
				content: '\e93c';
				vertical-align: bottom; } }
		@include tablet {
			padding: 55px 0 88px;
			h2 {
				em {
					display: inline-block; } }
			.pricing-wrapper {
				height: 616px;
				margin: 0 -15px 0 auto; }
			.pricing {
				padding: 44px 0 88px;
				width: 98%;
				float: right;
				height: 616px;
				p {
					padding: 0 22px; }
				.intro {
					margin-bottom: 44px; }
				.starting {
					margin: 22px 0 11px; }
				.yearly {
					margin: 11px 0 22px; } }
			.feature-list-wrapper {
				border: 1px solid #173d54;
				height: 616px;
				position: relative; }
			.feature-list {
				width: 95%;
				float: left;
				padding: 33px 22px 0;
				dl {
					text-align: left;
					position: relative;
					dt {
						text-align: left;
						position: relative;
						padding-left: 22px;
						font-size: 18px;
						font-weight: 600;
						transition: transform, .5s;
						&.trigger {
							&::before {
								content: '';
								width: 0;
								height: 0;
								border-top: 7px solid transparent;
								border-left: 10px solid $brand-black;
								border-bottom: 7px solid transparent;
								font-size: 0px;
								display: table-row;
								position: absolute;
								left: 0;
								top: 5px;
								transform: rotate(0deg);
								transform-origin: 50% 50%;
								transition: transform, .5s; } }
						&.trigger.shown {
							&::before {
								content: '';
								width: 0;
								height: 0;
								font-size: 0px;
								transform: rotate(90deg);
								transform-origin: 50% 50%; } } }
					dd {
						&.feature-listing {
							opacity: 0;
							min-height: 66px;
							font-size: 14px;
							line-height: 18px;
							font-weight: 300;
							margin-left: 22px;
							transition: all, .5s ease-in-out; }
						&.shown {
							opacity: 1;
							transition: all, .5s ease-in-out; } } } }

			.download-data-sheet {
				padding: 11px;
				font-size: 14px;
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				margin: 0;
				background: $brand-white;
				color: $brand-blue;
				&::before {
					content: '\e6b3';
					padding-right: 11px;
					font-family: LinearIcons; }
				a {
					color: $brand-blue; } } }

		@include desktop {
			.pricing {
				p {
					padding: 0 33px; } }
			.feature-list {
				padding: 33px 33px 0 44px; } }
		@include desktop-xl {
			.pricing {
				width: 70%; } } }


	.cta-sales {
		padding: 44px 0;
		background: $brand-sky-blue;
		text-align: center;
		position: relative;
		.contents {
			@extend .col-xs-12;
			padding: 0 55px; }
		h2 {
			margin-bottom: 11px;
			padding: 0 22px; }
		&::before {
			content: ' ';
			text-align: center;
			margin: 0 auto;
			width: 33px;
			border-style: solid;
			border-width: 22px;
			border-color: $brand-blue transparent transparent transparent;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;

			@include font-smoothing; }
		@include tablet {
			&::before {
				content: ' ';
				border-color: $brand-white transparent transparent transparent; } } } }



