.onboarding {
    text-align: center;
    background-image: linear-gradient( 0deg, rgb(255,255,255) 0%, rgb(243,243,243) 73%, rgb(231,231,231) 100%);

    a {
        color: white; }

    h1 {
        color: #282828;
        font-weight: 600;

        @include phone {
            min-height: 2.5em; }

        @include tablet {
            min-height: unset; }

        span {
            color: $brand-accent-green; } }

    h2 {
        color: $brand-accent-grayscale-2;
        font-weight: 300;
        padding-top: 22px; }

    h3 {
        color: $brand-accent-grayscale-2;
        font-weight: 300;
        padding-bottom: 10px; }

    p {
        color: #282828;
        font-weight: 300; }

    .rect-green {
        display: block;
        margin: auto;
        height: 4px;
        background-image: linear-gradient( 270deg, rgb(48,117,11) 0%, rgb(79,198,16) 100%); }

    .radius-25 {
        position: relative;
        border-radius: 25px;
        font-size: 18px;
        padding-left: 15px;
        padding-right: 38px;
        margin-right: 15px;
        font-weight: 300;
        margin-bottom: 15px;
        left: 22px; // The width of the search-icon since it is knocking this input off-center
        height: 44px;
        background: white;
        border: none;
        &:focus {
            outline: none; }
        &.large {
            width: 30%; } }

    .search-icon {
        color: $brand-accent-grayscale-1;
        font-size: 24px;
        position: relative;
        right: 28px;
        transition: color 1s ease-in-out;

        &.active {
            color: $brand-p-ent; } }

    .blue-cta {
        padding: 8px 33px;
        color: #ffffff;
        font-weight: 600;
        border-radius: 25px;
        background-color: #00b0ff;

        &:hover {
            background-color: $brand-s-ent; }

        @include tablet {
            display: inline; }

        @include phone {
            width: 100%;
            display: block; } }

    .logos {
        @include desktop {
            padding-top: 11px; } }

    .image-tile {
        background: #ffffff;
        box-shadow: 0px 0px 9px 1px rgba(0, 0, 0, 0.15);
        margin: 20px auto;
        height: 154px;
        width: 170px;
        transition: .15s transform ease-in-out;

        &:hover {
            transform: scale(1.1); } }

    .partner-link {
        display: flex;
        height: 100%;
        align-items: center;
        justify-content: center; }


    @include phone {
        padding: 100px 0;
        h3 {
            font-size: 26px;
            line-height: 33px; }
        p {
            line-height: 20px; }
        .container {
            width: 320px; }
        .rect-green {
            width: 80%;
            margin: 33px auto 11px; } }

    @include tablet {
        padding: 120px 0;
        h1 {
            font-size: 34px;
            line-height: 39px; }
        h2 {
            font-size: 34px;
            line-height: 26px; }
        h3 {
            font-size: 33px;
            line-height: 38px; }
        p {
            font-size: 18px;
            line-height: 26px;
            padding: 22px 0;
            width: 600px;
            margin: auto; }
        .rect-green {
            width: 500px;
            margin: 51px auto 22px;
            height: 4px; }
        .blue-cta {
            font-size: 18px;
            line-height: 26px;
            position: absolute; } }

    @include desktop {
        padding: 120px 0 88px;
        h3 {
            font-size: 44px;
            line-height: 40px;
            padding-bottom: 44px; }
        p {
            width: 740px; } }

    .cta-container {
        margin: auto;
        width: 243px;

        @include tablet {
            display: inline; } } }



