// Custom Styling for Solutions - Buy
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css
body {
	color: $brand-dk-grey; }
.ts-buy {
	// Styling used globally on this page
	sup {
		font-size: 10px;
		top: -.6em; }

	h1,
	h2,
	h3,
	h4 {
		font-weight: 200; }
	h1,
	h2,
	h3,
	h4,
	p {
		color: $brand-dk-grey; }
	h1 {
		font-size: 34px;
		line-height: 44px; }
	h2 {
		font-size: 26px;
		line-height: 33px; }
	h3 {
		font-size: 20px;
		line-height: 24px; }
	p {
		font-size: 14px;
		line-height: 20px; }
	@include phone {
		p {
			font-weight: 300; } }

	@include tablet {
		h1 {
			font-size: 40px;
			line-height: 44px; }
		h2 {
			font-size: 40px;
			line-height: 44px; }
		h3 {
			font-size: 24px;
			line-height: 33px; }
		p {
			font-size: 18px;
			line-height: 28px; } }
	@include desktop {
		h1 {
			font-size: 50px;
			//font-size: 3.15vw
			line-height: 55px; }
		h2 {
			font-size: 44px;
			line-height: 55px; }
		h3 {
			font-size: 28px;
			line-height: 40px; }
		h4 {
			font-size: 22px;
			line-height: 33px; }
		p {
			font-size: 18px;
			line-height: 28px; } }
	@include desktop-xl {
		h1 {
			font-size: 60px;
			line-height: 77px; }
		h2 {
			font-size: 44px;
			line-height: 55px; }

		h3 {
			font-size: 28px;
			line-height: 40px; }
		h4 {
			font-size: 28px;
			line-height: 40px; } }

	.btn-cta {
		background: $brand-s-apps;
		padding: 9px 22px !important;
		font-size: 18px !important;
		font-weight: 600 !important;
		height: 44px !important;
		margin: 0 auto !important;
		margin-left: 10px !important;
		border: none !important;
		box-shadow: none;
		border-radius: 22px !important;
		letter-spacing: -0.01em;
		color: $brand-white !important;
		text-align: center !important;
		@include transition(background 200ms ease-in);
		&:hover {
			cursor: pointer; }
		&:hover,
		&:focus,
		&:active {
			background: $brand-p-apps !important;
			box-shadow: none;
			border: none;
			outline: none;
			opacity: 1; } }
	// Email form field style
	input[type="e-mail"].form-control {
		border-radius: 0;
		border: none;
		height: 44px;
		margin-bottom: 22px;
		font-size: 18px;
		@include desktop {
			float: left;
			width: 60% !important; }
		@include desktop-xl {
			float: left;
			width: 50% !important; } }
	button[type="button"].btn-cta {
		@include desktop {
			display: inline-block;
			float: left;
			// margin-left: -30px !important
 } }			//width: 25% !important
	@include phone {
		.container {
			width: 320px; } }
	// Marketo Form | Style while being moved around
	.section-view {
		.mktoForm {
			font-family: $font-family-sans-serif;
			.mktoButtonRow {
				width: 100%;
				margin-top: 11px;
				text-align: center; }
			// Take off unneccessary divs and margins off the Marketo Form
			.mktoOffset {
				display: none; }
			.mktoLabel {
				display: none; }
			.mktoAsterix {
				display: none; }
			.mktoGutter {
				display: none; }
			.mktoButtonWrap {
				margin: 0 !important; }
			.mktoField {
				padding: 6px 12px;
				text-align: left;
				border: none;
				font-size: 18px;
				line-height: 22px;
				box-shadow: none;
				background: $brand-white;
				height: 44px;
				border-radius: 0 !important;
				@include placeholder {
					color: $brand-accent-grayscale-4 !important;
					font-size: 18px; }
				&:focus {
					outline: none !important; } }
			// Style the submit button
			.mktoButton {
				@extend .btn-cta;
				padding: 9px 33px !important;
				&:disabled {
					opacity: 1 !important; } }
			.mktoError {
				right: 0;
				bottom: 44px !important;
				left: 0;
				.mktoErrorMsg {
					background: #950202;
					border: none;
					background-image: none;
					color: $brand-white;
					box-shadow: none;
					border-radius: 0;
					text-shadow: none;
					margin-bottom: 7px;
					margin-top: 0;
					min-height: 40px; }
				.mktoErrorArrowWrap {
					top: 35px !important;
					width: 20px;
					height: 20px; }
				.mktoErrorArrow {
					width: 0;
					height: 0;
					background-color: transparent;
					border-left: 10px solid transparent;
					border-right: 10px solid transparent;
					border-top: 10px solid #950202;
					transform: none;
					margin-top: 0; } }
			@include phone {
				margin-top: 11px;
				#Company.mktoTextField {
					margin-top: 11px; } }
			@include tablet {
				width: 80% !important;
				.mktoFormRow {
					float: left;
					width: 100%; }
				// Style the input field
				.mktoField {
					width: 96% !important;
					text-align: left; }
				.mktoFormCol {
					float: none;
					margin-bottom: 0 !important; }
				.mktoFieldWrap {
					float: none;
					padding-right: 0; }
				.mktoButtonRow {
					margin-top: 22px;
					width: 80%;
					text-align: left; }
				.mktoButton {
					width: 60%; } }
			@include desktop {
				width: 100% !important;
				margin-left: 90px;
				.mktoFormRow {
					float: left;
					width: 230px;
					clear: none; }
				.mktoButtonRow {
					width: 180px;
					text-align: center;
					float: left;
					margin: 0 0 0 -35px; }
				.mktoButton {
					width: 180px; } }
			@include desktop-xl {
				margin-left: 180px; } } }

	.section-view {
		.icon-down-arrow {
			font-size: 16px;
			font-family: $font-family-sans-serif;
			color: #ff5200;
			font-weight: 300;
			position: absolute;
			bottom: 30px;
			left: 0;
			right: 0;
			margin: 0 auto;
			text-align: center;
			width: 240px;
			height: 40px;
			cursor: pointer;
			&::after {
				content: '';
				background: url(../img/carat-down-orange.png) no-repeat bottom center;
				width: 35px;
				height: 16px;
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				margin: 0 auto; }

			@include animation(bouncy 6s infinite);
			@include keyframes(bouncy) {
				0%, 20%, 50%, 80%, 100% {
					@include transform(translateY(0)); }
				40% {
					@include transform(translateY(-22px)); }
				60% {
					@include transform(translateY(-11px)); } } } }


