.supplychainfinancing {
	@include phone {
		p {
			font-size: 14px;
			line-height: 20px; }
		sup {
			font-size: 50%;
			top: -0.6em; } }
	@include tablet {
		sup {
			font-size: 50%;
			top: -0.6em; } }
	@include desktop {
		p {
			font-size: 18px;
			line-height: 26px; }
		sup {
			font-size: 60%;
			top: -0.5em; } }
	.hero {
		position: relative;
		h1 {
			color: $brand-white;
			padding-top: 0px;
			font-weight: 700; }
		p {
			color: $brand-white; }
		.isBlueText {
			color: $brand-p-ent; }
		.default_btn {
			background: #ff5200 !important;
			color: $brand-white !important;
			text-transform: initial !important;
			font-size: 18px;
			line-height: 26px;
			font-weight: 600; }
		.arrow {
			position: absolute;
			bottom: -1px;
			width: 100%;
			height: 0;
			background-color: #fff;
			z-index: 1015;
			box-sizing: border-box;
			&:after, &:before {
				content: "";
				position: absolute;
				bottom: 100%;
				width: 50%; }
			&.arrow:before {
				right: 50%;
				border-bottom: 16px solid #F2F2F7;
				border-right: 14px solid transparent; }
			&:after {
				left: 50%;
				border-bottom: 16px solid #F2F2F7;
				border-left: 14px solid transparent; } }
		@include phone {
			background: #282828 url(../img/supplychainfinance/enterprise-hero-bg-xs.jpg) no-repeat center center;
			background-size: cover;
			min-height: 580px;
			h1 {
				font-size: 24px;
				line-height: 30px; }
			.contents {
				padding-top: 66px; }
			.default_btn {
				font-size: 14px;
				line-height: 20px;
				margin-top: 11px; } }
		@include tablet {
			background: #282828 url(../img/supplychainfinance/enterprise-hero-bg-xs.jpg) no-repeat center center;
			background-size: cover;
			min-height: 760px;
			h1 {
				font-size: 34px;
				line-height: 44px; }
			.contents {
				padding-top: 66px; }
			.left-box {
				width: 405px;
				margin: auto; } }
		@include desktop {
			background: #282828 url(../img/supplychainfinance/enterprise-hero-bg.jpg) no-repeat center center;
			background-size: cover;
			min-height: 590px;
			.contents {
				padding-top: 130px; }
			.left-box {
				margin-left: 88px;
				@include make-md-column(6); } }
		@include desktop-xl {
			.contents {
				padding-top: 110px; }
			.left-box {
				padding-right: 155px; } } }
	.paymentPrograms {

		&.background-gradient {
			background-image: linear-gradient(#dcdcdc, white); }

		h2 {
			color: #1e1e1e;
			font-weight: 300;
			text-align: center;
			margin: auto; }
		h4 {
			color: #00b0ff;
			text-align: center;
			font-weight: 300; }
		.howItWorks {
			&-wrapper {
				@include make-xs-column(12);
				@include make-sm-column(4); }
			&-header {
				height: 77px;
				@include flex;
				img {
					display: block;
					margin: auto; } }
			&-body {
				h5 {
					text-align: center;
					font-weight: 300; }
				p {
					font-weight: 300;
					text-align: center; } }
			&-topSeparator {
				background-image: linear-gradient( 270deg, rgb(0,78,255) 0%, rgb(0,176,255) 100%);
				width: 199px;
				height: 4px;
				margin: auto;

				~ .howItWorks-body {
					padding-top: 22px; } } }

		@include phone {
			h2 {
				font-size: 24px;
				line-height: 24px; }
			h3 {
				font-weight: 300;
				margin-top: 11px;
				text-align: center;
				line-height: 20px;
				font-size: 26px; }
			h4 {
				font-size: 18px;
				line-height: 26px;
				margin-top: 11px; }
			h5 {
				font-size: 18px;
				line-height: 22px; }
			.container {
				padding: 25px 0 0 0; }
			.howItWorks {
				&-wrapper {
					padding-top: 15px; }
				&-body p {
					margin-top: 5px; } } }
		@include tablet {
			padding: 66px 0;
			h2 {
				font-size: 34px;
				line-height: 44px; }
			h3 {
				font-size: 28px;
				line-height: 26px;
				width: 730px;
				font-weight: 300;
				text-align: center;
				margin: auto;
				color: #1e1e1e;
				margin-top: 33px;
				margin-bottom: 33px; }
			h4 {
				font-size: 28px;
				line-height: 33px;
				margin-bottom: 18px; }
			.contents {
				padding: 0 15px; }
			.howItWorks {
				&-wrapper {
					padding: 0 11px !important; }
				&-body {
					h5 {
						font-size: 18px;
						line-height: 26px; }
					p {
						margin-top: 8px;
						font-size: 14px;
						line-height: 20px; } } } }
		@include desktop {
			.contents {
				padding: 0px; }
			.howItWorks {
				&-wrapper {
					padding: 0 22px !important; } } }
		@include desktop-xl {
			h2 {
				&.hasPadding {
					padding: 0 50px; } } } } }





