// Form styling for Sign Up Modal Form
// ---------------------------------------

/* For Phone, it would be outside main element | To override vendor settings, style on main level */
@include phone {
	.modal.modal-sign-up.in {
		.modal-dialog {
			@include transform(translate3d(0, 0, 0)); } }
	.modal.modal-sign-up.fade:not(.in).bottom {
		.modal-dialog {
			@include transform(translate3d(0, 25%, 0)); } }
	.modal.modal-sign-up {
		.modal-dialog {
			position: absolute;
			width: 100%;
			bottom: 0;
			margin: 0; }
		.modal-content {
			box-shadow: none;
			height: 515px;
			border: none; } } }
.modal.modal-sign-up {
	.modal-header {
		background: $brand-white;
		border-bottom: none;
		.close {
			font-size: 44px;
			font-weight: 200;
			color: $brand-accent-grayscale-2; }
		h3 {
			font-size: 34px;
			line-height: 44px;
			font-weight: 200;
			color: $brand-accent-grayscale-2;
			width: 250px;
			margin-top: 44px; } }
	.modal-body {
		input[type="checkbox"] {
			display: none; }
		input[type="checkbox"] + label {
			font-size: 18px; }
		input[type="checkbox"] + label span {
			background: $brand-accent-grayscale-5;
			display: inline-block;
			width: 24px;
			height: 24px;
			margin: 0px 15px 0 -40px;
			vertical-align: middle;
			cursor: pointer;
			border-radius: 50%;
			border: 6px solid $brand-accent-grayscale-5; }
		input[type="checkbox"]:checked + label span {
			border: 6px solid $brand-blue; }
		input[type="checkbox"] + label span,
		input[type="checkbox"]:checked + label span {
			@include transition(border 400ms ease-in); }
		label {
			margin-bottom: 22px;
			margin-left: 60px;
			font-weight: 200;
			color: $brand-accent-grayscale-2; }
		button.btn-primary-cta-ent {
			margin: 11px auto;
			float: none;
			padding-left: 44px;
			padding-right: 44px;
			display: table; }
		p.sub-enterprise {
			text-align: center;
			width: 180px;
			margin: 22px auto 0;
			color: $brand-accent-grayscale-4;
			font-style: italic; } } }

/* For >tablet - element is outside main element */
@include tablet {
	.modal.modal-sign-up {
		.modal-header {
			h3 {
				font-size: 34px;
				line-height: 44px;
				width: 540px;
				margin-top: 22px; } }
		.modal-body {
			padding: 0 15px 44px;
			p.sub-enterprise {
				text-align: center;
				width: 480px;
				margin: 22px auto 0;
				color: $brand-accent-grayscale-4;
				font-size: 14px;
				font-style: italic; } } } }

@include desktop {
	.modal.modal-sign-up {
		.modal-dialog {
			margin: 150px auto; } } }
