// Custom Styling for Supplier Onboarding
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.supplier-onboarding {
	.hero.key-to-success {
		padding: 121px 0;
		background: $brand-white url('../img/supplier-onboarding/hero-bg-xs.jpg') no-repeat center top;
		height: 477px;
		position: relative;
		text-align: left;
		h1 {
			color: $brand-white;
			margin-bottom: 11px; }
		h3 {
			color: $brand-white;
			margin-bottom: 44px;
			width: 66%; }
		.contents {
			@extend .col-xs-12; }
		.arrow {
			position: absolute;
			bottom: 0;
			width: 100%;
			height: 0;
			background-color: $brand-black-lighter; }

		.arrow:before,
		.arrow:after {
			content: '';
			position: absolute;
			bottom: 100%;
			width: 50%;
			box-sizing: border-box; }

		.arrow:before {
			right: 50%;
			border-bottom: 16px solid $brand-black-lighter;
			border-right: 14px solid transparent; }

		.arrow:after {
			left: 50%;
			border-bottom: 16px solid $brand-black-lighter;
			border-left: 14px solid transparent; }

		@include tablet {
			padding: 99px 0;
			background: $brand-white url('../img/supplier-onboarding/hero-bg.jpg') no-repeat center top;
			text-align: center;
			height: 501px;
			h1 {
				margin-bottom: 11px; }
			h3 {
				margin-bottom: 44px;
				width: 100%; } }
		@include desktop {
			padding: 143px 0; } }

	.supplier-participation {
		padding: 22px 0 44px;
		background: $brand-black-lighter;
		text-align: left;
		.intro-text,
		.velocity-graph {
			@extend .col-xs-12;
			@extend .col-sm-6; }
		.intro-text {
			@extend .col-lg-offset-1;
			@extend .col-lg-5; }

		h2 {
			margin: 0 0 11px 15px; }
		p {
			color: $brand-white;
			text-align: left; }
		em {
			font-style: normal;
			font-weight: 800; }
		img {
			margin: 22px 0 0 0;
			width: 100%; }
		@include tablet {
			h2 {
				margin: 0 0 33px 0;
				text-align: center; }
			p {
				font-size: 16px;
				line-height: 28px; }
			img {
				width: 330px;
				margin: 11px 0 0 0; } }
		@include desktop {
			img {
				width: 380px;
				float: right; }
			p {
				font-size: 18px;
				line-height: 33px; } }
		@include desktop-xl {
			img {
				width: 415px;
				float: left; } } }

	.supplier-love-us {
		padding: 44px 0 399px;
		background: $brand-white url('../img/supplier-onboarding/supplier-love-us-bg-xs.jpg') no-repeat center bottom;
		height: 1008px;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-offset-5;
			@extend .col-sm-7;
			@extend .col-md-offset-6;
			@extend .col-md-6;
			@extend .col-lg-offset-6;
			@extend .col-lg-5; }
		h3 {
			color: $brand-blue; }
		.owl-carousel {
			.quote-wrapper {
				width: 100%;
				padding: 22px 0;
				text-align: left;
				.quote {
					color: $brand-blue;
					font-weight: 300;
					font-size: 26px;
					line-height: 33px;
					position: relative; }
				.person-title {
					color: $brand-black;
					font-weight: 300;
					font-size: 14px;
					margin: 22px 0 0; } } }
		.owl-page {
			span {
				background: $brand-black-lighter !important; }
			&.active {
				span {
					background: $brand-blue !important; } } }
		.owl-controls {
			position: absolute !important;
			top: 266px !important;
			right: 0 !important;
			left: 0 !important; }
		@include tablet {
			padding: 66px 0;
			background: $brand-white url('../img/supplier-onboarding/supplier-love-us-bg.jpg') no-repeat 50% bottom;
			height: 708px;
			p {
				font-size: 16px;
				line-height: 28px; }
			.owl-controls {
				top: 242px !important; } }
		@include desktop {
			padding: 88px 0;
			.owl-controls {
				top: 209px !important; }
			p {
				font-size: 18px;
				line-height: 33px; } } }
	.tradeshift-difference {
		padding: 44px 0;
		background: $brand-white url('../img/supplier-onboarding/andres-bg-xs.jpg') no-repeat center top;
		height: 418px;
		text-align: center;
		.contents {
			@extend .col-xs-12;
			position: relative; }
		.btn-play-andres {
			background: url('../img/misc/btn-play-lg.png') no-repeat top center;
			background-size: 60%;
			width: 120px;
			height: 120px;
			margin: 0 auto;
			position: absolute;
			top: 110px;
			left: 0;
			right: 0; }
		h2 {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			width: 198px;
			margin: 0 auto;
			color: $brand-white; }
		p {
			position: absolute;
			top: 232px;
			color: $brand-white;
			padding: 0 55px; }
		@include tablet {
			padding: 0;
			background: $brand-white url('../img/supplier-onboarding/andres-bg.jpg') no-repeat center top;
			height: 429px;
			.contents {
				background: url('../img/supplier-onboarding/andres.png') no-repeat center -20%;
				height: 429px;
				padding: 99px 0; }
			.btn-play-andres {
				background: url('../img/misc/btn-play-lg.png') no-repeat top center;
				background-size: 100%;
				width: 120px;
				height: 120px;
				position: relative;
				top: 0; }
			h2 {
				position: relative;
				padding: 0;
				margin: 33px auto 0;
				width: 100%; }
			p {
				position: relative;
				top: 0; } } }
	.six-pronged-approach {
		padding: 44px 0;
		background: $brand-white url('../img/supplier-onboarding/rocket.png') no-repeat center 18%;
		position: relative;
		.contents {
			@extend .col-xs-12; }
		.heading {
			background: transparent; }
		.approach-list {
			background: $brand-sky-blue; }
		.first,
		.second,
		.third {
			@extend .col-xs-12;
			@extend .col-sm-5;
			@extend .col-md-6; }
		.fourth,
		.fifth,
		.sixth {
			@extend .col-xs-12;
			@extend .col-sm-offset-2;
			@extend .col-sm-5;
			@extend .col-md-offset-1;
			@extend .col-md-5; }
		.learn-more {
			text-align: center;
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			background: $brand-sky-blue;
			padding: 33px 0 11px;
			p {
				font-size: 18px;
				line-height: 22px; } }
		h3 {
			color: $brand-black;
			font-size: 26px;
			line-height: 33px;
			margin: 0 auto 501px;
			text-align: center;
			em {
				font-style: normal;
				font-weight: 800; } }
		h4 {
			color: $brand-blue;
			font-size: 18px;
			line-height: 22px;
			font-weight: 600;
			margin: 22px 0 11px;
			padding: 0; }
		p {
			font-size: 14px;
			line-height: 22px;
			margin: 0; }
		@include tablet {
			padding: 44px 0;
			background: $brand-white url('../img/supplier-onboarding/rocket.png') no-repeat center 101%;
			height: 836px;
			.approach-list {
				background: transparent; }
			.first,
			.second,
			.third {
				padding: 0 33px 0 15px; }
			.fourth,
			.fifth,
			.sixth {
				padding: 0 15px 0 33px; }
			h3 {
				width: 88%;
				margin: 0 auto 33px; }
			.learn-more {
				background: transparent;
				padding: 0; }
			h4 {
				margin: 22px 0 0;
				font-size: 16px; } }
		@include desktop {
			padding: 66px 0;
			height: 732px;
			.first,
			.second,
			.third {
				padding: 0 99px 0 0; }
			.fourth,
			.fifth,
			.sixth {
				padding: 0 0 0 33px; }
			h4 {
				font-size: 18px; } }

		@include desktop-xl {
			.first,
			.second,
			.third {
				padding: 0 88px 0 66px; }
			.fourth,
			.fifth,
			.sixth {
				padding: 0 66px 0 22px; }
			h3 {
				width: 66%; } } }
	.learn-more-resources {
		padding: 11px 0 44px;
		background: $brand-sky-blue;
		text-align: left;
		.contents {
			@extend .col-xs-12; }
		p {
			font-size: 14px;
			line-height: 22px;
			padding: 0;
			margin: 0; }
		@include tablet {
			padding: 22px 0;
			text-align: center; } }
	.supplier-success {
		padding: 44px 0;
		background: $brand-black-lighter url('../img/supplier-onboarding/footer-cta-bg-xs.jpg') no-repeat center top;
		height: 923px;
		color: $brand-white;
		text-align: left;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-offset-1;
			@extend .col-sm-10;
			@extend .col-lg-offset-2;
			@extend .col-lg-8; }
		.intro-text {
			padding: 0;
			p {
				margin: 11px 0 22px 0; } }
		.testimonial {
			margin-top: 243px;
			text-align: left;
			img {
				margin: 0 auto;
				width: 120px;
				display: block; }
			.quote {
				font-size: 26px;
				font-weight: 400;
				line-height: 33px;
				text-align: center;
				margin-top: 11px;
				letter-spacing: -0.01em; }
			.person {
				margin-top: 11px;
				font-size: 14px;
				line-height: 22px;
				text-align: center;
				em {
					font-style: normal;
					font-weight: 600;
					display: block; } } }
		h4 {
			color: $brand-white;
			font-size: 26px;
			line-height: 33px;
			font-weight: 600; }
		p {
			font-size: 18px;
			line-height: 22px; }

		.btn-primary-cta {
			margin: 0 auto;
			display: block;
			text-align: center !important;
			width: 198px; }
		@include tablet {
			padding: 44px 0;
			background: $brand-black-lighter url('../img/supplier-onboarding/footer-cta-bg.jpg') no-repeat center top;
			height: 528px;
			text-align: center;
			.intro-text {
				padding: 0 44px;
				p {
					margin: 11px 66px 22px; } }
			.testimonial {
				display: table-cell;
				padding: 121px 22px 0;
				img {
					margin: 0 33px 0 0;
					display: table-cell;
					float: left; }
				.quote {
					padding-bottom: 11px;
					display: table-cell;
					text-align: left;
					font-weight: 300; }
				.person {
					display: table-row;
					text-align: left;
					em {
						display: inline; } } } }
		@include desktop {
			.intro-text {
				padding: 0 66px;
				p {
					margin: 11px 88px 22px; } }
			.testimonial {
				display: table-cell;
				padding: 132px 88px 0; } }
		@include desktop-xl {
			.intro-text {
				p {
					margin: 11px 77px 22px; } } } } }



