// Utilities |  Helpers  | Mixins  | src: /less/_utilities.sass
//  We now include responsive mixins here
// Helper classes: prefix by u-

/* This is the utility to sniff out sitewide navigation media query */
/* We use mobile nav for device width up to 991px */
/* We then have the desktop nav view for device width over 992px */
$S:    $screen-sm-min;
$M:    $screen-md-min;

@mixin MQ($viewport) {
	@if $viewport == S {
		@media only screen and (min-width: $S) {
			@content; } }
	@else if $viewport == M {
		@media only screen and (min-width: $M) {
			@content; } } }


/* Bootstrappy Mixins for Responsive Layout */
@mixin phone {
	@media screen and (max-width: $screen-xs-max) {
		@content; } }

@mixin phoneplus {
	@media (min-width: $screen-xs) and (max-width: $screen-xs-max) {
		@content; } }

@mixin phone-to-desktop {
	@media screen and (max-width: $screen-sm-max) {
		@content; } }

@mixin tablet {
	@media (min-width: $screen-sm-min) {
		@content; } }

@mixin tablet-only {
	@media (max-width: $screen-sm-max) and (min-width: $screen-sm-min) {
		@content; } }

@mixin tablet-to-desktop {
	@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
		@content; } }

@mixin desktop-sm {
	@media (min-width: 992px) and (max-width: 1200px) {
		@content; } }
@mixin desktop {
	@media (min-width: $screen-md-min) {
		@content; } }

@mixin desktop-lg {
	@media (min-width: $screen-lg-min) {
		@content; } }

@mixin desktop-1500 {
	@media (min-width: 1500px) {
		@content; } }

@mixin desktop-1920 {
	@media (min-width: 1920px) {
		@content; } }
@mixin tablet-and-phone {
	@media (max-width: 992px) {
		@content; } }


/* When you need 5 in a 12 column grid - let's transform to col-*-15 */
@media (min-width: $screen-xs-min) {
	.col-xs-15-5 {
		width: 20%;
		float: left; } }
@media (min-width: $screen-sm-min) {
	.col-sm-15-5 {
		width: 20%;
		float: left; } }
@media (min-width: $screen-md-min) {
	.col-md-15-5 {
		width: 20%;
		float: left; } }
@media (min-width: $screen-lg-min) {
	.col-lg-15-5 {
		width: 20%;
		float: left; } }

@mixin make-column-15-5 {
	width: 20%;
	float: left; }


/* Deprecated as of 8/12/15 - still being used */
@mixin desktop-xl {
	@media (min-width: $screen-lg-min) {
		@content; } }

/* Font Smoothing */
@mixin font-smoothing {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale; }

/* Clearfix to separate content */

.clearfix {
	height: 44px;
	display: block; }

/* Visible Hidden Combos */

.hidden-xs-sm {
	@include phone {
		display: none !important; }
	@include tablet {
		display: none !important; }
	@include desktop {
		display: block !important; } }

.hidden-md-lg {
	display: block !important;
	@include desktop {
		display: none !important; } }

/* Background Treatment (Pertains to opacity overlays over background-images) */
@mixin bg-treatment-white {
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 10;
	&::after {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		width: 100%;
		height: 100%;
		z-index: 20;
		@extend .gradient-white100-white55; } }
@mixin bg-treatment-dark {
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 10;
	&::after {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		width: 100%;
		height: 100%;
		z-index: 20;
		@extend .gradient-black70-white10; } }
@mixin bg-customer-logos {
	&::after {
		content: '';
		position: absolute;
		left: 0;
		right: 0;
		width: 100%;
		height: 100px;
		@extend .gradient-customer-logos; } }
@mixin bg-casestudies-container {
	&::after {
		content: '';
		position: absolute;
		left: 0;
		right: 0;
		width: 100%;
		height: 100px;
		background: url('../img/logos/hero-gradient-bg.png') repeat-x; } }
@mixin bg-customer-logos-container {
	&::after {
		content: '';
		position: absolute;
		left: 0;
		right: 0;
		width: 100%;
		height: 100px; } }

/* Reset Utilities */

@mixin reset-to-desktop {
	position: relative;
	visibility: visible;
	background: none;
	top: initial;
	left: initial;
	height: initial; }

@mixin reset-animation {
	transition: initial; }

@mixin reset-box-model {
	margin: 0;
	padding: 0;
	border: 0; }

@mixin reset-font {
	font: inherit;
	font-size: 100%;
	vertical-align: baseline;
	text-transform: initial; }

/* IE 10 in Windows 8 Viewport Patch */
@-webkit-viewport {
	width: device-width; }
@-moz-viewport {
	width: device-width; }
@-ms-viewport {
	width: device-width; }
@-o-viewport {
	width: device-width; }
@viewport {
	width: device-width; }

/* Typed.js styling */
.typed-cursor {
	opacity: 1;
	-webkit-animation: blink 2s infinite;
	-moz-animation: blink 2s infinite;
	animation: blink 2s infinite; }

@keyframes blink {
	0% {
		opacity: .8; }
	50% {
		opacity: 0; }
	100% {
		opacity: .8; } }
@-webkit-keyframes blink {
	0% {
		opacity: .8; }
	50% {
		opacity: 0; }
	100% {
		opacity: .8; } }
@-moz-keyframes blink {
	0% {
		opacity: .8; }
	50% {
		opacity: 0; }
	100% {
		opacity: .8; } }

/* Vertical Alignment of Elements */
/* Refer to:  http://zerosixthree.se/vertical-align-anything-with-just-3-lines-of-css */
/* To prevent blurry elements due to half pixel placement, assign the preserve-3d to parent container */

@mixin preserve-3d {
	-webkit-transform-style: preserve-3d;
	-moz-transform-style: preserve-3d;
	transform-style: preserve-3d; }

@mixin vertical-align {
	position: relative;
	top: 50%;
	-webkit-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	transform: translateY(-50%); }

@mixin flex {
	display: -webkit-box      /* OLD - iOS 6-, Safari 3.1-6 */;
	display: -moz-box         /* OLD - Firefox 19- (buggy but mostly works) */;
	display: -ms-flexbox      /* TWEENER - IE 10 */;
	display: -webkit-flex     /* NEW - Chrome */;
	display: -ms-flexbox /* MS 10 */;
	display: flex; }

@mixin flex-justify($position) {
	-moz-align-items: $position;
	-ms-flex-pack: $position /* MS 10 */;
	-moz-justify-content: $position;
	-webkit-justify-content: $position;
	justify-content: $position; }

@mixin flex-direction($direction) {
	-moz-flex-direction: $direction;
	-webkit-flex-direction: $direction;
	-ms-flex-direction: $direction /* MS 10 */;
	flex-direction: $direction; }

@mixin flex-align($position) {
	-webkit-align-items: $position;
	-ms-flex-align: $position !important;
	align-items: $position; }



