$brand-accent-green:		#4fc610;
$brand-accent-blue:			#1e3d55;
$brand-accent-gold:			#ff9900;
$brand-accent-grayscale-1:	#1e1e1e;
$brand-accent-grayscale-2:	#282828;
$brand-accent-grayscale-3:	#3c3c3c;
$brand-accent-grayscale-4:	#646464;
$brand-accent-grayscale-5:	#d2d2d2;
$brand-accent-grayscale-6:	#e0e0e0;

$brand-blue:				#20A0E9;
$brand-green:				#6BAE58;
$brand-navy:				#1E3D55;
$brand-mid-blue:            #18648F;
$brand-bright-blue:         #1E91D2;
$brand-sky-blue:			#C5E1F7;
$brand-baby-blue:           #F3F9FD;
$brand-red:					#BE0000;
$brand-ts-red:              #BF0000;
$brand-orange:				#FF5200;
$brand-mandarin:			#F6AE00;
$brand-yellow:				#FFFFCC;
$brand-gold:                #FFAF33;
$brand-black:				#1E1E1E;
$brand-charcoal:			#272727;
$brand-dk-grey:				#3C3C3C;
$brand-grey:				#646464;
$brand-lt-grey:				#D2D2D2;
$brand-grey-form:			#E6E7E8;
$brand-maroon:              #BF0000;
$brand-white:               #FFFFFF;
$brand-brown:               #876F4A;

// Auxillary colors not on style guide
// Use is optional - if duplicate, use above variables first
// ---------------------------------------------------------
$text:						#1e1e1e; // legacy
$text-alt:					#FFFFFF; // legacy

$brand-blue-opaque-light:	rgba(24, 100, 143, 0.5);
$brand-blue-opaque:			rgba(24, 100, 143, 1);
$brand-blue-opaque-darker:	rgba(24, 100, 143, 0.9);
$brand-gray-opaque:         rgba(210, 210, 210, 0.6);

$brand-blue-light:          #F1F9Fd;
$brand-navy-light:          #1F3D55;
$brand-gray:                #888a8a;
$brand-lt-gray:             #E8E8E8;
$brand-gray-darker:         #464646;
$brand-gray-lighter:        #F1F1F1;
$brand-gray-lighter2:        #F2F2F2;
$brand-black-lighter: 		#282828;

// Colors of respective brand owners
// ------------------------------------------------------
$facebook:            #3b5998;
$twitter:             #00aced;
$pinterest:           #cb2027;
$linkedin:            #007bb6;
$instagram:           #517fa4;
$google-plus:         #d34836;
$glassdoor:           #7cb228;


// Typography - Font, line-height, and color for body text, headings, and more.
// -----------------------------------------------------------------------------

$font-family-sans-serif:  'Open Sans', Helvetica, Arial, sans-serif;
$font-family-serif:       'Bitter', Georgia, "Times New Roman", Times, serif;
$font-family-monospace:   Menlo, Monaco, Consolas, "Courier New", monospace;
$font-family-base:        $font-family-sans-serif;
$font-family-heading:     $font-family-sans-serif;
$font-awesome:            'FontAwesome';
$font-linearicons:        'LinearIcons';


// Headers - h1, h2, etc.
// ----------------------------------------------
h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $font-family-heading;
	margin-top: 0;
	margin-bottom: 0;
	color: #1E1E1E;
	&.on-dark {
		color: $brand-white; }
	&.centered {
		text-align: center; }
	@include font-smoothing; }
h1 {
	font-weight: 800;
	font-size: 28px;
	line-height: 33px;
	padding-top: 44px;
	@include tablet {
		font-size: 48px;
		line-height: 55px; }
	@include desktop {
		font-size: 48px;
		line-height: 55px; } }
h2 {
	font-weight: 300;
	font-size: 26px;
	line-height: 33px;
	color: #20A0E9;
	@include tablet {
		font-size: 34px;
		line-height: 44px; }
	@include desktop {
		font-size: 34px;
		line-height: 44px; } }
h3 {
	font-weight: 400;
	font-size: 18px;
	line-height: 22px;
	@include tablet {
		font-size: 26px;
		line-height: 33px; }
	@include desktop {
		font-weight: 400;
		font-size: 26px;
		line-height: 33px; } }
h4 {
	font-weight: 400;
	font-size: 18px;
	line-height: 22px; }
h5 {
	font-weight: 400;
	font-size: 11px;
	line-height: 11px; }

// Paragraph | Lead, Normal, and Subs
// ---------------------------------------------

p {
	font-size: 14px;
	font-weight: 400;
	line-height: 22px;
	@include font-smoothing;
	&.on-dark {
		color: $brand-white; }
	&.centered {
		text-align: center; }
	@include tablet {
		font-size: 18px;
		line-height: 33px; } }
p.lead {
	font-size: 18px; }
p.sub {
	font-size: 12px; }

a {
	color: $brand-blue; }

// Buttons - Based on Style Guide
// ----------------------------------------------

.btn {
	display: inline-block;
	padding: 6px 0px;
	margin-bottom: 0;
	font-size: 14px;
	font-weight: 400;
	line-height: 1.42857143;
	text-align: left;
	white-space: nowrap;
	vertical-align: middle;
	touch-action: manipulation;
	cursor: pointer;
	user-select: none;
	background-image: none;
	border: 1px solid transparent;
	border-radius: 0px;
	&:focus {
		outline: none; } }
.btn-request-demo {
	padding: 0px 11px;
	height: 22px;
	border-radius: 11px;
	background: $brand-white;
	color: $brand-blue;
	text-transform: uppercase;
	font-size: 11px !important;
	text-align: center;
	margin-top: -4px; }

.btn-request-demo-ent {
	padding: 7px 15px !important;
	height: 33px;
	border-radius: 33px;
	text-align: center;
	margin-top: -4px;
	background-color: $brand-p-ent;
	color: $brand-white !important;
	font-size: 12px;
	font-weight: 700 !important;
	margin-top: 11px;
	text-transform: uppercase;
	letter-spacing: 0.01em;
	&:hover,
	&:focus,
	&:active {
		background-color: $brand-s-ent !important;
		color: $brand-white !important;
		@include transition(background-color 200ms ease-out 0s); } }

.btn-request-demo-lg {
	padding: 0px 5px;
	width: 160px;
	height: 34px;
	border-radius: 17px;
	background-color: $brand-white;
	color: $brand-blue;
	text-transform: uppercase;
	font-size: 14px;
	text-align: center;
	margin-top: 0;
	z-index: 1000; }

.btn-primary-cta,
.btn-primary-cta-on-blue,
.btn-secondary-cta,
.btn-play-navy-cta,
.btn-play-secondary-cta,
.btn-transparent-white {
	font-weight: 600 !important;
	font-size: 18px !important;
	text-align: left !important;
	height: 44px !important;
	border-radius: 22px !important;
	border: none !important;
	padding: 8px 22px 10px 22px !important; }

.btn-primary-cta {
	color: #FFFFFF !important;
	background-color: #F05522 !important;
	background-image: initial !important;
	&:active {
		color: #FFFFFF !important;
		background-color: #90181C !important; }
	&:hover {
		color: #FFFFFF !important;
		background-color: #DB3A14 !important; }
	&:focus {
		color: #FFFFFF !important;
		background-color: #F05522 !important; } }

.btn-primary-cta-ent,
.btn-primary-cta-go {
	color: $brand-white;
	font-size: 12px;
	font-weight: 800;
	text-transform: uppercase;
	text-align: center;
	border-radius: 22px;
	padding: 6px 22px;
	letter-spacing: 0.01em; }

.btn-primary-cta-go {
	background-color: $brand-p-go;
	&:hover {
		background-color: $brand-s-go;
		color: $brand-white;
		@include transition(background-color 200ms ease-out 0s); } }

.btn-primary-cta-ent {
	background-color: $brand-p-ent;
	&:hover {
		background-color: $brand-s-ent;
		color: $brand-white;
		@include transition(background-color 200ms ease-out 0s); } }

.btn-primary-cta-on-blue {
	color: #20A0E9;
	background-color: #FFFFFF;
	&:active,
	&:focus {
		color: #FFFFFF;
		background-color: #1E3D55; }
	&:hover {
		color: #FFFFFF;
		background-color: #C5E1F7; } }

.btn-secondary-cta {
	color: #FFFFFF;
	background-color: #F4AD1B;
	&:active {
		color: #FFFFFF;
		background-color: #EA9103; }
	&:hover,
	&:visited {
		color: #FFFFFF;
		background-color: #F5B633; } }
.btn-transparent-white {
	color: $brand-white !important;
	background: transparent !important;
	border: 1px solid $brand-white !important; }

.btn-play-navy-cta {
	color: $brand-white;
	background: $brand-navy url('../img/icons/icon-play-alternative.png') no-repeat 22px center;
	background-size: 16px;
	padding: 5px 22px 7px 46px !important;
	&:active,
	&:hover,
	&:visited,
	&:focus {
		color: $brand-white;
		cursor: pointer; } }
.btn-play-secondary-cta {
	color: $brand-white;
	background: #F4AD1B url('../img/icons/icon-play-alternative.png') no-repeat 22px center;
	background-size: 16px;
	padding: 5px 22px 7px 46px !important;
	&:active,
	&:hover,
	&:visited,
	&:focus {
		color: $brand-white;
		cursor: pointer; } }
.btn-play {
	background: url('../img/roles/finance/btn-play.png') no-repeat top center;
	width: 83px;
	height: 83px; }

.btn-play-lg {
	background: url('../img/misc/btn-play-lg.png') no-repeat top center;
	width: 120px;
	height: 120px;
	color: $brand-white; }
.btn-play-bordered {
	background: url('../img/misc/btn-play-bordered.png') no-repeat top center;
	width: 108px;
	height: 108px;
	position: relative;
	z-index: 480; }
.btn-play-orange {
	background: url('../img/solutions/supplier-mgmt/btn-play-orange.png') no-repeat top center;
	width: 76px;
	height: 76px; }
.btn-play-gray {
	background: url('../img/solutions/risk/btn-play-gray.png') no-repeat top center;
	width: 82px;
	height: 82px; }
.btn-play-blue {
	background: url('../img/solutions/risk/btn-play-blue.png') no-repeat top center;
	width: 82px;
	height: 82px; }
.btn-play-white {
	background: url('../img/solutions/pay/btn-play-white.png') no-repeat top center;
	width: 72px;
	height: 72px; }

.btn-play-mandarin {
	background: $brand-mandarin url('../img/icons/icon-play-alternative.png') no-repeat 56% 50%;
	background-size: 24px;
	width: 66px;
	height: 66px;
	border-radius: 50%; }

.btn-timeline {
	height: 30px;
	width: 30px;
	border-radius: 50%; }
.btn-blue {
	background-color: $brand-blue; }
.btn-green {
	background-color: $brand-green; }
.btn-red {
	background-color: $brand-red; }
.btn-orange {
	background-color: $brand-orange; }
.btn-gold {
	background-color: $brand-accent-gold !important; }
.btn-white {
	color: $brand-orange !important;
	background-color: $brand-white !important; }
.btn-white-gold {
	color: $brand-accent-gold !important;
	background-color: $brand-white !important; }
.btn-blue-white {
	color: $brand-white !important;
	background-color: $brand-p-ent !important; }
.btn-tight {
	padding: 8px 14px !important; }
.btn-small {
	display: inline-block !important;
	padding: 6px 11px !important;
	font-size: 12px !important;
	font-weight: 600 !important;
	line-height: 1 !important;
	text-align: center !important;
	height: 22px !important;
	border-radius: 11px !important; }


// Forms
// ------------------------------------------
label {
	font-weight: 600; }

.label-default {
	font-weight: 300 !important;
	font-size: 11px !important;
	text-align: left !important;
	letter-spacing: 0.8px !important;
	text-transform: uppercase !important;
	background-color: transparent !important;
	color: $brand-blue !important;
	font-family: $font-family-sans-serif !important;
	margin-bottom: 8px !important; }
.input-fields-default {
	background-color: $brand-grey-form !important;
	font-weight: 300 !important;
	font-size: 18px !important;
	text-align: left !important;
	letter-spacing: 0.8px !important;
	color: $brand-grey !important;
	border-radius: 0 !important;
	border: none !important;
	box-shadow: none !important;
	height: 44px !important;
	font-family: $font-family-sans-serif !important;
	padding: 6px 12px !important;
	line-height: initial !important; }
.form-control,
.input-fields-default {
	&::-webkit-input-placeholder {
		font-family: $font-family-sans-serif !important;
		margin-top: 5px !important;
		border-radius: 0 !important; } }

@mixin label-default {
	font-weight: 300 !important;
	font-size: 11px !important;
	text-align: left !important;
	letter-spacing: 0.8px !important;
	text-transform: uppercase !important;
	background-color: transparent !important;
	color: $brand-blue !important;
	font-family: $font-family-sans-serif !important;
	margin-bottom: 8px !important; }

@mixin input-fields-default {
	background-color: $brand-grey-form !important;
	font-weight: 300 !important;
	font-size: 18px !important;
	text-align: left !important;
	letter-spacing: 0.8px !important;
	color: $brand-grey !important;
	border-radius: 0 !important;
	border: none !important;
	box-shadow: none !important;
	height: 44px !important;
	font-family: $font-family-sans-serif !important;
	padding: 6px 12px !important;
	line-height: initial !important;
	&::-webkit-input-placeholder {
		font-family: $font-family-sans-serif !important;
		margin-top: 5px !important;
		border-radius: 0 !important; } }





