// Custom Styling for SMB Page - Refined and old version
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

/* SMB Refined */
.smb-r {
	.hero.free-invoice-template {
		background: $brand-white url('../img/smb/hero-bg-xs.jpg') no-repeat 50% top;
		padding: 0 0 55px;
		text-align: center;
		position: relative;
		h1 {
			font-size: 48px;
			color: $brand-white;
			width: 290px;
			margin: 0 auto;
			font-weight: 600;
			line-height: 60px;
			em {
				font-weight: 800;
				font-style: normal; } }
		h3 {
			color: $brand-white;
			font-size: 16px;
			padding: 33px 0 11px;
			width: 290px;
			margin: 0 auto; }
		.row {
			background: url('../img/smb/ribbon.png') no-repeat 204% 0;
			background-size: 477px !important; }
		.intro-text-cta {
			@extend .col-xs-12;
			@extend .col-sm-offset-1;
			@extend .col-sm-8;
			padding-top: 176px; }
		form {
			width: 290px;
			margin: 0 auto; }
		input[type=text],
		select {
			background-color: $brand-white;
			opacity: .9;
			text-align: left;
			border-radius: 0;
			border: none;
			height: 44px;
			font-family: $font-family-sans-serif;
			font-size: 14px;
			padding: 11px 22px;
			width: 100%;
			&::-webkit-input-placeholder {
				color: $brand-black;
				font-size: 14px; } }
		select {
			-webkit-appearance: none;
			background: $brand-white url('../img/smb/dropdown-bg-xs.png') no-repeat 100% 0;
			option {
				border: 0;
				border-radius: 0;
				padding: 11px 22px; } }
		input[type=submit] {
			width: 100%;
			text-align: center !important;
			margin: 0; }
		.has-error {
			.help-block {
				color: #F60000 !important;
				font-weight: 600 !important; } }
		.form-control-feedback {
			top: 5px !important;
			right: 5px !important; }
		.agree-tos {
			color: $brand-white;
			font-size: 10px;
			text-align: left;
			width: 290px;
			margin: 0 auto;
			position: relative;
			padding-left: 44px;
			line-height: 16px;
			&::before {
				content: '\e66b';
				font-family: Linearicons;
				font-size: 38px;
				position: absolute;
				top: 11px;
				left: 0; }
			a {
				color: $brand-white;
				border-bottom: 1px solid $brand-white; } }
		@include tablet {
			background: $brand-white url('../img/smb/hero-bg-1920.jpg') no-repeat 60% top;
			height: 726px;
			text-align: left;
			h1,
			h3 {
				width: 480px;
				margin: 0; }
			h3 {
				font-size: 22px;
				padding: 0 0 22px; }
			.row {
				background: url('../img/smb/ribbon.png') no-repeat -50% 0; }
			form {
				width: 400px;
				margin: 0; }
			.form-group {
				margin-bottom: 22px; }
			select {
				background: $brand-white url('../img/smb/dropdown-bg-sm.png') no-repeat 100% 0; }
			input[type=submit] {
				margin: 0 auto 22px;
				display: block;
				width: 260px; }
			.agree-tos {
				margin: 0;
				width: 400px;
				line-height: 18px; } }
		@include desktop {
			background: $brand-white url('../img/smb/hero-bg-1920.jpg') no-repeat 50% top;
			.row {
				background: none; } } }

	.value {
		padding: 44px 0 22px;
		background: $brand-white;
		h4 {
			text-align: center;
			font-size: 26px;
			padding-bottom: 22px;
			color: $brand-blue; }
		.contents {
			@extend .col-xs-12; }
		.professional,
		.easy,
		.free {
			@extend .col-sm-4;
			position: relative;
			padding-top: 110px;
			ul {
				padding-left: 22px;
				width: 270px;
				margin: 0 auto;
				li {
					font-size: 18px;
					line-height: 22px;
					padding-bottom: 22px; }
				li:last-of-type {
					padding-bottom: 44px; } }
			&::before {
				font-family: Linearicons;
				font-size: 52px;
				margin-bottom: 22px;
				color: $brand-blue;
				position: absolute;
				border-radius: 50%;
				border: 2px solid $brand-blue;
				margin: 0 auto;
				text-align: center;
				width: 88px;
				height: 88px;
				top: 0;
				left: 0;
				right: 0; } }
		.professional {
			&::before {
				content: '\e83a'; } }
		.easy {
			&::before {
				content: '\e62b'; } }
		.free {
			&::before {
				content: '$'; } }
		@include tablet {
			.professional,
			.easy,
			.free {
				ul {
					width: 100%;
					margin: 0; } } }
		@include desktop-xl {
			.professional,
			.easy,
			.free {
				ul {
					li {
						padding-right: 22px; } } } } }
	.good-for-business {
		padding: 44px 0 462px;
		height: 1407px;
		background: $brand-gold url('../img/smb/product-shot-xs.jpg') no-repeat 50% bottom;
		h2,
		p {
			width: 290px;
			margin: 0 auto; }
		h2 {
			font-size: 36px;
			line-height: 48px;
			padding-bottom: 22px;
			color: $brand-navy;
			text-align: center;
			font-weight: 400; }
		p {
			font-size: 14px;
			line-height: 33px;
			padding-bottom: 22px; }
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-offset-1;
			@extend .col-sm-10;
			@extend .col-md-offset-0;
			@extend .col-md-9;
			@extend .col-lg-8; }
		@include tablet {
			background: $brand-gold url('../img/smb/product-shot-xs.jpg') no-repeat 50% 125%;
			background-size: 998px;
			height: 1100px;
			h2,
			p {
				width: 100%;
				margin: 0;
				text-align: left; }
			p {
				font-size: 18px;
				line-height: 33px; } }
		@include desktop {
			background: $brand-gold url('../img/smb/product-shot-sm.jpg') no-repeat 40% top;
			background-size: 1920px;
			height: 583px;
			h2 {
				padding-right: 0;
				width: 480px; }
			p {
				padding-left: 0; }
			p:last-of-type {
				padding-right: 110px; }
			.contents {
				padding-left: 11px; } }
		@include desktop-xl {
			background: $brand-gold url('../img/smb/product-shot-sm.jpg') no-repeat 44% top;
			p {
				padding: 0 33px 22px 0; }
			p:last-of-type {
				padding: 0 110px 0 0; }
			.contents {
				padding-left: 33px; } } }

	.looking {
		padding: 55px 0;
		background: $brand-navy;
		color: $brand-white;
		text-align: center;
		h3,
		p {
			width: 290px;
			margin: 0 auto;
			color: $brand-white;
			text-align: center; }
		h3 {
			font-size: 34px;
			line-height: 44px;
			margin-bottom: 22px; }
		p {
			font-size: 18px;
			line-height: 22px;
			padding: 11px;
			a {
				color: $brand-white; } }
		@include tablet {
			h3,
			p {
				width: 100%;
				margin: 0; } } } }
