				width: 55%;
				margin: 0 auto 44px; }
			.screenshot {
				background: $brand-white url('../img/smb/screenshot.png') no-repeat -5% bottom;
				margin-top: 0;
				background-size: 789px;
				height: 500px;
				.cta {
					margin-right: 22px; } } } } }

/* Modal form styling */










.marketplace-buyer {
	.hero.simple-invoicing {
		background: $brand-mandarin url('../img/smb/InkandToner.jpg') no-repeat center top;
		background-size: cover;
		.intro-text-cta {
			@extend .col-xs-12;
			@extend .col-sm-6;
			@extend .col-md-6;
			@extend .col-lg-offset-1;
			@extend .col-lg-6;
			h1 {
				margin: 66px auto 33px;
				color: $brand-white;
				font-weight: 600;
				font-size: 34px;
				line-height: 44px;
				text-transform: capitalize;
				em {
					font-weight: 800;
					font-style: normal; } }
			h3 {
				padding: 0 22px;
				margin: 0 auto 33px;
				color: $brand-white; }

			button {
				margin: 0 auto 132px;
				font-size: 14px !important;
				text-align: center !important; } }
		.cta-form {
			@extend .col-sm-offset-1;
			@extend .col-sm-5;
			@extend .col-md-offset-1;
			@extend .col-md-4;
			@extend .col-lg-offset-0;
			@extend .col-lg-3; }
		@include phone {
			.intro-text-cta {
				background: $brand-mandarin url('../img/smb/InkandToner.jpg') no-repeat center top;
				background-size: cover; }
			.icon-smb {
				display: none !important; } }
		@include tablet {
			background: $brand-mandarin url('../img/smb/InkandToner.jpg') no-repeat center top;
			background-size: cover;
			&.designer {
				background: $brand-mandarin url('../img/smb/hero-bg-2.jpg') no-repeat center top;
				background-size: cover; }
			&.baker {
				background: $brand-mandarin url('../img/smb/hero-bg-1.jpg') no-repeat center top;
				background-size: cover; }
			&.designer,
			&.baker {
				.intro-text-cta {
					background: none; } }
			.intro-text-cta {
				background: none;
				h1 {
					margin: 0 0 33px;
					font-size: 42px; }

				h3 {
					margin: 0;
					padding: 0; } }

			.cta-form {
				h3 {
					color: $brand-white;
					margin: 11px 0 22px;
					font-size: 22px;
					white-space: nowrap; }
				p {
					color: $brand-white;
					font-size: 16px;
					line-height: 22px;
					margin: 0 0 22px 0; }
				.agree-tos {
					margin: 0;
					padding-right: 22px;
					font-size: 10px;
					line-height: 14px; }
				a {
					text-decoration: underline;
					color: $brand-white; }
				.form-group {
					margin-bottom: 22px; } } }
		@include desktop {
			padding: 88px 0 0;
			&.designer {
				background: $brand-mandarin url('../img/smb/hero-bg-2-icon.jpg') no-repeat 55% top; }
			&.baker {
				background: $brand-mandarin url('../img/smb/hero-bg-1-icon.jpg') no-repeat 55% top; }
			.intro-text-cta-form {
				position: relative;
				padding-bottom: 77px; }
			.intro-text-cta {
				h1 {
					font-size: 48px;
					line-height: 55px; }
				h3 {
					padding-right: 33px; } }
			.icon-smb {
				position: absolute;
				bottom: 44px;
				right: 11px;
				left: auto;
				z-index: 1010; }
			.mandarin-bg {
				position: absolute;
				background: $brand-mandarin;
				right: 0;
				bottom: 0;
				border-bottom: 100px solid $brand-mandarin;
				border-left: 100px solid transparent;
				z-index: 1005; } }
		@include desktop-xl {
			&.designer {
				background: $brand-mandarin url('../img/smb/hero-bg-2-icon.jpg') no-repeat 45% top; }
			&.baker {
				background: $brand-mandarin url('../img/smb/hero-bg-1-icon.jpg') no-repeat 45% top; }
			.intro-text-cta {
				h1 {
					padding-right: 66px; }
				h3 {
					padding-right: 77px; } }
			.icon-smb {
				position: absolute;
				bottom: 44px;
				right: 33px; } } }

	.invoicing {
		padding: 44px 0;
		.contents {
			@extend .col-xs-12;
			@extend .col-md-offset-1;
			@extend .col-md-10;
			@extend .col-lg-offset-2;
			@extend .col-lg-8; }
		.contents-1,
		.contents-2 {
			@extend .col-xs-12;
			@extend .col-md-6; }
		h2 {
			font-weight: 400; }
		em {
			font-style: normal;
			font-weight: 700; }
		@include tablet {
			padding: 44px 0 33px;
			h2 {
				font-weight: 300;
				text-align: center;
				margin-bottom: 33px; } } }
	.value {
		padding: 44px 0;
		background: $brand-blue;
		color: $brand-white;
		text-align: center;
		.free-invoices,
		.get-paid-faster,
		.export-sales,
		.find-new-clients {
			@extend .col-xs-12;
			@extend .col-sm-12;
			[class^="icon-"],
			[class*=" icon-"] {
				font-family: Linearicons;
				font-size: 44px;
				margin-bottom: 22px; }
			h4 {
				color: $brand-white;
				margin-bottom: 22px;
				font-weight: 600; }
			p {
				color: $brand-white;
				margin-bottom: 44px;
				padding: 0 22px; } }
		@include tablet {
			padding: 44px 0 0; } }
	.no-upsell {
		padding: 44px 0 33px;
		position: relative;
		h2 {
			font-weight: 400; }
		&::after {
			content: ' ';
			text-align: center;
			margin: 0 auto;
			width: 26px;
			height: 16px;
			border-style: solid;
			border-width: 16px 13px 0 13px;
			border-color: $brand-blue transparent transparent transparent;
			position: absolute;
			top: 0;
			left: 0;
			right: 0; }
		.contents {
			@extend .col-xs-12;
			@extend .col-md-offset-1;
			@extend .col-md-10;
			@extend .col-lg-offset-2;
			@extend .col-lg-8; }
		p {
			em {
				font-style: normal;
				font-weight: 700; } }
		@include tablet {
			h2 {
				margin-bottom: 33px;
				font-weight: 300;
				text-align: center; } } }
	.sign-up {
		padding: 44px 0 55px;
		text-align: center;
		position: relative;
		color: $brand-white;
		&.designer {
			background: $brand-blue url('../img/smb/cta-bg-800-2.jpg') no-repeat center bottom;
			background-size: cover;
			color: $brand-white; }
		&::after {
			content: ' ';
			text-align: center;
			margin: 0 auto;
			width: 26px;
			height: 16px;
			border-style: solid;
			border-width: 16px 13px 0 13px;
			border-color: $brand-mandarin transparent transparent transparent;
			position: absolute;
			top: 0;
			left: 0;
			right: 0; }
		h2 {
			color: $brand-white;
			font-weight: 800;
			font-size: 26px;
			padding: 0 22px;
			margin-bottom: 33px; }
		button {
			font-size: 14px !important; }
		.contents {
			@extend .col-xs-12; }
		.arrow {
			// position: absolute
			position: static;
			bottom: 0;
			width: 100%;
			height: 0;
			background-color: $brand-white; }

		.arrow:before,
		.arrow:after {
			content: '';
			position: absolute;
			bottom: 100%;
			width: 50%;
			box-sizing: border-box; }

		.arrow:before {
			right: 50%;
			border-bottom: 16px solid $brand-white;
			border-right: 14px solid transparent; }

		.arrow:after {
			left: 50%;
			border-bottom: 16px solid $brand-white;
			border-left: 14px solid transparent; }
		@include tablet {
			&.baker {
				background: $brand-blue url('../img/smb/cta-bg-1.jpg') no-repeat center bottom; }
			&.designer {
				background: $brand-blue url('../img/smb/cta-bg-2.jpg') no-repeat center bottom;
				background-size: cover; }
			h2 {
				font-size: 34px; } }
		@include desktop {
			padding: 55px 0 66px;
			p {
				padding-right: 200px;
				padding-left: 200px; } } }
	.what-else {
		padding: 33px 0;
		text-align: center;
		h2 {
			padding: 0 22px;
			font-weight: 400;
			margin-bottom: 22px; }
		ul {
			@extend .list-unstyled;
			padding: 0;
			border-spacing: 0 11px;
			text-align: left;
			margin-bottom: 0;
			li {
				display: table-row;
				font-size: 18px;
				&::before {
					content: '+';
					font-size: 22px;
					display: table-cell;
					padding-right: 11px; } } }
		.features {
			@extend .col-xs-12;
			@extend .col-md-6;
			@extend .col-lg-5; }
		.features-1,
		.features-2 {
			@extend .col-xs-12;
			@extend .col-sm-6;
			@extend .col-md-12; }
		.screenshot {
			@extend .col-xs-12;
			@extend .col-sm-12;
			@extend .col-md-6;
			@extend .col-lg-7;
			text-align: center;
			background: $brand-white url('../img/smb/screenshot.png') no-repeat center bottom;
			background-size: 320px;
			height: 233px;
			cursor: pointer;
			margin-top: 33px;
			.cta {
				color: $brand-mandarin;
				font-size: 16px;
				font-weight: 600;
				border: none;
				margin: 0 auto;
				background: $brand-white;
				display: block;
				width: 260px;
				height: 44px;
				position: relative;
				text-align: right;
				&::before {
					content: '\e8d5';
					font-family: LinearIcons;
					font-size: 28px;
					font-weight: 600;
					line-height: 1.2;
					position: absolute;
					color: $brand-mandarin;
					position: absolute;
					left: 10px;
					bottom: 0;
					transform: rotate(-45deg);
					-webkit-transform: rotate(-45deg);
					-moz-transform: rotate(-45deg);
					-o-transform: rotate(-45deg);
					-ms-transform: rotate(-45deg);
					filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=-.5);
					@include font-smoothing; }
				em {
					font-style: normal;
					text-decoration: underline; } } }
		@include tablet {
			h2 {
				margin-bottom: 33px; }
			.screenshot {
				background-size: 768px;
				height: 486px;
				.cta {
					margin-right: 99px;
					font-size: 18px;
					width: 290px; } } }

		@include desktop {
			h2 {
				width: 55%;
				margin: 0 auto 44px; }
			.screenshot {
				background: $brand-white url('../img/smb/screenshot.png') no-repeat -5% bottom;
				margin-top: 0;
				background-size: 789px;
				height: 500px;
				.cta {
					margin-right: 22px; } } } } }


























/* Modal form styling */

.modal-smb {
	input[type=text],
	input[type=email],
	select {
		@extend .input-fields-default;
		width: 100%;
		font-size: 14px !important;
		color: $brand-black-lighter !important;
		&::-webkit-input-placeholder {
			color: $brand-black;
			font-size: 14px; } }
	select {
		-webkit-appearance: none;
		option {
			border: 0;
			border-radius: 0;
			padding: 11px 22px; } }
	input[type=submit] {
		text-align: center !important;
		margin: 11px 0 0; }
	.has-error {
		.help-block {
			color: #F60000 !important;
			font-weight: 600 !important; } }
	.form-control-feedback {
		top: 30px !important;
		right: 5px !important; } }








