// Custom Styling for Vision Page
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.vision-page {
	.hero.software {
		height: 539px;
		padding: 44px 0;
		background: $brand-blue;
		.contents {
			@extend .col-xs-12; }
		h1 {
			color: $brand-white;
			font-size: 34px;
			line-height: 44px; }

		h3 {
			color: $brand-white;
			margin-top: 44px;
			font-size: 26px;
			line-height: 33px;
			padding: 0;
			font-weight: 300; }
		@include tablet {
			height: 520px;
			text-align: center;
			&::after {
				top: 520px; }
			h1 {
				color: $brand-white;
				margin-top: 55px;
				font-size: 48px;
				line-height: 55px; }
			h3 {
				color: $brand-white;
				margin-top: 33px;
				font-size: 34px;
				line-height: 44px;
				padding: 0 44px; } }
		@include desktop {
			padding: 0;
			height: 530px;
			&::after {
				top: 530px; }
			h1 {
				margin-top: 77px; }
			h3 {
				color: $brand-white;
				margin-top: 55px;
				font-size: 34px;
				line-height: 55px;
				padding: 0 66px; } }
		@include desktop-xl {
			h1 {
				margin-top: 110px; } } }
	.digital-new-imperative {
		background: $brand-black-lighter;
		padding: 66px 0 55px;
		text-align: center;
		position: relative;
		&::after {
			content: ' ';
			text-align: center;
			margin: 0 auto;
			width: 26px;
			height: 16px;
			border-style: solid;
			border-width: 16px 13px 0 13px;
			border-color: $brand-blue transparent transparent transparent;
			position: absolute;
			top: 0;
			left: 0;
			right: 0; }
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-offset-1;
			@extend .col-sm-7;
			@extend .col-md-offset-1;
			@extend .col-md-7;
			@extend .col-lg-offset-1;
			@extend .col-lg-6; }
		img {
			margin: 0 auto 22px; }
		h2 {
			color: $brand-white;
			text-align: left; }
		p {
			color: $brand-blue;
			text-align: left; }
		p.that-platform {
			font-family: $font-family-heading;
			font-weight: 700;
			font-size: 18px; }
		@include tablet {
			background: $brand-black url('../img/vision/digital-bg-desktop.png') no-repeat 50%;
			background-size: cover;
			text-align: left;
			padding: 66px 0 55px;
			h2 {
				width: 620px; }
			p {
				line-height: 26px; } }
		@include desktop {
			p {
				line-height: 33px; } } }

	.differentiator-intro {
		background: $brand-black;
		background-size: cover;
		padding: 44px 0;
		position: relative;
		.contents {
			@extend .col-xs-12;
			@extend .col-sm-offset-1;
			@extend .col-sm-10;
			@extend .col-md-offset-1;
			@extend .col-md-9;
			@extend .col-lg-offset-1;
			@extend .col-lg-9; }
		h2 {
			color: $brand-white;
			width: 280px; }
		p,
		ul > li {
			color: $brand-white;
			font-size: 14px;
			line-height: 22px; }
		ul {
			padding-left: 0;
			list-style: none;
			display: table;
			li {
				margin-bottom: 6px;
				display: table-row;
				&::before {
					content: '•';
					font-size: 16px;
					padding-right: 6px;
					display: table-cell;
					line-height: 33px; } } }
		@include tablet {
			padding: 55px 0;
			background: $brand-black;
			h2 {
				width: 100%; }
			p,
			ul > li {
				color: $brand-white;
				font-size: 18px;
				line-height: 33px; } } }

	.differentiator-list {
		padding: 33px 0 66px 0;
		position: relative;
		h4 {
			color: $brand-blue;
			margin-top: 22px;
			font-size: 26px;
			line-height: 33px; }
		img {
			width: 110px;
			margin-top: 33px; }
		&::before {
			content: ' ';
			text-align: center;
			margin: 0 auto;
			width: 26px;
			height: 16px;
			border-style: solid;
			border-width: 16px 13px 0 13px;
			background: none;
			border-color: $brand-black transparent transparent transparent;
			position: absolute;
			top: 0;
			left: 0;
			right: 0; }
		@include tablet {
			padding: 55px 0 66px 0;
			h4 {
				font-size: 22px; }
			img {
				width: 184px;
				margin-top: 11px; } }
		@include desktop {
			h4 {
				font-size: 26px;
				line-height: 33px; } }
		.modern-apps {
			.modern {
				@extend .col-xs-12;
				@extend .col-sm-6;
				@extend .col-md-offset-1;
				@extend .col-md-5;
				text-align: center;
				h4 {
					text-align: left; }
				p {
					text-align: left;
					font-size: 14px;
					line-height: 22px; }
				@include desktop {
					p {
						font-size: 18px;
						line-height: 33px;
						padding-right: 22px; } } }
			.apps {
				@extend .col-sm-6;
				@extend .col-md-5;
				text-align: center;
				h4 {
					text-align: left; }
				p {
					text-align: left;
					font-size: 14px;
					line-height: 22px; }
				@include desktop {
					p {
						font-size: 18px;
						line-height: 33px;
						padding-right: 22px; } } } }
		.supplier-network-spend {
			@include desktop {
				margin-top: 33px; }
			.supplier-network {
				@extend .col-sm-6;
				@extend .col-md-offset-1;
				@extend .col-md-5;
				text-align: center;
				h4 {
					text-align: left; }
				p {
					text-align: left;
					font-size: 14px;
					line-height: 22px; }
				@include desktop {
					p {
						font-size: 18px;
						line-height: 33px;
						padding-right: 22px; } } }
			.spend {
				@extend .col-sm-6;
				@extend .col-md-5;
				text-align: center;
				h4 {
					text-align: left; }
				p {
					text-align: left;
					font-size: 14px;
					line-height: 22px; }
				@include desktop {
					p {
						font-size: 18px;
						line-height: 33px;
						padding-right: 22px; } } } } } }


