// Styling for Apps pages (single-apps.php, single-apps-*.php)
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css
// Each app page has custom styling.  We use the name of the partner as parent selector on main element
// .apps-single for single-apps.php  |  .apps-timestarter serve as namespace example for custom apps page

.apps-single {
	.hero {
		padding: 22px 0 0;
		color: $brand-white;
		text-align: center;
		height: 539px;
		background-size: cover;
		.intro-text-cta {
			@include make-xs-column(12);
			@include make-sm-column-offset(1);
			@include make-sm-column(10);
			@include make-md-column-offset(2);
			@include make-md-column(8); }
		h1 {
			color: $brand-white;
			margin: 143px 0 11px 0;
			font-size: 26px;
			line-height: 33px;
			font-weight: 800;
			text-align: center; }
		h3 {
			color: $brand-white;
			padding: 0 22px;
			margin-bottom: 22px; }

		.app-logo {
			@include make-xs-column(12);
			@include make-md-column-offset(1);
			@include make-md-column(10);
			text-align: right; }

		@include phone {
			.contents {
				width: 290px;
				float: none;
				margin: 0 auto; }
			.app-logo {
				img {
					width: 122px; } } }
		@include tablet {
			padding: 11px 0;
			height: 462px;
			h3 {
				font-size: 22px; } }
		@include desktop {
			height: 605px;
			h1 {
				margin: 220px 0 11px 0;
				font-size: 34px;
				line-height: 44px; }
			h3 {
				padding: 0 44px; } }
		@include desktop-xl {
			h1 {
				margin: 220px 44px 11px; }
			h3 {
				padding: 0 110px; } } }

	.value {
		background: $brand-white url('../img/apps/timestarter/tst-ts-xs.jpg') no-repeat center bottom;
		text-align: center;
		.contents {
			@include make-xs-column(12);
			@include make-lg-column-offset(1);
			@include make-lg-column(10); }
		h2 {
			color: $brand-blue;
			margin: 0 0 22px 0;
			letter-spacing: -0.01em; }
		h3 {
			margin: 0 0 22px 0; }
		p {
			color: $brand-black-lighter;
			margin-bottom: 22px; }
		ul {
			padding-left: 20px;
			list-style: disc;
			text-align: left;
			li {
				padding-bottom: 11px; } }
		hr.dash {
			background: url('../img/hr-dotted.png') repeat-x top center; }
		@include phone {
			padding: 44px 0 308px;
			h3 {
				padding: 0 11px; }
			h4 {
				color: $brand-blue;
				font-size: 14px;
				font-weight: 700;
				margin-bottom: 11px;
				text-align: left; }
			.contents {
				width: 309px;
				float: none;
				margin: 0 auto; } }
		@include tablet {
			padding: 44px 30px 121px;
			background: $brand-white url('../img/apps/timestarter/tst-ts.jpg') no-repeat 21% bottom;
			background-size: 1496px;
			h2 {
				text-align: center;
				margin-bottom: 11px; }
			h4 {
				text-align: left;
				color: $brand-blue;
				margin-bottom: 11px;
				font-size: 16px;
				font-weight: 700; }
			p {
				text-align: center;
				font-size: 16px; }
			ul {
				text-align: left;
				width: 264px;
				li {
					font-size: 16px; } } }

		@include desktop {
			padding: 44px 0 209px;
			background: $brand-white url('../img/apps/timestarter/tst-ts.jpg') no-repeat 13% bottom;
			h2 {
				padding: 0 66px;
				font-weight: 200; }
			h3 {
				font-weight: 200; }
			h4 {
				margin-top: 66px;
				font-size: 18px; }
			p {
				padding: 0;
				font-size: 18px;
				margin-right: 7%;
				margin-left: 7%; }
			ul {
				width: 423px;
				li {
					font-size: 18px;
					line-height: 33px; } }
			hr.dash {
				margin-top: 44px;
				height: 44px; } }
		@include desktop-xl {
			padding: 44px 0 220px;
			background: $brand-white url('../img/apps/timestarter/tst-ts-2560.jpg') no-repeat 45% bottom;
			background-size: 2559px;
			.contents {
				padding: 0; }
			p {
				margin-right: 7%;
				margin-left: 7%; }
			ul {
				width: 440px;
				li {
					padding-right: 20px; } } }

		// +desktop-1500
 }		// 	background: $brand-white url('../img/apps/timestarter/tst-ts-2560.jpg') no-repeat 3% bottom

	.feature-one {
		padding: 44px 0;
		background: $brand-blue;
		color: $brand-white;
		text-align: center;
		h2 {
			color: $brand-white; }
		.content-wrapper {
			@include make-sm-column(6); }
		@include phone {
			.contents {
				width: 100%;
				max-width: 320px;
				float: none;
				margin: 0 auto;
				padding: 0 11px;
				img {
					width: 100%;
					max-width: 320px; } }
			p {
				padding: 0 11px; } }
		@include tablet {
			text-align: left;
			h2 {
				margin-bottom: 11px; }
			p {
				font-size: 16px; }
			img {
				float: left;
				width: 352px;
				margin: 99px 0 0 15px; }
			.content-wrapper {
				float: right;
				width: 50%; } }
		@include desktop {
			p {
				font-size: 18px; }
			img {
				width: 440px;
				margin: 33px 0 0 30px; } }
		@include desktop-xl {
			img {
				width: 440px;
				margin: 33px 0 0 11%; }
			.content-wrapper {
				float: right;
				width: 38%;
				margin-right: 7%; } } }

	.feature-two {
		padding: 44px 0;
		background: $brand-baby-blue;
		text-align: center;
		position: relative;
		h2 {
			padding: 0 15px; }
		.contents {
			@include make-sm-column-offset(1);
			@include make-sm-column(10);
			@include make-md-column-offset(2);
			@include make-md-column(8);
			img {
				margin-bottom: 22px; } }

		@include phone {
			.contents {
				width: 100%;
				max-width: 320px;
				float: none;
				margin: 0 auto;
				padding: 0 15px;
				img {
					width: 100%;
					max-width: 320px; }
				p {
					padding: 0 3px; } } }
		@include tablet {
			h2 {
				margin-bottom: 11px; }
			p {
				font-size: 16px; }
			img {
				width: 330px; } }
		@include desktop {
			.contents {
				padding: 0; } } }

	.cta-driver {
		padding: 462px 0 0;
		background: $brand-black url('../img/apps/timestarter/activate-bg-xs.jpg') no-repeat center top;
		position: relative;
		height: 1012px;
		p {
			color: $brand-white;
			em {
				font-style: normal;
				color: $brand-orange;
				display: block; } }
		.contents {
			@include make-sm-column-offset(1);
			@include make-sm-column(10);
			@include make-md-column-offset(0);
			@include make-md-column(8);
			@include make-lg-column(7); }
		.btn {
			margin-top: 11px; }
		.arrow {
			position: absolute;
			top: 0;
			width: 100%;
			height: 0;
			background-color: $brand-baby-blue; }
		.arrow:before,
		.arrow:after {
			content: '';
			position: absolute;
			top: 0;
			width: 50%;
			box-sizing: border-box; }
		.arrow:before {
			right: 50%;
			border-bottom: 16px solid transparent;
			border-right: 14px solid $brand-baby-blue; }
		.arrow:after {
			left: 50%;
			border-bottom: 16px solid transparent;
			border-left: 14px solid $brand-baby-blue; }
		@include phone {
			text-align: center;
			h2 {
				padding: 0 15px; }
			p:last-of-type {
				padding: 0 30px; }
			.contents {
				width: 100%;
				max-width: 320px;
				float: none;
				margin: 0 auto;
				padding: 0 15px; } }
		@include tablet {
			text-align: center; }
		@include desktop {
			padding: 66px 0 0;
			background: $brand-black url('../img/apps/timestarter/activate-bg.jpg') no-repeat center top;
			text-align: left;
			height: 594px;
			h2 {
				margin-bottom: 11px; }
			.contents {
				padding-right: 0; } }
		@include desktop-xl {
			h2 {
				padding-right: 33px; } }
		@include desktop-1920 {
			background-size: cover;
			height: 660px; } } }

