/* Start of Custom Apps Page:  Biz2Credit */
.apps-bizcredit {
	.btn-primary-cta {
		padding: 8px 52px 10px 52px !important; }
	@include phone {
		.container {
			width: 320px; } }

	// .hero section
	//----------------------------------------------------
	.hero {
		.intro-text-cta {
			padding-top: 66px; }
		.logo {}

		h1 {
			color: #000;
			margin: 22px 90px 11px; }

		h3 {
			color: #000 !important;
			padding: 0 160px; }
		@include phone {
			background: url('/wp-content/uploads/2017/01/biz2credit-hero-bg.jpg') no-repeat 62% !important;
			.intro-text-cta {
				padding-top: 0; }
			h1 {
				margin: 22px 0 11px; }
			h3 {
				padding: 0; } }
		@include tablet-only {
			background: url('/wp-content/uploads/2017/01/biz2credit-hero-bg.jpg') no-repeat 58% !important;
			h1 {
				margin: 22px auto 11px;
				width: 450px; }
			h3 {
				padding: 0;
				width: 400px;
				margin-right: auto;
				margin-left: auto; } }
		@media (max-width: 1200px) and (min-width: 992px) {
			h1 {
				margin: 22px 25px 11px; }
			h3 {
				padding: 0 100px; } } }


	// .section-two
	//----------------------------------------------------
	.section-two {
		padding-top: 66px;
		padding-bottom: 66px;

		@include phone {
			p {
				padding: 0 15px !important; } } }

	// .value section
	//----------------------------------------------------
	.value {
		background: #f2f2f2;
		padding-bottom: 88px;
		.contents {
			width: 100%;
			margin-left: 0px; }
		h2 {
			margin-top: 11px; }
		.text-v {
			padding-top: 33px; }
		ul {
			// width: 375px !important
			width: auto;
			padding-top: 33px;
			// list-style-type: none
			li {
				padding-right: 0px; } }

		.product-screenshot {
			// width: 765px
			width: 65%;
			float: right;
			margin-left: 33px; }
		.product-screenshot-sm {
			display: none; }

		p {
			margin-right: 0px; }

		@include phone {
			padding-bottom: 0;
			.text-v {
				padding-top: 0px;
				width: 100%; }
			.product-screenshot {
				display: none; }
			.product-screenshot-sm {
				display: inline-block; }
			ul {
				padding-top: 0px;
				text-align: center; } }

		@include tablet-only {
			padding-bottom: 0;
			.text-v {
				padding-top: 0px;
				width: 500px;
				margin: auto;
				p {
					margin-bottom: 0px; } }
			ul {
				padding-top: 0px;
				text-align: center;
				li {
					// text-align: left
					// text-align: center
					// width: 455px
					// margin: auto
 } }					// list-style-type: none
			.product-screenshot {
				display: none; }
			.product-screenshot-sm {
				display: inline-block; } }

		@media (max-width: 1200px) and (min-width: 992px) {
			ul {
				padding-top: 11px;
				li {
					line-height: 26px;
					font-size: 16px;
					padding-left: 0px; } }
			.product-screenshot {
				width: 60%;
				margin-left: 22px; } } }

	// .ctac-driver section
	//----------------------------------------------------
	.cta-driver {
		background: #1E1E1E url("../img/apps/biz2credit/biz2credit-section-3-bg.jpg") no-repeat center top;
		.contents {
			width: 585px;
			float: none !important;
			margin: auto;
			text-align: center; }
		.arrow {
			display: none; }
		h2 {
			color: $brand-white;
			font-weight: 400; }
		@include phone {
			padding-top: 66px;
			padding-bottom: 66px;
			height: auto; }
		@include tablet-only {
			padding-top: 66px;
			padding-bottom: 66px;
			height: auto; } } }







